<template>
  <div class="col-12">

    <div v-if="currentCycle === 'range'">
      <b-card class="bg-dark mb-0">
        <span class="pull-left">
          <span class="p-2 text-light">RANGE STATUS: </span>
          <span class="p-2 dashboard-border-light" :style="{ background: configuration.custom_colors.open, color: configuration.custom_colors.open_text}">{{ summary.open }}</span>
          <span class="p-2 dashboard-border-light" :style="{ background: configuration.custom_colors.expiring, color: configuration.custom_colors.expiring_text}">{{ summary.expiring }}</span>
          <span class="p-2 dashboard-border-light" :style="{ background: configuration.custom_colors.expired, color: configuration.custom_colors.expired_text}">{{ summary.expired }}</span>
        </span>
      </b-card>
      <b-card class="bg-light border-light">
        <div class="row">

          <div v-bind:class="[ splitColumns ]" class="lane-wrapper">
            <div v-for="item in sessionsFirst" class="lane-nomargin" @click="addRemoveSelectedLane(item.lane)">
              <div :style="{ background: item.laneBgColor, color: item.laneTextColor, border: '2px solid #333'}" class="lane" v-if="item.barColor">
                <span :style="{ background: item.laneBgDefaultColor, color: item.laneTextDefaultColor, border: '2px solid #4D4D4D'}" class="lane-number text-center">
                  {{ item.lane }}
                </span>
                <div class="client-info">
                  <span class="top-info-right">{{ item.sessionType }}</span>
                  <div class="clear"></div>
                  <span class="bottom-info-right">{{ item.clientName }}</span>
                </div>
                <div class="time-info">
                  <span class="top-info">{{ item.displayDescription }}</span>
                  <div class="clear"></div>
                  <span class="bottom-info">{{ item.displayValue }}</span>
                </div>
              </div>
              <div :style="{ background: item.laneBgDefaultColor, color: item.laneTextDefaultColor, border: '2px solid ' + item.laneBgColor}" class="lane" v-if="item.borderColor">
                <span :style="{ background: item.laneBgDefaultColor, color: item.laneTextDefaultColor, border: '2px solid ' + item.laneBgColor}" class="lane-number text-center">
                  {{ item.lane }}
                </span>
                <div class="client-info">
                  <span class="top-info-right">{{ item.sessionType }}</span>
                  <div class="clear"></div>
                  <span class="bottom-info-right">{{ item.clientName }}</span>
                </div>
                <div class="time-info">
                  <span class="top-info">{{ item.displayDescription }}</span>
                  <div class="clear"></div>
                  <span class="bottom-info">{{ item.displayValue }}</span>
                </div>
              </div>
            </div>
          </div>

          <div v-bind:class="[ splitColumns ]" v-if="splitLanes" class="lane-wrapper">
            <div v-for="item in sessionsSecond" class="lane-nomargin" @click="addRemoveSelectedLane(item.lane)">
              <div :style="{ background: item.laneBgColor, color: item.laneTextColor, border: '2px solid #333'}" class="lane" v-if="item.barColor">
                <span :style="{ background: item.laneBgDefaultColor, color: item.laneTextDefaultColor, border: '2px solid #4D4D4D'}" class="lane-number text-center">
                  {{ item.lane }}
                </span>
                <div class="client-info">
                  <span class="top-info-right">{{ item.sessionType }}</span>
                  <div class="clear"></div>
                  <span class="bottom-info-right">{{ item.clientName }}</span>
                </div>
                <div class="time-info">
                  <span class="top-info">{{ item.displayDescription }}</span>
                  <div class="clear"></div>
                  <span class="bottom-info">{{ item.displayValue }}</span>
                </div>
              </div>
              <div :style="{ background: item.laneBgDefaultColor, color: item.laneTextDefaultColor, border: '2px solid ' + item.laneBgColor}" class="lane" v-if="item.borderColor">
                <span :style="{ background: item.laneBgDefaultColor, color: item.laneTextDefaultColor, border: '2px solid ' + item.laneBgColor}" class="lane-number text-center">
                  {{ item.lane }}
                </span>
                <div class="client-info">
                  <span class="top-info-right">{{ item.sessionType }}</span>
                  <div class="clear"></div>
                  <span class="bottom-info-right">{{ item.clientName }}</span>
                </div>
                <div class="time-info">
                  <span class="top-info">{{ item.displayDescription }}</span>
                  <div class="clear"></div>
                  <span class="bottom-info">{{ item.displayValue }}</span>
                </div>
              </div>
            </div>
          </div>

        </div>
      </b-card>
    </div>



    <div v-if="currentCycle === 'waitinglist'">
      <b-card class="bg-dark mb-0">
        <span class="pull-left">
          <span class="p-2 text-light">WAITING LIST <span class="border border-dark bg-yellow p-2 ml-2 text-dark">{{ waiting_list.length }}</span></span>
        </span>
      </b-card>
      <b-card class="bg-light border-light">
        <div class="row">
          <b-table striped hover :items="waiting_list" :fields="waiting_list_fields" class="m-0">
            <template slot="client_name" slot-scope="data">
              {{ data.item.client_name }}
            </template>
            <template slot="lanes" slot-scope="data">
              {{ data.item.lanes }} | {{ data.item.session_length.name }}
            </template>
            <template slot="source" slot-scope="data">
              {{ data.item.source }} | {{ formatTime(data.item.queue_time) }}
            </template>
            <template slot="checked_in" slot-scope="data">
              <b-badge :variant="getBadge(data.item.checked_in)">{{data.value}}</b-badge> | {{ formatTime(data.item.check_in_time) }}
            </template>
            <template slot="reservation" slot-scope="data">
              <b-badge :variant="getBadge(data.item.reservation)">{{data.value}}</b-badge>
            </template>
            <template slot="session_length" slot-scope="data">
              {{data.item.session_length.name}}
            </template>

          </b-table>
        </div>
      </b-card>
    </div>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import dashboard from '@/services/dashboard'
  import formats from '@/services/formats'
  import VueTimers from 'vue-timers/mixin'

  export default {
    name: 'RangeDashboard',
    mixins: [VueTimers],
    data () {
      return {
        displayCode: null,
        displayMode: null,
        currentCycle: 'range',
        apiKey: null,
        rangeID: null,
        configuration: null,
        customColors: null,
        sessions: [],
        sessionsOrdered: {},
        sessionsFirst: [],
        sessionsSecond: [],
        splitLanes: false,
        splitColumns: 'col-md-12',
        splitFirstSet: [],
        splitSecondSet: [],
        selectedLanes: [],
        selectedIDs: [],
        selectedLanesPPU: [],
        PPUInvoice: {},
        loading: true,
        pendingRefresh: false,
        summary: {
          open: 0,
          occupied: 0,
          expiring: 0,
          expired: 0
        },
        waiting_list: [],
        waiting_list_remove_ID: null,
        waiting_list_fields: {
          queue: {
            label: 'Queue',
            sortable: true
          },
          client_name: {
            label: 'Name'
          },
          lanes: {
            label: '# Lanes'
          },
          source: {
            label: 'Source'
          },
          checked_in: {
            label: 'Arrived',
            formatter: (value) => formats.format_yes_no(value)
          },
          reservation: {
            label: 'Reservation',
            formatter: (value) => formats.format_yes_no(value)
          }
        }
      }
    },
    timers: {
      refreshDashboard: {time: 60000, autostart: true, repeat: true},
      cycleDashboard: {time: 60000, autostart: true, repeat: true}
    },
    mounted: function () {
      this.displayCode = this.$route.params.id
      this.getDisplayConfiguration()
    },
    methods: {
      refreshDashboard () {
        if (!this.pendingRefresh) {
          this.getDisplayConfiguration()
        }
      },
      cycleDashboard () {
        if (this.displayMode === 'mixed') {
          if (this.currentCycle === 'range') {
            this.currentCycle = 'waitinglist'
          } else if (this.currentCycle === 'waitinglist') {
            this.currentCycle = 'range'
          }
        } else if (this.displayMode === 'range') {
          this.currentCycle = 'range'
        } else if (this.displayMode === 'waitinglist') {
          this.currentCycle = 'waitinglist'
        }
      },
      getDisplayConfiguration () {
        rangepro.get_secure('public/display/' + this.displayCode, this.handleDisplayConfigurationResponse, 'GetStoreRanges')
      },
      getInitialRangeConfiguration () {
        rangepro.get_pub_secure('public/range/' + this.rangeID, this.apiKey, this.handleInitialConfigurationResponse, 'GetStoreRanges')
      },
      getRangeSessions () {
        rangepro.get_pub_secure('public/range/' + this.rangeID + '/sessions', this.apiKey, this.handleSessionsResponse, 'GetStoreRanges')
      },
      getWaitingList () {
        rangepro.get_pub_secure('public/range/' + this.rangeID + '/waiting_list/queue/' + formats.get_date(), this.apiKey, this.handleWaitingListResponse, 'GetStoreRanges')
      },
      handleDisplayConfigurationResponse (data, success) {
        if (success) {
          this.rangeID = data.display['range']
          this.apiKey = data.display['api_key']
          this.displayMode = this.getMode(data.display['mode'])
          this.getInitialRangeConfiguration()
          this.getWaitingList()
        }
      },
      handleInitialConfigurationResponse (data, success) {
        if (success) {
          this.configuration = data.range
          this.getRangeSessions()
        }
      },
      handleSessionsResponse (data, success) {
        if (success) {
          // Set sessions and clear out lane split.
          this.sessions = data.sessions
          this.splitFirstSet = []
          this.splitSecondSet = []
          // Process lane segments being called sets in dashboard.  Determine layout then process
          // the lanes returned and display.
          this.splitLanes = dashboard.check_split(this.configuration.lanes)
          var countLanes = 1
          // If lanes are greater than 8 then split between two sets.
          if (this.splitLanes) {
            // Get the lane number as the split point.
            let splitLane = dashboard.split_lane(this.configuration.lanes)
            this.splitColumns = 'col-md-6'
            // Process each set left and right.
            while (countLanes <= splitLane) {
              this.splitFirstSet.push(countLanes)
              countLanes++
            }
            while (countLanes <= this.configuration.lanes) {
              this.splitSecondSet.push(countLanes)
              countLanes++
            }
          } else {
            // If lanes are less than 8, just display in one set.
            countLanes = 1
            while (countLanes <= this.configuration.lanes) {
              this.splitFirstSet.push(countLanes)
              countLanes++
            }
          }
          // Process sessions into their respective sets for display.
          this.processSessions()
        }
      },
      handleWaitingListResponse (data, success) {
        if (success) {
          this.waiting_list = data.waiting_list
        }
      },
      processSessions () {
        // Process sessions and configure presentation of lane data.
        this.sessionsOrdered = dashboard.order_sessions(this.sessions)
        var processed = dashboard.setup_sessions(this.configuration, this.sessionsOrdered, this.splitFirstSet, this.splitSecondSet)
        this.sessionsFirst = processed.completeFirstSet
        this.sessionsSecond = processed.completeSecondSet
        // Present summary data about the range.
        this.summary.open = processed.open
        this.summary.occupied = processed.occupied
        this.summary.expiring = processed.expiring
        this.summary.expired = processed.expired
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      },
      formatTime (value) {
        return formats.format_ts(value)
      },
      getMode (mode) {
        if (mode === 'range only') {
          return 'range'
        } else if (mode === 'waiting list only') {
          return 'waitinglist'
        } else if (mode === 'cycle range and waiting list') {
          return 'mixed'
        }
      }
    }
  }
</script>

<style scoped>
.dashboard-border-dark {
  border: 1px solid #0b0e0f;
}

.dashboard-border-light {
  border: 1px solid #f7f7f7;
}

.lane-wrapper {
  position: relative;
  overflow: hidden;
  margin-left: 0px;
  margin-right: 0px;
  padding-left: 2px !important;
  padding-right: 2px !important;
}

.lane {
  width: 100%;
  height: 45px;
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
}

.lane:hover{
  color: #eee !important;
  background-color: #222 !important;
}

.lane-nomargin {
  padding-top: 1px !important;
  padding-bottom: 1px !important;
  padding-left: 2px !important;
  padding-right: 2px !important;
  margin-left: 0px;
  margin-right: 0px;
}

.lane-number {
  font-size: 1.0em;
  position: relative;
  float: left;
  margin-top: 2px;
  margin-left: 2px;
  padding: 6px;
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
}

/* LANE SELECTION AND LANE MOVE */
.lane_selection {
  position: relative;
  float: right;
  margin-top: 10px;
  margin-right: 5px;
  border: 1px solid #5C5C5C;
}

.button_notselected {
  color: #eee;
  background-color: black;
}

.button_selected {
  color: #000 !important;
  background-color: #33CC33 !important;
}

.btn-circle {
  width: 24px;
  height: 24px;
  text-align: center;
  padding: 4px;
  font-size: 10px;
  line-height: 1.42;
  border-radius: 15px;
}

/* LANE CUSTOMER INFORMATION */
.lane .time-info {
  position: relative;
  float: right;
  margin-right: 10px;
}

.lane .client-info {
  position: relative;
  float: left;
  margin-left: 15px;
}

.lane .top-info {
  position: relative;
  top: 2px;
  left: 0px;
  font-family: 'Oswald', sans-serif;
  font-size: 10px;
  color: inherit;
}

.lane .top-info-right {
  position: relative;
  float: left;
  margin-top: 4px;
  font-family: 'Oswald', sans-serif;
  font-size: 10px;
  color: inherit;
}

.lane .bottom-info-right {
  position: relative;
  float: left;
  margin-bottom: 0px;
  font-family: 'Oswald', sans-serif;
  font-size: 16px;
  color: inherit;
}

.lane .bottom-info {
  position: relative;
  bottom: 2px;
  left: 0px;
  font-family: 'Oswald', sans-serif;
  font-size: 16px;
  color: inherit;
}
</style>



