import store from '../store'
import router from '../router'
import { AuthenticationDetails, CognitoUser, CognitoUserPool, CognitoUserAttribute } from 'amazon-cognito-identity-js'

export default {

  authenticate: function (username, password) {
    var authenticationData = {
      Username: username,
      Password: password
    }

    var authenticationDetails = new AuthenticationDetails(authenticationData)
    var cognitoUser = getUserData(username)
    authenticateUser(cognitoUser, authenticationDetails, username)
  },
  register: function (username, password) {
    var response = {
      'success': false,
      'msg': null
    }
    var poolData = getPoolData()
    var userPool = new CognitoUserPool(poolData)

    userPool.signUp(username, password, null, null, function (err, result) {
      if (err) {
        if (err.statusCode === 200) {
          router.push({name: 'Verify', params: {username: username}})
        } else {
          console.log('Error creating user: ' + err.message.toString())
          return response
        }
      }
      console.log('Successful Registration: ' + username)
      router.push({name: 'Verify', params: {username: username}})
    })
  },
  verify: function (username, code) {
    var response = {
      'success': false,
      'msg': null,
      'payload': null
    }
    var cognitoUser = getUserData(username)

    if (cognitoUser != null) {
      cognitoUser.confirmRegistration(code, true, function (err, result) {
        if (err) {
          if (err.statusCode === 200) {
            console.log('Verified Email: ' + result)
            router.push({name: 'Login'})
          } else {
            console.log('Error verifying email: ' + err)
            return response
          }
        }
        console.log('Verified Email: ' + result)
        router.push({name: 'Login'})
      })
    }
  },
  signout: function () {
    var cognitoUser = getUserData(store.getters.profile)
    store.commit('setIdToken', null)
    store.commit('setAccessToken', null)
    store.commit('setAuthenticated', false)
    store.commit('setProfile', null)
    store.commit('setProfileData', null)

    cognitoUser.signOut()
    router.push({name: 'Login'})
  },
  adminChange: function (username, oldPassword, newPassword, first, last) {
    var authenticationData = {
      Username: username,
      Password: oldPassword
    }

    var attributeList = {
      'given_name': first,
      'family_name': last
    }

    var authenticationDetails = new AuthenticationDetails(authenticationData)
    var cognitoUser = getUserData(username)
    authenticateUser(cognitoUser, authenticationDetails, username, true, newPassword, attributeList)
  },
  authChange: function (oldPassword, newPassword) {
    var cognitoUser = getCurrentUser()
    cognitoUser.getSession(function (err, session) {
      if (err) {
        console.log(err)
        store.commit('addNotification', {'message': 'Error Getting Session', 'style': 'danger', 'icon': 'user'})
        return
      }
      cognitoUser.changePassword(oldPassword, newPassword, function (err, result) {
        if (err) {
          store.commit('addNotification', {'message': 'Error Changing Password', 'style': 'danger', 'icon': 'user'})
          return
        }
        store.commit('addNotification', {'message': 'Successfully Changed Password', 'style': 'success', 'icon': 'user'})
      })
    })
  },
  updateProfile: function (first, last, email) {
    // Update the user's profile
    var attributeList = []
    var dataFirstName = {
      Name: 'given_name',
      Value: first
    }
    var dataLastName = {
      Name: 'family_name',
      Value: last
    }
    var dataEmail = {
      Name: 'email',
      Value: email
    }

    var attributeFirstName = new CognitoUserAttribute(dataFirstName)
    var attributeLastName = new CognitoUserAttribute(dataLastName)
    var attributeEmail = new CognitoUserAttribute(dataEmail)

    attributeList.push(attributeFirstName)
    attributeList.push(attributeLastName)
    attributeList.push(attributeEmail)

    var cognitoUser = getCurrentUser()
    cognitoUser.getSession(function (err, session) {
      if (err) {
        console.log(err)
        store.commit('addNotification', {'message': 'Error Getting Session', 'style': 'danger', 'icon': 'user'})
        return
      }
      cognitoUser.updateAttributes(attributeList, function (err, result) {
        if (err) {
          console.log(err)
          store.commit('addNotification', {'message': 'Error Updating Profile', 'style': 'danger', 'icon': 'user'})
          return
        }
        store.commit('addNotification', {'message': 'Successfully Updated Profile', 'style': 'success', 'icon': 'user'})
        loadProfile()
      })
    })
  },
  forgotPasswordStart: function (username) {
    var cognitoUser = getUserData(username)

    cognitoUser.forgotPassword({
      onSuccess: function (result) {
        store.commit('addNotification', {'message': 'Verification Code Sent', 'style': 'success', 'icon': 'user'})
        console.log('Change password: ' + result)
        router.push({name: 'ForgotChange', params: {username: username}})
      },
      onFailure: function (err) {
        console.log(err)
        store.commit('addNotification', {'message': 'Error Sending Verification', 'style': 'danger', 'icon': 'user'})
      }
    })
  },
  forgotPasswordChange: function (username, code, newPassword) {
    var cognitoUser = getUserData(username)

    cognitoUser.confirmPassword(code, newPassword, {
      onSuccess: function (result) {
        store.commit('addNotification', {'message': 'Successfully Changed Password', 'style': 'success', 'icon': 'user'})
        console.log(result)
        router.push({name: 'Login'})
      },
      onFailure: function (err) {
        store.commit('addNotification', {'message': 'Error - Check Verification Code', 'style': 'danger', 'icon': 'user'})
        console.log(err)
      }
    })
  }
}

function getPoolData () {
  // Get pool data for Cognito Pool
  var poolData = {
    UserPoolId: process.env.P_ID,
    ClientId: process.env.UP_ID
  }
  return poolData
}

function getUserData (username) {
  // Get user instance for authentication
  var poolData = getPoolData()
  var userPool = new CognitoUserPool(poolData)
  var userData = {
    Username: username,
    Pool: userPool
  }
  console.log(username)
  return new CognitoUser(userData)
}

function getCurrentUser () {
  // Get current user session
  var poolData = getPoolData()
  var userPool = new CognitoUserPool(poolData)
  var cognitoUser = userPool.getCurrentUser()
  return cognitoUser
}

function loadProfile () {
  // Load current user profile
  var profileData = {}
  var cognitoUser = getCurrentUser()
  cognitoUser.getSession(function (err, session) {
    if (err) {
      console.log(err)
      return
    }
    cognitoUser.getUserAttributes(function (err, result) {
      if (err) {
        console.log(err)
        return
      }
      for (var i = 0; i < result.length; i++) {
        if (result[i].getName() === 'given_name') {
          profileData['first'] = result[i].getValue()
        } else if (result[i].getName() === 'family_name') {
          profileData['last'] = result[i].getValue()
        }
      }
      store.commit('setProfileData', profileData)
    })
  })
}

function authenticateUser (cognitoUser, authenticationDetails, username, change = null, newPassword = null, changeAttributes = []) {
  cognitoUser.authenticateUser(authenticationDetails, {
    onSuccess: function (result) {
      console.log('ID Token: ' + result.idToken.jwtToken)
      store.commit('setIdToken', result.idToken.jwtToken)
      store.commit('setAccessToken', result.getAccessToken().getJwtToken())
      store.commit('setAuthenticated', true)
      store.commit('setProfile', username)
      store.commit('setEmail', result.idToken.payload.email)
      store.commit('resetNotifications')
      store.commit('addNotification', {'message': 'Successfully Logged In', 'style': 'success', 'icon': 'user'})
      loadProfile()
      router.push({name: 'Store Range Configuration'})
    },

    onFailure: function (err) {
      if (err.statusCode === 200) {
        console.log('Error ID Token: ' + err.idToken.jwtToken)
        store.commit('setIdToken', err.idToken.jwtToken)
        store.commit('setAccessToken', err.getAccessToken().getJwtToken())
        store.commit('setAuthenticated', true)
        store.commit('setProfile', username)
        store.commit('setEmail', err.idToken.payload.email)
        store.commit('resetNotifications')
        store.commit('addNotification', {'message': 'Successfully Logged In', 'style': 'success', 'icon': 'user'})
        loadProfile()
        router.push({name: 'Account'})
      } else {
        console.log('Error authenticating: ' + err.message)
        router.push({name: 'Error', params: {error: 'Invalid Username or Password'}})
      }
    },

    newPasswordRequired: function (userAttributes, requiredAttributes) {
      // User was signed up by an admin and must provide new
      // password and required attributes, if any, to complete
      // authentication.
      var attributesData = changeAttributes
      if (change) {
        cognitoUser.completeNewPasswordChallenge(newPassword, attributesData, this)
      } else {
        router.push({name: 'Change', params: {username: username}})
      }
    }

  })
}



