<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="10" sm="10">
          <b-card no-body class="mx-4">
            <b-card-body class="p-4">
              <h1><img src="@/assets/icon.png" width="50" height="50"> Complete Registration</h1>
              <p class="text-muted">Complete your RangePro store admin registration.</p>

              <span class="form-group__message text-danger" v-if="!$v.oldPassword.required">Temporary Password Required.</span>
              <b-input-group class="mb-3" v-bind:class="{ 'form-group--error': $v.oldPassword.$error }">
                <div class="input-group-prepend">
                  <span class="input-group-text"><i class="icon-lock"></i></span>
                </div>
                <input type="password" class="form-control" placeholder="Temporary Password" v-model="oldPassword">
              </b-input-group>

              <b-input-group class="mb-3">
                <div class="input-group-prepend">
                  <span class="input-group-text"><i class="icon-lock"></i></span>
                </div>
                <input type="password" class="form-control" placeholder="New Password" v-model="newPassword">
                <b-input-group-append>
                  <b-btn variant="danger" v-if="score < 3">Too Weak</b-btn>
                  <b-btn variant="success" v-if="score === 3">Perfect</b-btn>
                </b-input-group-append>
              </b-input-group>

              <span class="form-group__message text-danger" v-if="!$v.newRepeat.sameAsPassword">Passwords Must Be Identical.</span> <span class="form-group__message text-danger" v-if="!$v.newPassword.minLength">Passwords Must Be At Least 8 Characters.</span>
              <b-input-group class="mb-1" v-bind:class="{ 'form-group--error': $v.newRepeat.$error }">
                <div class="input-group-prepend">
                  <span class="input-group-text"><i class="icon-lock"></i></span>
                </div>
                <input type="password" class="form-control" placeholder="Current Password" v-model="newRepeat">
              </b-input-group>

              <span class="p-1">
                <div class="custom-control custom-checkbox col-md-12">
                  <input type="checkbox" class="custom-control-input" id="termsCheck" v-model="terms" checked>
                  <label class="custom-control-label" for="termsCheck">By selecting this option, I accept the <a href="https://docs.range-pro.com/terms_of_service.html" target="_blank">Terms of Service</a>.</label>
                </div>
              </span>

              <b-row>
                <b-col cols="2">

                </b-col>
                <b-col cols="10">
                  <span class="text-danger">{{ this.$route.params.error }}</span>
                </b-col>
              </b-row>

              <b-button @click="change(username, oldPassword, newRepeat)" variant="success" block :disabled="$v.$invalid || !terms || score < 3">Change Password</b-button>
            </b-card-body>
          </b-card>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
  import auth from '../services/auth'
  import { required, sameAs, minLength } from 'vuelidate/lib/validators'

  export default {
    name: 'LoginChangePassword',
    data () {
      return {
        score: 0,
        terms: false,
        oldPassword: '',
        newPassword: '',
        newRepeat: '',
        username: ''
      }
    },
    mounted () {
      this.username = this.$route.params.username
    },
    watch: {
      newPassword: function (value) {
        this.score = 0
        var hasNumber = /\d/.test(value)
        var hasLowercase = /[a-z]/.test(value)
        var hasUppercase = /[A-Z]/.test(value)
        if (hasNumber) {
          this.score += 1
        }
        if (hasLowercase) {
          this.score += 1
        }
        if (hasUppercase) {
          this.score += 1
        }
      }
    },
    methods: {
      change (user, oldPassword, newPassword) {
        return auth.adminChange(user, oldPassword, newPassword)
      }
    },
    validations: {
      oldPassword: {
        required
      },
      newPassword: {
        minLength: minLength(8)
      },
      newRepeat: {
        sameAsPassword: sameAs('newPassword')
      }
    }
  }
</script>



