          session_types: [],
          selected_session_type: null,
          default_session_type: null
        },
        customSessionLength: {
          name: 'Custom',
          button_text: 'Custom',
          pay_per_use: false,
          unlimited: false,
          minutes_length: 0,
          minutes_setup: 0
