            key: 'id',
            label: 'Action'
          }
        ],
        addReservation: {
          member_id: null,
          client_name: null,
          phone: '#',
          lanes: null,
          reservation_date: null,
          reservation_time: null,
          paid_in_full: false,
          paid_partial: false,
          paid_partial_amount: '0',
          special_instructions_ack: false
        },
        open_times: [],
        blocked_times: []
      }
    },
    mounted: function () {
      this.rangeID = this.$route.params.id
      this.reservationID = this.$route.params.reservationID
      this.waitinglistID = this.$route.params.waitinglistID
      this.getInitialRangeConfiguration()
    },
    methods: {
      getInitialRangeConfiguration () {
        this.loading = true
        rangepro.get_secure('store/range/' + this.rangeID, this.handleInitialConfigurationResponse, 'GetStoreRanges')
      },
      getReservation () {
        this.loading = true
        rangepro.get_secure('store/range/' + this.rangeID + '/reservation/' + this.reservationID, this.handleReservationResponse, 'GetReservation')
      },
      getWaitingList () {
        this.loading = true
        rangepro.get_secure('store/range/' + this.rangeID + '/waiting_list/' + this.waitinglistID, this.handleWaitingListResponse, 'GetReservation')
      },
      searchReservations () {
        var totalMinutes = 0
        if (this.search.search_hours > 0 || this.search.search_minutes > 0) {
          totalMinutes = (this.search.search_hours * 60) + this.search.search_minutes
        } else {
          totalMinutes = this.options.selected_session_length['minutes_length']
        }
        // If store has chosen to force the reservation
        // open dialogue and continue.
        if (this.search.search_force) {
          this.forceReservation()
        } else {
          this.loading = true
          this.searched = true
          var reservationSearchCriteria = {
            reservation: {
              date: formats.format_date(this.search.search_date),
              time: formats.format_mil_to_std_time(this.search.search_time),
              lanes: this.search.search_lanes,
              minutes_duration: totalMinutes
            }
          }
          rangepro.post_secure('store/range/' + this.rangeID + '/reservations/available', reservationSearchCriteria, this.handleSearchResponse, 'SearchRangeReservations', null)
        }
      },
      reservationSave () {
        var sessionLength = null
        if (this.search.search_hours > 0 || this.search.search_minutes > 0) {
          var totalMinutes = (this.search.search_hours * 60) + this.search.search_minutes
          this.customSessionLength.minutes_length = totalMinutes
          sessionLength = this.customSessionLength
        } else {
          sessionLength = this.options.selected_session_length
        }
        var reservation = {
          reservations: {
            client_name: this.addReservation.client_name,
            lanes: this.addReservation.lanes,
            phone: this.addReservation.phone,
            paid_in_full: this.addReservation.paid_in_full,
            paid_partial: this.addReservation.paid_partial,
            paid_partial_amount: this.addReservation.paid_partial_amount,
            reservation_date: this.addReservation.reservation_date,
            reservation_time: this.addReservation.reservation_time,
            session_length: sessionLength,
            session_type: this.options.selected_session_type,
            special_instructions: this.configuration.reservations.special_instructions,
            special_instructions_ack: this.addReservation.special_instructions_ack
          }
        }
        if (this.addReservation.member_id) {
          reservation.reservations.member_id = this.addReservation.member_id
        }
        rangepro.post_secure('store/range/' + this.rangeID + '/reservations', reservation, this.handleReservationSave, 'AddRangeReservation', null)
        this.loading = true
      },
      reservationUpdate () {
        var sessionLength = null
        if (this.search.search_hours > 0 || this.search.search_minutes > 0) {
          var totalMinutes = (this.search.search_hours * 60) + this.search.search_minutes
          this.customSessionLength.minutes_length = totalMinutes
          sessionLength = this.customSessionLength
        } else {
          sessionLength = this.options.selected_session_length
        }
        var reservation = {
          reservations: {
            client_name: this.reservation.client_name,
            lanes: this.addReservation.lanes,
            phone: this.reservation.phone,
            paid_in_full: this.reservation.paid_in_full,
            paid_partial: this.reservation.paid_partial,
            paid_partial_amount: this.reservation.paid_partial_amount,
            reservation_date: this.addReservation.reservation_date,
            reservation_time: this.addReservation.reservation_time,
            session_length: sessionLength,
            session_type: this.options.selected_session_type
          }
        }
        if (this.reservation.member_id) {
          reservation.reservations.member_id = this.reservation.member_id
        }
        rangepro.put_secure('store/range/' + this.rangeID + '/reservation/' + this.reservationID, reservation, this.handleReservationSave, 'UpdateRangeReservation', null)
        this.loading = true
      },
      deleteWaitingListCustomer () {
        rangepro.delete_secure('store/range/' + this.rangeID + '/waiting_list', this.waitinglistID, this.handleWaitingListDelete, 'CheckInWaitingList', null)
        this.loading = true
      },
      handleInitialConfigurationResponse (data, success) {
        if (success) {
          this.configuration = data.range
          // Setup add modal options for session_lengths and session_types
          this.options.session_lengths = dashboard.setup_add_options(this.configuration.session_lengths)
          this.options.session_types = dashboard.setup_add_options(this.configuration.session_types)
          this.default_session_length = dashboard.default_add_option(this.configuration.session_lengths)
          this.default_session_type = dashboard.default_add_option(this.configuration.session_types)
          this.options.selected_session_length = this.default_session_length
          this.options.selected_session_type = this.default_session_type
          this.loading = false
          if (this.reservationID) {
            this.changeReservation = true
            this.getReservation()
          } else if (this.waitinglistID) {
            this.convertWaitingList = true
            this.getWaitingList()
          }
        } else {
          this.loading = false
        }
      },
      handleReservationResponse (data, success) {
        if (success) {
          this.reservation = data.reservation
          this.search.search_lanes = this.reservation.lanes
          for (var x in this.options.session_types) {
            if (this.reservation.session_type['name'] === this.options.session_types[x]['text']) {
              this.options.selected_session_type = this.options.session_types[x]['value']
            }
          }
          for (var y in this.options.session_lengths) {
            if (this.reservation.session_length['name'] === this.options.session_lengths[y]['text']) {
              this.options.selected_session_length = this.options.session_lengths[y]['value']
            }
          }
        } else {
          this.changeReservation = false
        }
        this.loading = false
      },
      handleWaitingListResponse (data, success) {
        if (success) {
          this.waitinglist = data.waiting_list
          this.search.search_lanes = this.waitinglist.lanes
          for (var x in this.options.session_types) {
            if (this.waitinglist.session_type['name'] === this.options.session_types[x]['text']) {
              this.options.selected_session_type = this.options.session_types[x]['value']
            }
          }
          for (var y in this.options.session_lengths) {
            if (this.waitinglist.session_length['name'] === this.options.session_lengths[y]['text']) {
              this.options.selected_session_length = this.options.session_lengths[y]['value']
            }
          }
        } else {
          this.convertWaitingList = false
        }
        this.loading = false
      },
      handleSearchResponse (data, success) {
        if (success) {
          this.available_times = data.available_times
          this.open_times = data.open_times
          this.blocked_times = data.blocked_times
          this.search_time_available = data.search_time_available
          this.search_time_available_time = data.search_time_display
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleReservationSave (data, success) {
        if (success) {
          this.loading = false
          this.searched = false
          if (this.convertWaitingList) {
            this.deleteWaitingListCustomer()
          } else {
            router.push({name: 'Store Range Dashboard', params: {id: this.rangeID}})
          }
        } else {
          this.loading = false
        }
      },
      handleWaitingListDelete (data, success) {
        if (success) {
          router.push({name: 'Store Range Dashboard', params: {id: this.rangeID}})
        }
      },
      forceReservation () {
        this.addReservation.reservation_time = formats.format_mil_to_std_time(this.search.search_time)
        this.addReservation.reservation_date = formats.format_date(this.search.search_date)
        this.addReservation.lanes = this.search.search_lanes
        if (this.changeReservation) {
          this.reservationUpdate()
        } else if (this.convertWaitingList) {
          this.addReservation.client_name = this.waitinglist.client_name
          this.addReservation.phone = this.waitinglist.phone
          this.addReservation.member_id = this.waitinglist.member_id
          this.addReservation.paid_in_full = this.waitinglist.paid_in_full
          this.addReservation.paid_partial = this.waitinglist.paid_partial
          this.addReservation.paid_partial_amount = this.waitinglist.paid_partial_amount
          this.$refs.reservationAdd.show()
        } else {
          this.$refs.reservationAdd.show()
        }
      },
      selectAvailableTime (time) {
        this.addReservation.reservation_time = time
        this.addReservation.reservation_date = formats.format_date(this.search.search_date)
        this.addReservation.lanes = this.search.search_lanes
        if (this.changeReservation) {
          this.reservationUpdate()
        } else if (this.convertWaitingList) {
          this.addReservation.client_name = this.waitinglist.client_name
          this.addReservation.phone = this.waitinglist.phone
          this.addReservation.member_id = this.waitinglist.member_id
          this.addReservation.paid_in_full = this.waitinglist.paid_in_full
          this.addReservation.paid_partial = this.waitinglist.paid_partial
          this.addReservation.paid_partial_amount = this.waitinglist.paid_partial_amount
          this.$refs.reservationAdd.show()
        } else {
          this.$refs.reservationAdd.show()
        }
      },
      checkInstructions () {
        if (!this.configuration.reservations.special_instructions) {
          this.reservationSave()
        } else {
          if (!this.addReservation.special_instructions_ack) {
            this.$refs.ackInstructions.show()
          } else {
            this.reservationSave()
          }
        }
      },
      formatTime (time) {
        return formats.format_mil_to_std_time(time)
      },
      formatMinutes (value) {
        return formats.format_minutes_display(value)
      },
      formatDate (date) {
        return formats.format_date(date)
      },
      clearPhone () {
        if (this.addReservation.phone === '(###) ###-####') {
          this.addReservation.phone = ''
        }
      },
      closeAck () {
        this.addReservation.special_instructions_ack = true
        this.reservationSave()
        this.$refs.ackInstructions.hide()
      }
    },
    validations: {
      search: {
        search_date: {
          required
        },
        search_time: {
          required
        },
        search_lanes: {
          required
        }
      },
      addReservation: {
        client_name: {
          required
        },
        phone: {
          required
        },
        paid_partial_amount: {
          required
        }
      }
    }
  }
</script>

<style scoped>

</style>



