import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'

// Containers
import Full from '@/containers/Full'
import Display from '@/containers/Display'
import Kiosk from '@/containers/Kiosk'

// Views
import Dashboard from '@/views/Dashboard'
import Profile from '@/views/Profile'
import RangeConfigurationView from '@/views/StoreViews/Range/RangeConfiguration'
import RangeConfigurationFormView from '@/views/StoreViews/Range/RangeConfigurationForm'
import RangeDashboardView from '@/views/StoreViews/Range/RangeDashboard'
import RangeReservationsView from '@/views/StoreViews/Range/RangeReservations'
import RangeReservationsListView from '@/views/StoreViews/Range/RangeReservationsList'
import RangeHistoryView from '@/views/StoreViews/Range/RangeHistory'
import StoreMembersView from '@/views/StoreViews/Store/StoreMembers'
import StoreMemberDetailView from '@/views/StoreViews/Store/StoreMemberDetail'
import StoreDisplaysView from '@/views/StoreViews/Store/StoreDisplays'
import StoreKiosksView from '@/views/StoreViews/Store/StoreKiosks'
import StoreWaiversView from '@/views/StoreViews/Store/StoreWaivers'
import StoreWaiversFormView from '@/views/StoreViews/Store/StoreWaiversForm'
import KioskDashboardView from '@/views/StoreViews/Store/Kiosk/KioskDashboard'
import WaiversDashboardView from '@/views/StoreViews/Store/Kiosk/Waivers/WaiversDashboard'
import WaiverFormView from '@/views/StoreViews/Store/Kiosk/Waivers/WaiverForm'

// Components
import Page404 from '@/components/Page404'
import Login from '@/components/Login'
import Verify from '@/components/Verify'
import LoginChangePassword from '@/components/LoginChangePassword'
import ForgotPasswordStart from '@/components/ForgotPasswordStart'
import ForgotPasswordChange from '@/components/ForgotPasswordChange'

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      redirect: '/home/dashboard',
      name: 'Root',
      meta: {
        title: 'Home',
        auth: true
      }
    },
    {
      path: '*',
      redirect: '/404'
    },
    {
      path: '/404',
      component: Page404
    },
    {
      path: '/display/:id',
      name: 'Display',
      component: Display,
      meta: {
        title: 'Store Display',
        auth: false
      }
    },
    {
      path: '/home',
      name: 'Home',
      component: Full,
      meta: {
        title: 'Account',
        auth: true
      },
      children: [
        {
          path: 'dashboard',
          name: 'Account',
          component: Dashboard,
          meta: {
            title: 'Account',
            auth: true
          }
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta: {
        title: 'Login',
        auth: false
      }
    },
    {
      path: '/forgot',
      name: 'Forgot',
      component: ForgotPasswordStart,
      meta: {
        title: 'Forgot Password',
        auth: false
      }
    },
    {
      path: '/forgot/:username',
      name: 'ForgotChange',
      component: ForgotPasswordChange,
      meta: {
        title: 'Verify Change Password',
        auth: false
      }
    },
    {
      path: '/error/:error',
      name: 'Error',
      component: Login,
      meta: {
        title: 'Login',
        auth: false
      }
    },
    {
      path: '/verify/:username',
      name: 'Verify',
      component: Verify,
      meta: {
        title: 'Verify Registration',
        auth: false
      }
    },
    {
      path: '/change/:username',
      name: 'Change',
      component: LoginChangePassword,
      meta: {
        title: 'Change Password',
        auth: false
      }
    },
    {
      path: '/account',
      name: 'Account',
      component: Full,
      meta: {
        title: 'Account Settings',
        auth: true
      },
      children: [
        {
          path: 'profile',
          name: 'Profile',
          component: Profile,
          meta: {
            title: 'Account Profile',
            auth: true
          }
        }
      ]
    },
    {
      path: '/kiosk',
      name: 'Kiosk',
      component: Kiosk,
      meta: {
        title: 'Store Kiosk',
        auth: true
      },
      children: [
        {
          path: 'dashboard/:id',
          name: 'Kiosk Dashboard',
          component: KioskDashboardView,
          meta: {
            title: 'Kiosk Dashboard',
            auth: true
          }
        },
        {
          path: 'waivers/dashboard/:id',
          name: 'Waivers Dashboard',
          component: WaiversDashboardView,
          meta: {
            title: 'Waivers Dashboard',
            auth: true
          }
        },
        {
          path: 'waiver/:id/form/:formid',
          name: 'Waiver Form',
          component: WaiverFormView,
          meta: {
            title: 'Waiver Form',
            auth: true
          }
        }
      ]
    },
    {
      path: '/store',
      name: 'Store',
      component: Full,
      meta: {
        title: 'Store',
        auth: true
      },
      children: [
        {
          path: 'members',
          name: 'Members',
          component: StoreMembersView,
          meta: {
            title: 'Store Members',
            auth: true
          }
        },
        {
          path: 'members/:id',
          name: 'Member Detail',
          component: StoreMemberDetailView,
          meta: {
            title: 'Store Member Detail',
            auth: true
          }
        },
        {
          path: 'displays',
          name: 'Displays',
          component: StoreDisplaysView,
          meta: {
            title: 'Store Displays',
            auth: true
          }
        },
        {
          path: 'kiosks',
          name: 'Kiosks',
          component: StoreKiosksView,
          meta: {
            title: 'Store Kiosks',
            auth: true
          }
        },
        {
          path: 'waivers',
          name: 'Store Waivers',
          component: StoreWaiversView,
          meta: {
            title: 'Store Waivers',
            auth: true
          }
        },
        {
          path: 'waivers/add',
          name: 'Store Waivers Add',
          component: StoreWaiversFormView,
          meta: {
            title: 'Store Waivers Add',
            auth: true
          }
        },
        {
          path: 'waivers/update/:id',
          name: 'Store Waivers Update',
          component: StoreWaiversFormView,
          meta: {
            title: 'Store Waivers Update',
            auth: true
          }
        }
      ]
    },
    {
      path: '/range',
      name: 'Range',
      component: Full,
      meta: {
        title: 'Store Ranges',
        auth: true
      },
      children: [
        {
          path: 'configuration',
          name: 'Store Range Configuration',
          component: RangeConfigurationView,
          meta: {
            title: 'Range Configuration',
            auth: true
          }
        },
        {
          path: 'configuration/add',
          name: 'Store Range Configuration Add',
          component: RangeConfigurationFormView,
          meta: {
            title: 'Range Configuration Add',
            auth: true
          }
        },
        {
          path: 'configuration/update/:id',
          name: 'Store Range Configuration Update',
          component: RangeConfigurationFormView,
          meta: {
            title: 'Range Configuration Update',
            auth: true
          }
        },
        {
          path: 'dashboard/:id',
          name: 'Store Range Dashboard',
          component: RangeDashboardView,
          meta: {
            title: 'Range Dashboard',
            auth: true
          }
        },
        {
          path: 'reservations/:id',
          name: 'Store Range Reservations',
          component: RangeReservationsView,
          meta: {
            title: 'Range Reservations',
            auth: true
          }
        },
        {
          path: 'reservations/:id/change/:reservationID',
          name: 'Store Range Reservations Change',
          component: RangeReservationsView,
          meta: {
            title: 'Range Reservations',
            auth: true
          }
        },
        {
          path: 'reservations/:id/convert/:waitinglistID',
          name: 'Store Range Reservations Convert',
          component: RangeReservationsView,
          meta: {
            title: 'Range Reservations',
            auth: true
          }
        },
        {
          path: 'reservations/list/:id',
          name: 'Store Range Reservations List',
          component: RangeReservationsListView,
          meta: {
            title: 'Range Reservations List',
            auth: true
          }
        },
        {
          path: 'history/:id',
          name: 'Store Range History',
          component: RangeHistoryView,
          meta: {
            title: 'Range History',
            auth: true
          }
        }
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  // Use the page's router title to name the page
  if (to.meta && to.meta.title) {
    document.title = to.meta.title
  }

  // Redirect to the login page if not authenticated
  // for pages that have 'auth: true' set
  if (to.meta && to.meta.auth !== undefined) {
    if (to.meta.auth) {
      if (store.getters.isAuthenticated) {
        next()
      } else {
        router.push({ name: 'Login' })
      }
    } else {
      if (store.getters.isAuthenticated) {
        router.push({ name: 'Home' })
      } else {
        next()
      }
    }
  } else {
    next()
  }
})

export default router



