<template>
  <div class="col-12">
    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Store</strong> Waiver <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
        <router-link :to="{ name: 'Store Waivers'}">
          <b-button class="pull-right" type="submit" size="sm" variant="primary">
            <i class="fa fa-th"></i> Waivers
          </b-button>
        </router-link>
        <a href="https://docs.range-pro.com/store_account.html#waivers" target="new" class="button">
          <b-button type="submit" size="sm" variant="primary" class="pull-right mr-1"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>

      <b-form-group
        label="Name"
        label-for="waiverName"
        :label-cols="2"
        :horizontal="true">
        <b-form-input id="waiverName" type="text" v-model="waiver.name"></b-form-input>
        <span class="form-group__message text-danger" v-if="!$v.waiver.name.required">Waiver Name is Required</span>
      </b-form-group>

      <b-form-group
        label="Description"
        label-for="waiverDescription"
        :label-cols="2"
        :horizontal="true">
        <b-form-input id="waiverDescription" type="text" v-model="waiver.description"></b-form-input>
        <span class="form-group__message text-danger" v-if="!$v.waiver.description.required">Waiver Description is Required</span>
      </b-form-group>

      <b-form-group
        label="Enabled"
        label-for="waiverEnabled"
        :label-cols="2"
        :horizontal="true">
        <b-form-select id="waiverEnabled"
                       :plain="true"
                       :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                       v-model="waiver.enabled">
        </b-form-select>
      </b-form-group>

      <b-form-group
        label="Capture Signatures"
        label-for="captureSignatures"
        :label-cols="2"
        :horizontal="true">
        <b-form-select id="captureSignatures"
                       :plain="true"
                       :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                       v-model="waiver.capture_signatures">
        </b-form-select>
      </b-form-group>

      <b-form-group
        label="Waiver Content"
        label-for="waiverContent"
        :label-cols="2"
        :horizontal="true">
        <quill-editor v-model="waiver.waiver_text"></quill-editor>
      </b-form-group>

      <div slot="footer">
        <b-button class="pull-right" type="submit" size="sm" variant="success" @click="routeUpdate()" :disabled="$v.waiver.$invalid || loading"><i class="fa fa-dot-circle-o"></i> Save</b-button>
      </div>

    </b-card>



    <b-card v-if="id" class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Store</strong> Waiver Questions <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true"> <b-button type="submit" size="sm" variant="success" class="pull-right" v-b-modal.questionAdd><i class="fa fa-plus"></i> Add</b-button>
      </div>
      <b-table striped hover :items="questions" :fields="fields">

        <template slot="enabled" slot-scope="data">
          <b-badge :variant="getBadge(data.item.enabled)">{{data.value}}</b-badge>
        </template>

        <template slot="id" slot-scope="data">
          <b-button type="submit" size="sm" variant="warning" @click="selectQuestion(data.item.name, data.item.enabled, data.item.order, data.item.question_type, data.item.question_text, data.item.id )"><i class="fa fa-trash-o"></i> Edit</b-button>
          <b-button type="submit" size="sm" variant="danger" @click="confirmDeleteQuestion(data.item.id)"><i class="fa fa-trash-o"></i> Delete</b-button>
        </template>
      </b-table>

      <nav>
        <b-pagination :total-rows="getRowCount(questions)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
      </nav>

      <b-modal ref="confirmDelete" hide-footer title="Delete Question" class="modal-dark text-light">
        <div class="d-block text-left text-dark">
          <p>Please confirm to delete this question.</p>
        </div>
        <b-btn class="mt-3" variant="outline-danger" block @click="deleteQuestion()">Confirm</b-btn>
      </b-modal>

    </b-card>


    <b-modal ref="questionAdd" id="questionAdd" hide-footer size="lg" centered hide-footer title="Add Waiver Question" class="modal-dark text-light">
      <div ref="card" class="text-dark">

        <b-form-group
          label="Name"
          label-for="questionName"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="questionName" type="text" v-model="addQuestion.name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.addQuestion.name.required">Question Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Enabled"
          label-for="questionEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="questionEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="addQuestion.enabled">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Order"
          label-for="questionOrder"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="questionOrder"
                         :plain="true"
                         :options="[
                           { value: 1, text: '1'}, { value: 2, text: '2'}, {value: 3, text: '3'}, { value: 4, text: '4'},
                           { value: 5, text: '5'}, { value: 6, text: '6'}, { value: 7, text: '7'}, { value: 8, text: '8'},
                           { value: 9, text: '9'}, { value: 10, text: '10'}, { value: 11, text: '11'}, { value: 12, text: '12'},
                           { value: 13, text: '13'}, { value: 14, text: '14'}, { value: 15, text: '15'}, { value: 16, text: '16'},
                           { value: 17, text: '17'}, { value: 18, text: '18'}, { value: 19, text: '19'}, { value: 20, text: '20'},
                           { value: 21, text: '21'}, { value: 22, text: '22'}, { value: 23, text: '23'}, { value: 24, text: '24'},
                           { value: 25, text: '25'}, { value: 26, text: '26'}, { value: 27, text: '27'}, { value: 28, text: '28'},
                           { value: 29, text: '29'}, { value: 30, text: '30'}, { value: 31, text: '31'}, { value: 32, text: '32'},
                           { value: 33, text: '33'}, { value: 34, text: '34'}, { value: 35, text: '35'}, { value: 36, text: '36'},
                           { value: 37, text: '37'}, { value: 38, text: '38'}, { value: 39, text: '39'}, { value: 40, text: '40 '},
                         ]"
                         v-model="addQuestion.order">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Type"
          label-for="questionType"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="questionType"
                         :plain="true"
                         :options="[
                           { value: 'input_text', text: 'Input Text'}, { value: 'boolean', text: 'Yes/No'}
                         ]"
                         v-model="addQuestion.question_type">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Question"
          label-for="questionText"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="questionText" type="text" v-model="addQuestion.question_text"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.addQuestion.question_text.required">Question Text is Required</span>
        </b-form-group>

        <b-btn class="mt-3" variant="outline-success" block @click="saveQuestion()" :disabled="$v.addQuestion.$invalid">Add Question</b-btn>

      </div>
    </b-modal>


    <b-modal ref="questionEdit" id="questionEdit" hide-footer size="lg" centered hide-footer title="Edit Waiver Question" class="modal-dark text-light">
      <div ref="card" class="text-dark">

        <b-form-group
          label="Name"
          label-for="questionName"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="questionName" type="text" v-model="updateQuestion.name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.updateQuestion.name.required">Question Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Enabled"
          label-for="questionEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="questionEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="updateQuestion.enabled">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Order"
          label-for="questionOrder"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="questionOrder"
                         :plain="true"
                         :options="[
                           { value: 1, text: '1'}, { value: 2, text: '2'}, {value: 3, text: '3'}, { value: 4, text: '4'},
                           { value: 5, text: '5'}, { value: 6, text: '6'}, { value: 7, text: '7'}, { value: 8, text: '8'},
                           { value: 9, text: '9'}, { value: 10, text: '10'}, { value: 11, text: '11'}, { value: 12, text: '12'},
                           { value: 13, text: '13'}, { value: 14, text: '14'}, { value: 15, text: '15'}, { value: 16, text: '16'},
                           { value: 17, text: '17'}, { value: 18, text: '18'}, { value: 19, text: '19'}, { value: 20, text: '20'},
                           { value: 21, text: '21'}, { value: 22, text: '22'}, { value: 23, text: '23'}, { value: 24, text: '24'},
                           { value: 25, text: '25'}, { value: 26, text: '26'}, { value: 27, text: '27'}, { value: 28, text: '28'},
                           { value: 29, text: '29'}, { value: 30, text: '30'}, { value: 31, text: '31'}, { value: 32, text: '32'},
                           { value: 33, text: '33'}, { value: 34, text: '34'}, { value: 35, text: '35'}, { value: 36, text: '36'},
                           { value: 37, text: '37'}, { value: 38, text: '38'}, { value: 39, text: '39'}, { value: 40, text: '40 '},
                         ]"
                         v-model="updateQuestion.order">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Type"
          label-for="questionType"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="questionType"
                         :plain="true"
                         :options="[
                           { value: 'input_text', text: 'Input Text'}, { value: 'boolean', text: 'Yes/No'}
                         ]"
                         v-model="updateQuestion.question_type">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Question"
          label-for="questionText"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="questionText" type="text" v-model="updateQuestion.question_text"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.updateQuestion.question_text.required">Question Text is Required</span>
        </b-form-group>

        <b-btn class="mt-3" variant="outline-success" block @click="editQuestion()" :disabled="$v.updateQuestion.$invalid">Update Question</b-btn>

      </div>
    </b-modal>

  </div>
</template>

<script>
  import Vue from 'vue'
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'
  import { required } from 'vuelidate/lib/validators'
  import VueQuillEditor from 'vue-quill-editor'

  // require styles
  import 'quill/dist/quill.core.css'
  import 'quill/dist/quill.snow.css'
  import 'quill/dist/quill.bubble.css'

  // mount with global
  Vue.use(VueQuillEditor)

  export default {
    name: 'WaiverForm',
    data () {
      return {
        id: null,
        loading: false,
        waiver: {
          name: null,
          description: null,
          enabled: true,
          capture_signatures: true,
          waiver_text: null
        },
        questions: [],
        edit_id: null,
        delete_id: null,
        updateQuestion: {
          name: null,
          enabled: true,
          order: 1,
          question_type: 'input_text',
          question_text: null
        },
        addQuestion: {
          name: null,
          enabled: true,
          order: 1,
          question_type: 'input_text',
          question_text: null
        },
        fields: [
          {
            key: 'order',
            label: 'Order',
            sortable: true,
            sortDirection: 'asc'
          },
          {
            key: 'name',
            label: 'Name'
          },
          {
            key: 'enabled',
            label: 'Enabled',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'id',
            label: 'Action'
          }
        ],
        currentPage: 1,
        perPage: 5,
        totalRows: 0
      }
    },
    mounted: function () {
      if (this.$route.params.id) {
        this.loading = true
        this.id = this.$route.params.id
        this.getData(null)
        this.getQuestions(null)
      }
    },
    methods: {
      getData (response) {
        this.loading = true
        rangepro.get_secure('store/waiver/' + this.id, this.handleResponse, 'ErrorWaiverConfigurationResponse')
      },
      getQuestions (response) {
        this.loading = true
        rangepro.get_secure('store/waiver/' + this.id + '/questions', this.handleQuestionsResponse, 'ErrorWaiverQuestionsResponse')
      },
      addItem () {
        this.loading = true
        var data = {
          waiver: {
            name: this.waiver.name,
            description: this.waiver.description,
            enabled: this.waiver.enabled,
            capture_signatures: this.waiver.capture_signatures,
            waiver_text: String(this.waiver.waiver_text)
          }
        }
        rangepro.post_secure('store/waivers', data, this.handleUpdateRepsonse, 'AddEditStoreWaiverConfiguration', 'Saved Waiver Configuration')
      },
      updateItem () {
        this.loading = true
        var data = {
          waiver: {
            name: this.waiver.name,
            description: this.waiver.description,
            enabled: this.waiver.enabled,
            capture_signatures: this.waiver.capture_signatures,
            waiver_text: String(this.waiver.waiver_text)
          }
        }
        rangepro.put_secure('store/waiver/' + this.id, data, this.handleUpdateRepsonse, 'AddEditStoreWaiverConfiguration', 'Updated Waiver Configuration')
      },
      saveQuestion () {
        this.loading = true
        var question = {
          question: this.addQuestion
        }
        rangepro.post_secure('store/waiver/' + this.id + '/questions', question, this.handleAddQuestionResponse, 'AddWaiverQuestion', 'Successfully Added Waiver Question')
      },
      editQuestion () {
        this.loading = true
        var question = {
          question: this.updateQuestion
        }
        rangepro.put_secure('store/waiver/' + this.id + '/question/' + this.edit_id, question, this.handleAddQuestionResponse, 'EditWaiverQuestion', 'Successfully Edited Waiver Question')
      },
      deleteQuestion () {
        this.loading = true
        this.$refs.confirmDelete.hide()
        rangepro.delete_secure('store/waiver/' + this.id + '/question', this.delete_id, this.getQuestions, 'GetStoreDisplays', 'Removed Waiver Question')
      },
      routeUpdate () {
        if (this.id) {
          this.updateItem()
        } else {
          this.addItem()
        }
      },
      handleResponse (data, success) {
        if (success) {
          this.waiver = data.waiver
          delete this.waiver.id
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleQuestionsResponse (data, success) {
        if (success) {
          this.questions = data.questions
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleUpdateRepsonse (data, success) {
        if (success && this.id === null) {
          // get ID from success response and don't route back
          this.id = data.id
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleAddQuestionResponse (data, success) {
        if (success) {
          this.addQuestion.name = null
          this.addQuestion.enabled = true
          this.addQuestion.order = 1
          this.addQuestion.question_type = 'input_text'
          this.addQuestion.question_text = null
          this.updateQuestion.name = null
          this.updateQuestion.enabled = true
          this.updateQuestion.order = 1
          this.updateQuestion.question_type = 'input_text'
          this.updateQuestion.question_text = null
          this.edit_id = null
          this.$refs.questionAdd.hide()
          this.$refs.questionEdit.hide()
          this.getQuestions()
        } else {
          this.loading = false
        }
      },
      selectQuestion (name, enabled, order, type, text, id) {
        this.edit_id = id
        this.updateQuestion.name = name
        this.updateQuestion.enabled = enabled
        this.updateQuestion.order = order
        this.updateQuestion.question_type = type
        this.updateQuestion.question_text = text
        this.$refs.questionEdit.show()
      },
      confirmDeleteQuestion (id) {
        this.$refs.confirmDelete.show()
        this.delete_id = id
      },
      getBadge (status) {
        return formats.getBadge(status)
      },
      getRowCount (items) {
        return items.length
      }
    },
    validations: {
      waiver: {
        name: {
          required
        },
        description: {
          required
        },
        waiver_text: {
          required
        }
      },
      addQuestion: {
        name: {
          required
        },
        question_text: {
          required
        }
      },
      updateQuestion: {
        name: {
          required
        },
        question_text: {
          required
        }
      }
    }
  }
</script>

<style scoped>

</style>



