          {
            key: 'enabled',
            label: 'Enabled'
          },
          {
            key: 'public',
            label: 'Public'
          },
          {
            key: 'default',
            label: 'Default',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'id',
            label: 'Action'
          }
        ],
        time_fields: [
          {
            key: 'time_start',
            label: 'Start Time',
            sortable: true
          },
          {
            key: 'time_stop',
            label: 'Stop Time'
          },
          {
            key: 'members',
            label: 'Members Only',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'id',
            label: 'Action'
          }
        ],
        blocked_fields: [
          {
            key: 'date',
            label: 'Date',
            sortable: true
          },
          {
            key: 'start_time',
            label: 'Start Time'
          },
          {
            key: 'end_time',
            label: 'End Time'
          },
          {
            key: 'all_day',
            label: 'Block Entire Day',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'id',
            label: 'Action'
          }
        ]
      }
    },
    mounted: function () {
      if (this.$route.params.id) {
        this.loading = true
        this.id = this.$route.params.id
        this.getData(null)
      }
    },
    methods: {
      getData (response) {
        rangepro.get_secure('store/range/' + this.id, this.handleResponse, 'ErrorRegistrationUsers')
      },
      addItem () {
        this.loading = true
        var data = {
          range: this.item
        }
        rangepro.post_secure('store/ranges', data, this.handleUpdateRepsonse, 'AddEditStoreRanges', 'Added Store Range')
      },
      updateItem () {
        this.loading = true
        var data = {
          range: this.item
        }
        rangepro.put_secure('store/range/' + this.id, data, this.handleUpdateRepsonse, 'AddEditStoreRanges', 'Updated Store Range')
      },
      routeUpdate () {
        if (this.id) {
          this.updateItem()
        } else {
          this.addItem()
        }
      },
      handleResponse (data, success) {
        if (success) {
          this.item = data.range
          delete this.item.id
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleUpdateRepsonse (data, success) {
        if (success) {
          router.push({name: 'Store Range Configuration'})
        }
      },
      getBadge (status) {
        return formats.getBadge(status)
      },
      addClear (reset, modal) {
        this.edit_list_id = null
        reset()
        modal.show()
      },
      addListItem (list, data, reset, modal) {
        if (this.edit_list_id != null) {
          this.deleteListItem(list, this.edit_list_id)
          this.edit_list_id = null
        }
        list.push(data)
        reset()
        modal.hide()
      },
      addListHoursItem (list, data, reset, modal) {
        if (this.edit_list_id != null) {
          this.deleteListItem(list[data.dow], this.edit_list_id)
          this.edit_list_id = null
        }
        var timeRange = {
          time_start: formats.format_mil_to_std_time(data.time_start),
          time_stop: formats.format_mil_to_std_time(data.time_stop),
          members: data.members
        }
        list[data.dow].push(timeRange)
        reset()
        modal.hide()
      },
      addListBlockItem (list, data, reset, modal) {
        data.date = formats.format_date(data.date)
        data.start_time = formats.format_mil_to_std_time(data.start_time)
        data.end_time = formats.format_mil_to_std_time(data.end_time)
        list.push(data)
        reset()
        modal.hide()
      },
      editListItem (list, index, modal, listType, day) {
        this.edit_list_id = index
        if (listType === 'type') {
          this.session_types_add.name = list[index]['name']
          this.session_types_add.legend = list[index]['legend']
          this.session_types_add.enabled = list[index]['enabled']
          this.session_types_add.default = list[index]['default']
        } else if (listType === 'length') {
          this.session_lengths_add.name = list[index]['name']
          this.session_lengths_add.button_text = list[index]['button_text']
          this.session_lengths_add.enabled = list[index]['enabled']
          this.session_lengths_add.public = list[index]['public']
          this.session_lengths_add.default = list[index]['default']
          this.session_lengths_add.unlimited = list[index]['unlimited']
          this.session_lengths_add.minutes_length = list[index]['minutes_length']
          this.session_lengths_add.minutes_setup = list[index]['minutes_setup']
          this.session_lengths_add.pay_per_use = list[index]['pay_per_use']
          this.session_lengths_add.pay_per_use_rate = list[index]['pay_per_use_rate']
          this.session_lengths_add.pay_per_use_interval = list[index]['pay_per_use_interval']
          this.session_lengths_add.custom_colors = list[index]['custom_colors']
        } else if (listType === 'time') {
          this.time_add.dow = day
          this.time_add.time_start = formats.format_std_time_to_mil(list[index]['time_start'])
          this.time_add.time_stop = formats.format_std_time_to_mil(list[index]['time_stop'])
          this.time_add.members = list[index]['members']
        }
        modal.show()
      },
      deleteListItem (list, index) {
        delete list.splice(index, 1)
      },
      resetSessionTypes () {
        this.session_types_add = JSON.parse(JSON.stringify(this.session_types_base))
      },
      resetSessionLengths () {
        this.session_lengths_add = JSON.parse(JSON.stringify(this.session_lengths_base))
      },
      resetTime () {
        this.time_add = JSON.parse(JSON.stringify(this.time_add_base))
      },
      resetBlockedTime () {
        this.blocked_times_add = JSON.parse(JSON.stringify(this.blocked_times_base))
      }
    },
    validations: {
      item: {
        name: {
          required
        },
        terminology: {
          required
        },
        reservations: {
          terminology: {
            required
          },
          max_reservations_per_hour: {
            numeric
          },
          max_member_reservations_per_hour: {
            numeric
          },
          max_member_reservations_per_day: {
            numeric
          },
          max_member_reservations_hours: {
            numeric
          },
          max_public_reservations_per_hour: {
            numeric
          }
        }
      },
      session_types_add: {
        name: {
          required
        },
        legend: {
          required
        }
      },
      session_lengths_add: {
        name: {
          required
        },
        button_text: {
          required
        },
        minutes_length: {
          numeric
        },
        minutes_setup: {
          numeric
        },
        pay_per_use_rate: {
          numeric
        },
        pay_per_use_interval: {
          numeric
        }
      },
      time_add: {
        time_start: {
          required
        },
        time_stop: {
          required
        }
      },
      blocked_times_add: {
        date: {
          required
        },
        start_time: {
          required
        },
        end_time: {
          required
        }
      }
    }
  }
</script>

<style scoped>

</style>



