<template>
  <div class="app">
    <AppHeader/>
    <div class="app-body">
      <main class="main">
        <div class="container-fluid p-2">
          <Alerts/>
          <StoreDisplay />
        </div>
      </main>
    </div>
    <AppFooter/>
  </div>
</template>

<script>
import { HeaderCollapsed as AppHeader, Footer as AppFooter, Alerts } from '../components/'
import StoreDisplay from '@/components/StorePages/Store/Display'

export default {
  name: 'display',
  components: {
    AppHeader,
    AppFooter,
    Alerts,
    StoreDisplay
  },
  data () {
    return {
    }
  },
  computed: {
    name () {
      return this.$route.name
    },
    list () {
      return this.$route.matched
    }
  }
}
</script>



