<template>
  <b-nav-item-dropdown right no-caret>
    <template slot="button-content">
      <i class="fa fa-2x fa-user-circle-o"></i>
    </template>
    <b-dropdown-header tag="div" class="text-center"><strong>Account: {{ this.$store.getters.profile }}</strong></b-dropdown-header>
    <b-dropdown-item @click="profile()"><i class="fa fa-user" ></i> Profile</b-dropdown-item>
    <b-dropdown-item @click="signout()"><i class="fa fa-lock"></i> Logout</b-dropdown-item>
  </b-nav-item-dropdown>
</template>
<script>
  import auth from '../../services/auth'
  import router from '../../router/index.js'

  export default {
    name: 'header-dropdown-accnt',
    data: () => {
      return { itemsCount: 42 }
    },
    methods: {
      signout () {
        return auth.signout()
      },
      profile () {
        router.push({name: 'Profile'})
      }
    }
  }
</script>



