<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="6" sm="8">
          <b-card no-body class="mx-4">
            <b-card-body class="p-4">
              <h1>Verify Email</h1>
              <p class="text-muted">Verification code sent to your email account.</p>

              <span class="form-group__message text-danger" v-if="!$v.code.required">Verification Code is Required.</span><span class="form-group__message text-danger" v-if="!$v.code.numeric">Must Contain Numberic Characters.</span>
              <b-input-group class="mb-3" v-bind:class="{ 'form-group--error': $v.code.$error }">
                <div class="input-group-prepend">
                  <span class="input-group-text"><i class="icon-user"></i></span>
                </div>
                <input type="text" class="form-control" placeholder="12345" v-model="code">
              </b-input-group>

              <b-button @click="verify(username, code)" variant="success" block :disabled="$v.$invalid">Confirm Account</b-button>
            </b-card-body>
          </b-card>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
  import auth from '../services/auth'
  import { required, numeric } from 'vuelidate/lib/validators'

  export default {
    name: 'Verify',
    data () {
      return {
        code: '',
        username: ''
      }
    },
    mounted () {
      this.username = this.$route.params.username
    },
    methods: {
      verify (user, code) {
        return auth.verify(user, code)
      }
    },
    validations: {
      code: {
        required,
        numeric
      }
    }
  }
</script>



