<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="8">
          <b-card-group>
            <b-card no-body class="p-4">
              <b-card-body>
                <h1><img src="@/assets/icon.png" width="50" height="50"> Store Admin</h1>
                <p class="text-muted">Forgot Password Change</p>

                <span class="form-group__message text-danger" v-if="!$v.code.required">Verification Code is Required</span>
                <b-input-group class="mb-3">
                  <div class="input-group-prepend"><span class="input-group-text"><i class="icon-user"></i></span></div>
                  <input type="text" class="form-control" placeholder="Verification Code" v-model="code">
                </b-input-group>

                <b-input-group class="mb-3">
                  <div class="input-group-prepend">
                    <span class="input-group-text"><i class="icon-lock"></i></span>
                  </div>
                  <input type="password" class="form-control" placeholder="New Password" v-model="newPassword">
                  <b-input-group-append>
                    <b-btn variant="danger" v-if="score < 3">Too Weak</b-btn>
                    <b-btn variant="success" v-if="score === 3">Perfect</b-btn>
                  </b-input-group-append>
                </b-input-group>

                <span class="form-group__message text-danger" v-if="!$v.newRepeat.sameAsPassword">Passwords Must Be Identical.</span> <span class="form-group__message text-danger" v-if="!$v.newPassword.minLength">Passwords Must Be At Least 8 Characters.</span>
                <b-input-group class="mb-3" v-bind:class="{ 'form-group--error': $v.newRepeat.$error }">
                  <div class="input-group-prepend">
                    <span class="input-group-text"><i class="icon-lock"></i></span>
                  </div>
                  <input type="password" class="form-control" placeholder="Current Password" v-model="newRepeat">
                </b-input-group>
                <b-row>
                  <Alerts />
                </b-row>
                <b-row>
                  <b-col cols="6">
                    <b-button @click="changePassword(username, code, newRepeat)" variant="primary" class="px-4 mt-2" :disabled="$v.$invalid || score < 3">Change</b-button>
                  </b-col>
                  <b-col cols="6" class="text-right">
                  </b-col>
                </b-row>
              </b-card-body>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>
<script>
  import auth from '../services/auth'
  import { required, sameAs, minLength } from 'vuelidate/lib/validators'
  import { Alerts } from '../components/'

  export default {
    name: 'ForgotPasswordChange',
    components: {
      Alerts
    },
    data () {
      return {
        score: 0,
        username: '',
        code: '',
        newPassword: '',
        newRepeat: '',
        error: ''
      }
    },
    mounted () {
      this.username = this.$route.params.username
    },
    watch: {
      newPassword: function (value) {
        this.score = 0
        var hasNumber = /\d/.test(value)
        var hasLowercase = /[a-z]/.test(value)
        var hasUppercase = /[A-Z]/.test(value)
        if (hasNumber) {
          this.score += 1
        }
        if (hasLowercase) {
          this.score += 1
        }
        if (hasUppercase) {
          this.score += 1
        }
      }
    },
    methods: {
      changePassword (username, code, newRepeat) {
        return auth.forgotPasswordChange(username, code, newRepeat)
      },
      changePasswordError (error) {
        this.error = error
      }
    },
    validations: {
      newPassword: {
        minLength: minLength(8)
      },
      newRepeat: {
        sameAsPassword: sameAs('newPassword')
      },
      code: {
        required
      }
    }
  }
</script>



