import formats from '@/services/formats'

export default {

  check_split (lanes) {
    if (lanes > 8) {
      return true
    }
  },
  split_lane (lanes) {
    let lane = parseInt(lanes / 2)
    let remainder = lanes - lane
    if (lane < remainder) {
      lane++
    }
    return lane
  },
  order_sessions (sessions) {
    var sessionsOrdered = {}
    for (var session in sessions) {
      sessionsOrdered[sessions[session].lane] = sessions[session]
    }
    return sessionsOrdered
  },
  setup_add_options (values) {
    // Setup session types for form select. This will process session_length
    // and session type.
    var response = []
    for (var index in values) {
      var optionText = values[index].name
      var option = {
        text: optionText,
        value: values[index]
      }
      try {
        if (!values[index].public) {
          response.push(option)
        }
      } catch (err) {
        response.push(option)
      }
    }
    return response
  },
  default_add_option (values) {
    // Find default value for add options session_length
    // and session_type
    for (var index in values) {
      if (values[index].default) {
        return values[index]
      }
    }
    return null
  },
  setup_sessions (configuration, sessions, firstSet, secondSet) {
    // Setup lane session display on dashboard.
    var response = {
      completeFirstSet: null,
      completeSecondSet: null,
      open: 0,
      occupied: 0,
      expiring: 0,
      expired: 0
    }
    response.completeFirstSet = processSet(configuration, sessions, firstSet)
    response.completeSecondSet = processSet(configuration, sessions, secondSet)
    // Process range summary for dashboard display.
    var summaryFirstSet = processSummary(response.completeFirstSet)
    var summarySecondSet = processSummary(response.completeSecondSet)
    response.open = summaryFirstSet.open + summarySecondSet.open
    response.occupied = summaryFirstSet.occupied + summarySecondSet.occupied
    response.expiring = summaryFirstSet.expiring + summarySecondSet.expiring
    response.expired = summaryFirstSet.expired + summarySecondSet.expired

    return response
  },
  calculate_ppu_bill (sessions) {
    var response = {
      lineItems: []
    }
    var runningMinutes = 0
    var runningTotal = 0
    var currentEPOC = new Date().getTime()
    for (var index in sessions) {
      var ppuIntervalMinutes = sessions[index]['session_length']['pay_per_use_interval']
      var ppuRate = sessions[index]['session_length']['pay_per_use_rate']
      var currentSessionLengthMinutes = parseFloat(((currentEPOC - (sessions[index].session_start * 1000)) / 1000 / 60).toFixed(0))
      var sessionCost = parseFloat(((currentSessionLengthMinutes / ppuIntervalMinutes) * ppuRate).toFixed(2))
      // Only process sessions with positive values.  Setup minutes can cause negative values for start times
      // in the future.
      if (currentSessionLengthMinutes > 0) {
        runningMinutes = runningMinutes + currentSessionLengthMinutes
        runningTotal = runningTotal + sessionCost
        var lineItem = {
          lane: sessions[index].lane,
          name: sessions[index].client_name,
          minutes: currentSessionLengthMinutes.toFixed(0) + ' Min.',
          cost: '$' + sessionCost.toFixed(2)
        }
        response.lineItems.push(lineItem)
      }
    }
    // Add last line item for total minutes and cost
    response.lineItems.push({
      lane: null,
      name: 'TOTAL',
      minutes: runningMinutes.toFixed(0) + ' Min.',
      cost: '$' + runningTotal.toFixed(2)
    })
    response.totalCost = '$' + response.totalCost
    return response
  }
}

function processSummary (completeLaneSet) {
  var setSummary = {
    open: 0,
    occupied: 0,
    expiring: 0,
    expired: 0
  }
  for (var session in completeLaneSet) {
    setSummary[completeLaneSet[session].status]++
  }
  return setSummary
}

function processSet (configuration, sessions, laneSet) {
  var set = []
  var defaultLaneSession = {
    laneBgDefaultColor: '#333',
    laneBgColor: configuration['custom_colors']['open'],
    laneTextDefaultColor: '#eeeeee',
    laneTextColor: configuration['custom_colors']['open_text'],
    barColor: configuration['custom_colors']['bar'],
    borderColor: configuration['custom_colors']['border'],
    clientName: null,
    sessionType: null,
    displayValue: null,
    displayDescription: null
  }
  // Loop through lane sets and assign session values for lane status, color and text displays.
  var sessionData = {}
  for (var lane in laneSet) {
    var laneNumber = {
      lane: laneSet[lane],
      status: 'open'
    }
    if (laneSet[lane] in sessions) {
      var sessionDisplay = processLane(configuration, sessions, laneSet[lane])
      sessionData = Object.assign({}, defaultLaneSession, laneNumber, sessionDisplay)
    } else {
      sessionData = Object.assign({}, laneNumber, defaultLaneSession)
    }
    set.push(sessionData)
  }
  return set
}

function processLane (configuration, sessions, lane) {
  var laneValues = {
    clientName: sessions[lane].client_name,
    sessionType: sessions[lane].session_type.name + ' - ' + sessions[lane].session_length.name
  }
  var currentEPOC = new Date().getTime()
  var sessionLength = sessions[lane]['session_length']
  var expireMinutes = configuration.expiring_flag
  var currentSessionLengthMinutes = ((currentEPOC - (sessions[lane].session_start * 1000)) / 1000 / 60).toFixed(0)
  var currentSessionRemainingMinutes = (((sessions[lane].session_end * 1000) - currentEPOC) / 1000 / 60).toFixed(0)
  var sessionUnlimited = sessionLength.unlimited
  var sessionPayPerUse = sessionLength.pay_per_use
  var laneState = 'occupied'

  // Determine lane display value
  if (sessionUnlimited) {
    laneValues['displayValue'] = 'UNLIMITED'
    laneValues['displayDescription'] = 'EXPIRES:'
  } else if (sessionPayPerUse) {
    laneValues['displayValue'] = formats.format_minutes_display(currentSessionLengthMinutes)
    laneValues['displayDescription'] = 'MINUTES USED:'
  } else {
    laneValues['displayDescription'] = 'EXPIRES:'
    if (currentSessionRemainingMinutes <= expireMinutes && currentSessionRemainingMinutes > 0) {
      laneState = 'expiring'
      laneValues['displayValue'] = formats.format_minutes_display(currentSessionRemainingMinutes)
    } else if (currentSessionRemainingMinutes <= 0) {
      laneState = 'expired'
      laneValues['displayValue'] = 'EXPIRED'
    } else {
      laneValues['displayValue'] = formats.format_minutes_display(currentSessionRemainingMinutes)
    }
  }
  // Determine custom colors for lane state and present it for occupied.
  if (sessionLength['custom_colors']['enable_custom']) {
    laneValues['laneBgColor'] = sessionLength['custom_colors']['button_color']
    laneValues['laneTextColor'] = sessionLength['custom_colors']['button_text']
  } else {
    if (laneState === 'occupied') {
      laneValues['laneBgColor'] = configuration['custom_colors']['occupied']
      laneValues['laneTextColor'] = configuration['custom_colors']['occupied_text']
    }
  }
  // Regardless of custom color, override for states expiring and expired.
  if (laneState === 'expiring') {
    laneValues['laneBgColor'] = configuration['custom_colors']['expiring']
    laneValues['laneTextColor'] = configuration['custom_colors']['expiring_text']
  } else if (laneState === 'expired') {
    laneValues['laneBgColor'] = configuration['custom_colors']['expired']
    laneValues['laneTextColor'] = configuration['custom_colors']['expired_text']
  }
  laneValues['status'] = laneState
  return laneValues
}



