          <span class="form-group__message text-danger" v-if="!$v.editReservation.client_name.required">Customer Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Phone Number"
          label-for="customerPhone"
          :label-cols="2"
          :horizontal="true">
          <masked-input
            type="tel"
            name="phone"
            class="form-control"
            v-model="editReservation.phone"
            :mask="['(' , /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]"
            :guide="true"
            placeholderChar="#" @focus="clearReservationPhone()">
          </masked-input>
          <span class="form-group__message text-danger" v-if="!$v.editReservation.phone.required">Phone is Required</span>
        </b-form-group>

        <b-form-group
          label="Paid in Full"
          label-for="paidInFull"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="paidInFull"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="editReservation.paid_in_full">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Paid Partial"
          label-for="paidPartial"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="paidPartial"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="editReservation.paid_partial">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Paid Partial ($)"
          label-for="paidPartialAmount"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="paidPartialAmount" type="text" v-model="editReservation.paid_partial_amount"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.editReservation.paid_partial_amount.required">Partial Amount is Required</span>
        </b-form-group>

        <b-button class="mt-3" block variant="outline-primary" @click="reservationEdit(true)"><i class="fa fa-calendar"></i> Change Reservation</b-button>
        <b-btn class="mt-3" variant="outline-success" block @click="reservationEdit(false)" :disabled="$v.editReservation.$invalid">Save Information</b-btn>

      </div>
    </b-modal>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import dashboard from '@/services/dashboard'
  import router from '@/router'
  import formats from '@/services/formats'
  import VueTimers from 'vue-timers/mixin'
  import MaskedInput from 'vue-text-mask'
  import { required, numeric } from 'vuelidate/lib/validators'

  export default {
    name: 'RangeDashboard',
    mixins: [VueTimers],
    components: {
      MaskedInput
    },
    data () {
      return {
        rangeID: null,
        configuration: null,
        customColors: null,
        move_operation: null,
        sessions: [],
        sessionsOrdered: {},
        sessionsFirst: [],
        sessionsSecond: [],
        splitLanes: false,
        splitColumns: 'col-md-12',
        splitFirstSet: [],
        splitSecondSet: [],
        selectedLanes: [],
        moveSelectedLanes: [],
        selectedIDs: [],
        moveSelectedIDs: [],
        selectedLanesPPU: [],
        PPUInvoice: {},
        loading: true,
        redirectToReservationPageConvert: false,
        redirectToReservationPageUpdateReservation: false,
        pendingRefresh: false,
        options: {
          session_lengths: [],
          selected_session_length: null,
          default_session_length: null,
          session_types: [],
          selected_session_type: null,
          default_session_type: null
        },
        customSessionLength: {
          name: 'Custom',
          button_text: 'Custom',
          pay_per_use: false,
          unlimited: false,
          minutes_length: 0,
          minutes_setup: 0
        },
        addSession: {
          client_name: null,
          reservation: false,
          source: null,
          custom_hours: 0,
          custom_minutes: 0,
          member_id: null
        },
        extendSession: {
          custom_hours: 0,
          custom_minutes: 0
        },
        addWaitingList: {
          client_name: null,
          phone: '#',
          lanes: 1,
          specific_lanes: [],
          paid_in_full: false,
          paid_partial: false,
          paid_partial_amount: '0',
          custom_hours: 0,
          custom_minutes: 0,
          member_id: null
        },
        editWaitingList: {
          client_name: null,
          phone: '#',
          lanes: 1,
          specific_lanes: [],
          paid_in_full: false,
          paid_partial: false,
          paid_partial_amount: '0',
          custom_hours: 0,
          custom_minutes: 0
        },
        editReservation: {
          client_name: null,
          phone: '#',
          paid_in_full: false,
          paid_partial: false,
          paid_partial_amount: '0'
        },
        summary: {
          open: 0,
          occupied: 0,
          expiring: 0,
          expired: 0
        },
        waiting_list: [],
        waiting_list_edit_id: null,
        waiting_list_remove_ID: null,
        waiting_list_fields: {
          queue: {
            label: 'Queue',
            sortable: true
          },
          client_name: {
            label: 'Name'
          },
          lanes: {
            label: 'Request'
          },
          source: {
            label: 'Source'
          },
          checked_in: {
            label: 'Arrived',
            formatter: (value) => formats.format_yes_no(value)
          },
          reservation: {
            label: 'Res.',
            formatter: (value) => formats.format_yes_no(value)
          },
          paid: {
            label: 'Paid'
          },
          id: {
            label: 'Action'
          }
        },
        reservation_edit_id: null,
        active_members: [],
        active_filter: null,
        active_member_fields: [
          {
            key: 'name',
            label: 'Member Name',
            sortable: true
          },
          {
            key: 'status',
            label: 'Status'
          },
          {
            key: 'show_details',
            label: 'Detail'
          }
        ],
        active_currentPage: 1,
        active_perPage: 5,
        active_totalRows: 0
      }
    },
    timers: {
      refreshDashboard: {time: 60000, autostart: true, repeat: true},
      refreshMembers: {time: 300000, autostart: true, repeat: true}
    },
    mounted: function () {
      this.rangeID = this.$route.params.id
      this.getInitialRangeConfiguration()
      this.getWaitingList()
      this.getActiveMembers()
    },
    methods: {
      refreshDashboard () {
        if (!this.pendingRefresh) {
          this.getRangeSessions()
          this.getWaitingList()
        }
      },
      refreshMembers () {
        if (!this.pendingRefresh) {
          this.getActiveMembers()
        }
      },
      getActiveMembers () {
        this.loading = true
        rangepro.get_secure('store/members/active', this.handleActiveMemberResponse, 'GetStoreActiveMembers')
      },
      getInitialRangeConfiguration () {
        this.loading = true
        rangepro.get_secure('store/range/' + this.rangeID, this.handleInitialConfigurationResponse, 'GetStoreRanges')
      },
      getRangeSessions () {
        this.loading = true
        rangepro.get_secure('store/range/' + this.rangeID + '/sessions', this.handleSessionsResponse, 'GetStoreRanges')
      },
      getWaitingList () {
        this.loading = true
        rangepro.get_secure('store/range/' + this.rangeID + '/waiting_list/queue/' + formats.get_date(), this.handleWaitingListResponse, 'GetStoreRanges')
      },
      deleteSessions () {
        this.loading = true
        if (this.selectedIDs.length > 0) {
          var sessions = {
            session: {
              ids: this.selectedIDs
            }
          }
          rangepro.delete_bulk_secure('store/range/' + this.rangeID + '/sessions/bulk', sessions, this.handleBulkUpdateResponse, 'DeleteRangeSession', null)
        }
      },
      addSessions () {
        this.loading = true
        if (this.selectedLanes.length > 0) {
          var sessions = {
            session: {
              lanes: this.selectedLanes,
              data: {
                client_name: this.addSession.client_name,
                reservation: this.addSession.reservation,
                source: this.addSession.source,
                session_length: this.options.selected_session_length,
                session_type: this.options.selected_session_type
              }
            }
          }
          if (this.addSession.member_id) {
            sessions.session.data.member_id = this.addSession.member_id
          }
          rangepro.post_secure('store/range/' + this.rangeID + '/sessions/bulk', sessions, this.handleBulkUpdateResponse, 'AddRangeSessions', null)
        }
      },
      extendSessions () {
        this.loading = true
        if (this.selectedLanes.length > 0) {
          var sessions = {
            session: {
              ids: this.selectedIDs,
              data: this.options.selected_session_length
            }
          }
          rangepro.put_secure('store/range/' + this.rangeID + '/sessions/bulk', sessions, this.handleBulkUpdateResponse, 'ExtendRangeSessions', null)
        }
      },
      moveSessions () {
        this.loading = true
        if (this.selectedLanes.length > 0 && this.moveSelectedLanes.length > 0) {
          var sessions = {
            session: {
              source_ids: this.selectedIDs,
              destination_ids: this.moveSelectedIDs,
              source_lanes: this.selectedLanes,
              destination_lanes: this.moveSelectedLanes
            }
          }
          rangepro.put_secure('store/range/' + this.rangeID + '/sessions/move', sessions, this.handleBulkUpdateResponse, 'MoveRangeSessions', null)
        }
      },
      saveWaitingList () {
        this.loading = true
        var waitingList = {
          waiting_list: {
            client_name: this.addWaitingList.client_name,
            lanes: this.addWaitingList.lanes,
            phone: this.addWaitingList.phone,
            paid_in_full: this.addWaitingList.paid_in_full,
            paid_partial: this.addWaitingList.paid_partial,
            paid_partial_amount: this.addWaitingList.paid_partial_amount,
            session_length: this.options.selected_session_length,
            session_type: this.options.selected_session_type
          }
        }
        if (this.addWaitingList.member_id) {
          waitingList.waiting_list.member_id = this.addWaitingList.member_id
        }
        rangepro.post_secure('store/range/' + this.rangeID + '/waiting_list', waitingList, this.handleWaitingListAction, 'AddWaitingList', null)
      },
      updateWaitingList () {
        this.loading = true
        var waitingList = {
          waiting_list: {
            client_name: this.editWaitingList.client_name,
            lanes: this.editWaitingList.lanes,
            phone: this.editWaitingList.phone,
            paid_in_full: this.editWaitingList.paid_in_full,
            paid_partial: this.editWaitingList.paid_partial,
            paid_partial_amount: this.editWaitingList.paid_partial_amount,
            session_length: this.options.selected_session_length,
            session_type: this.options.selected_session_type
          }
        }
        rangepro.put_secure('store/range/' + this.rangeID + '/waiting_list/' + this.waiting_list_edit_id, waitingList, this.handleWaitingListAction, 'AddWaitingList', null)
      },
      updateReservation () {
        this.loading = true
        var reservation = {
          reservations: {
            client_name: this.editReservation.client_name,
            phone: this.editReservation.phone,
            paid_in_full: this.editReservation.paid_in_full,
            paid_partial: this.editReservation.paid_partial,
            paid_partial_amount: this.editReservation.paid_partial_amount
          }
        }
        rangepro.put_secure('store/range/' + this.rangeID + '/reservation/' + this.reservation_edit_id, reservation, this.handleWaitingListAction, 'EditReservation', null)
      },
      checkInCustomer (waitingListID) {
        rangepro.put_secure('store/range/' + this.rangeID + '/waiting_list/' + waitingListID + '/check_in', {}, this.handleWaitingListAction, 'CheckInWaitingList', null)
        this.loading = true
      },
      checkInReservationCustomer (reservationID) {
        rangepro.put_secure('store/range/' + this.rangeID + '/reservation/' + reservationID + '/check_in', {}, this.handleWaitingListAction, 'CheckInWaitingList', null)
        this.loading = true
      },
      markUsedReservationCustomer (reservationID) {
        rangepro.put_secure('store/range/' + this.rangeID + '/reservation/' + reservationID + '/used', {}, this.handleWaitingListAction, 'MarkUsedWaitingList', null)
        this.loading = true
      },
      deleteWaitingListCustomer (waitingListID) {
        rangepro.delete_secure('store/range/' + this.rangeID + '/waiting_list', waitingListID, this.handleWaitingListAction, 'CheckInWaitingList', null)
        this.loading = true
      },
      deleteReservationCustomer (reservationID) {
        rangepro.delete_secure('store/range/' + this.rangeID + '/reservation', reservationID, this.handleWaitingListAction, 'DeleteReservation', null)
        this.loading = true
      },
      handleActiveMemberResponse (data, success) {
        if (success) {
          this.active_members = data.members
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleInitialConfigurationResponse (data, success) {
        if (success) {
          this.configuration = data.range
          // Setup add modal options for session_lengths and session_types
          this.options.session_lengths = dashboard.setup_add_options(this.configuration.session_lengths)
          this.options.session_types = dashboard.setup_add_options(this.configuration.session_types)
          this.default_session_length = dashboard.default_add_option(this.configuration.session_lengths)
          this.default_session_type = dashboard.default_add_option(this.configuration.session_types)
          this.options.selected_session_length = this.default_session_length
          this.options.selected_session_type = this.default_session_type
          this.waiting_list_fields.lanes.label = this.configuration.terminology
          this.getRangeSessions()
        }
      },
      handleSessionsResponse (data, success) {
        if (success) {
          // Set sessions and clear out lane split.
          this.sessions = data.sessions
          this.splitFirstSet = []
          this.splitSecondSet = []
          // Process lane segments being called sets in dashboard.  Determine layout then process
          // the lanes returned and display.
          this.splitLanes = dashboard.check_split(this.configuration.lanes)
          var countLanes = 1
          // If lanes are greater than 8 then split between two sets.
          if (this.splitLanes) {
            // Get the lane number as the split point.
            let splitLane = dashboard.split_lane(this.configuration.lanes)
            this.splitColumns = 'col-md-6'
            // Process each set left and right.
            while (countLanes <= splitLane) {
              this.splitFirstSet.push(countLanes)
              countLanes++
            }
            while (countLanes <= this.configuration.lanes) {
              this.splitSecondSet.push(countLanes)
              countLanes++
            }
          } else {
            // If lanes are less than 8, just display in one set.
            countLanes = 1
            while (countLanes <= this.configuration.lanes) {
              this.splitFirstSet.push(countLanes)
              countLanes++
            }
          }
          this.loading = false
          // Process sessions into their respective sets for display.
          this.processSessions()
        } else {
          this.loading = false
        }
      },
      handleWaitingListResponse (data, success) {
        if (success) {
          this.waiting_list = data.waiting_list
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleWaitingListAction (data, success) {
        if (success) {
          if (this.redirectToReservationPageConvert) {
            router.push({name: 'Store Range Reservations Convert', params: {id: this.rangeID, waitinglistID: this.waiting_list_edit_id}})
          } else if (this.redirectToReservationPageUpdateReservation) {
            router.push({name: 'Store Range Reservations Change', params: {id: this.rangeID, reservationID: this.reservation_edit_id}})
          }
          this.options.selected_session_length = this.default_session_length
          this.options.selected_session_type = this.default_session_type
          this.addWaitingList.client_name = null
          this.addWaitingList.phone = '(###) ###-####'
          this.addWaitingList.lanes = 1
          this.addWaitingList.paid_in_full = false
          this.addWaitingList.paid_partial = false
          this.addWaitingList.paid_partial_amount = '0'
          this.addWaitingList.custom_hours = 0
          this.addWaitingList.custom_minutes = 0
          this.addWaitingList.member_id = null
          this.editWaitingList.client_name = null
          this.editWaitingList.phone = '(###) ###-####'
          this.editWaitingList.lanes = 1
          this.editWaitingList.paid_in_full = false
          this.editWaitingList.paid_partial = false
          this.editWaitingList.paid_partial_amount = '0'
          this.editWaitingList.custom_hours = 0
          this.editWaitingList.custom_minutes = 0
          this.getWaitingList()
          this.selectedLanes = []
        }
        this.loading = false
      },
      handleBulkUpdateResponse (data, success) {
        if (success) {
          // Reset Forms
          this.options.selected_session_length = this.default_session_length
          this.options.selected_session_type = this.default_session_type
          this.addSession.client_name = null
          this.addSession.custom_hours = 0
          this.addSession.custom_minutes = 0
          this.extendSession.custom_hours = 0
          this.extendSession.custom_minutes = 0
          this.addSession.member_id = null
          this.refreshDashboard()
        }
        this.selectedIDs = []
        this.moveSelectedIDs = []
        this.loading = false
      },
      processSessions () {
        // Process sessions and configure presentation of lane data.
        this.sessionsOrdered = dashboard.order_sessions(this.sessions)
        var processed = dashboard.setup_sessions(this.configuration, this.sessionsOrdered, this.splitFirstSet, this.splitSecondSet)
        this.sessionsFirst = processed.completeFirstSet
        this.sessionsSecond = processed.completeSecondSet
        // Present summary data about the range.
        this.summary.open = processed.open
        this.summary.occupied = processed.occupied
        this.summary.expiring = processed.expiring
        this.summary.expired = processed.expired
      },
      addRemoveSelectedLane (lane) {
        // Add and remove lane IDs from selected array.
        if (this.move_operation) {
          // If the length of move selected does not equal, you can select more or if they are selecting a move lane that was already selected.
          // BUT, the lane cannot be in the source lanes
          if ((this.moveSelectedLanes.length !== this.selectedLanes.length || this.moveSelectedLanes.indexOf(lane)) > -1 && !(this.selectedLanes.indexOf(lane) > -1)) {
            if (this.moveSelectedLanes.indexOf(lane) > -1) {
              var moveIndex = this.moveSelectedLanes.indexOf(lane)
              this.moveSelectedLanes.splice(moveIndex, 1)
            } else {
              this.moveSelectedLanes.push(lane)
            }
          }
        } else {
          if (this.selectedLanes.indexOf(lane) > -1) {
            var index = this.selectedLanes.indexOf(lane)
            this.selectedLanes.splice(index, 1)
          } else {
            this.selectedLanes.push(lane)
          }
        }
      },
      processSelectedLanes (method) {
        // For multi-select operations, loop through selections and retrieve lane information.  Then
        // submit the passed operation.
        this.loading = true
        this.selectedLanesPPU = []
        this.selectedIDs = []
        this.moveSelectedIDs = []
        for (var index in this.selectedLanes) {
          // Check if index is last in array and set pendingRefresh to false.
          var lane = this.selectedLanes[index]
          try {
            var id = this.sessionsOrdered[lane].id
            this.selectedIDs.push(id)
            // Check if this is a PPU lane, for delete session operations. This will auto
            // calculate the used session amount.
            if (this.sessionsOrdered[lane]['session_length']['pay_per_use']) {
              this.selectedLanesPPU.push(this.sessionsOrdered[lane])
            }
          } catch (err) {
            // Log for debug purposes
            console.log('Unable to find current session for lane: ' + lane)
          }
        }
        for (var moveIndex in this.moveSelectedLanes) {
          // Check if index is last in array and set pendingRefresh to false.
          var moveLane = this.moveSelectedLanes[moveIndex]
          try {
            var moveID = this.sessionsOrdered[moveLane].id
            this.moveSelectedIDs.push(moveID)
            // Check if this is a PPU lane, for delete session operations. This will auto
            // calculate the used session amount.
            if (this.sessionsOrdered[moveLane]['session_length']['pay_per_use']) {
              this.selectedLanesPPU.push(this.sessionsOrdered[moveLane])
            }
          } catch (err) {
            // If there is no move destination ID, place NONE
            this.moveSelectedIDs.push('NONE')
            // Log for debug purposes
            console.log('Unable to find current session for lane: ' + lane)
          }
        }
        // Call passed method, i.e. sessionEnd or sessionAdd then clear selectedLanes
        method()
        this.selectedLanes = []
      },
      sessionEnd () {
        this.$refs.confirmDeleteSessions.hide()
        this.deleteSessions()
        // If there are PPU lanes selected, calculate the total bill and present to dashboard.
        // This will NOT prevent deletion of the sessions.
        if (this.selectedLanesPPU.length > 0) {
          this.PPUInvoice = dashboard.calculate_ppu_bill(this.selectedLanesPPU)
          this.$refs.ppuInvoice.show()
        }
      },
      sessionAdd () {
        this.$refs.sessionAdd.hide()
        // If custom hours or minutes is selected, use custom session length.
        this.setCustomSessionLength(this.addSession.custom_hours, this.addSession.custom_minutes)
        this.addSessions()
      },
      sessionExtend () {
        this.$refs.sessionExtend.hide()
        // If custom hours or minutes is selected, use custom session length.
        this.setCustomSessionLength(this.extendSession.custom_hours, this.extendSession.custom_minutes)
        this.extendSessions()
      },
      sessionMove () {
        if (this.move_operation) {
          this.move_operation = false
          // move the sessions here
          this.processSelectedLanes(this.moveSessions)
          this.moveSelectedLanes = []
        } else {
          this.move_operation = true
        }
      },
      sessionsClear () {
        var i = 1
        while (i <= this.configuration.lanes) {
          this.selectedLanes.push(i)
          i++
        }
        this.$refs.confirmClearAllSessions.hide()
        this.processSelectedLanes(this.sessionEnd)
      },
      waitingListAdd () {
        this.$refs.waitinglistAdd.hide()
        // If custom hours or minutes is selected, use custom session length.
        this.setCustomSessionLength(this.addWaitingList.custom_hours, this.addWaitingList.custom_minutes)
        this.saveWaitingList()
      },
      waitingListEdit (convertReservation) {
        this.$refs.waitinglistEdit.hide()
        // If custom hours or minutes is selected, use custom session length.
        this.setCustomSessionLength(this.editWaitingList.custom_hours, this.editWaitingList.custom_minutes)
        this.updateWaitingList()
        if (convertReservation) {
          this.redirectToReservationPageConvert = true
        }
      },
      reservationEdit (changeReservation) {
        this.$refs.reservationEdit.hide()
        this.updateReservation()
        if (changeReservation) {
          this.redirectToReservationPageUpdateReservation = true
        }
      },
      moveWaitingListCustomer (waitingListID) {
        if (this.selectedLanes.length > 0) {
          for (var index in this.waiting_list) {
            if (this.waiting_list[index]['id'] === waitingListID) {
              this.addSession.client_name = this.waiting_list[index]['client_name']
              this.options.selected_session_length = this.waiting_list[index]['session_length']
              this.options.selected_session_type = this.waiting_list[index]['session_type']
              this.addSession.member_id = this.waiting_list[index]['member_id']
              this.addSessions()
              this.loading = true
              this.deleteWaitingListCustomer(waitingListID)
            }
          }
        }
      },
      moveReservationCustomer (reservationID) {
        if (this.selectedLanes.length > 0) {
          for (var index in this.waiting_list) {
            if (this.waiting_list[index]['id'] === reservationID) {
              this.addSession.client_name = this.waiting_list[index]['client_name']
              this.addSession.reservation = true
              this.addSession.reservation_source = this.waiting_list[index]['source']
              this.options.selected_session_length = this.waiting_list[index]['session_length']
              this.options.selected_session_type = this.waiting_list[index]['session_type']
              this.addSession.member_id = this.waiting_list[index]['member_id']
              this.addSessions()
              this.loading = true
              this.markUsedReservationCustomer(reservationID)
            }
          }
        }
      },
      editWaitingListCustomer (waitingListID, ID) {
        this.waiting_list_edit_id = ID
        this.editWaitingList.client_name = this.waiting_list[waitingListID]['client_name']
        this.editWaitingList.phone = this.waiting_list[waitingListID]['phone']
        this.editWaitingList.lanes = this.waiting_list[waitingListID]['lanes']
        this.editWaitingList.paid_in_full = this.waiting_list[waitingListID]['paid_in_full']
        this.editWaitingList.paid_partial = this.waiting_list[waitingListID]['paid_partial']
        this.editWaitingList.paid_partial_amount = this.waiting_list[waitingListID]['paid_partial_amount']
        for (var x in this.options.session_lengths) {
          if (this.options.session_lengths[x]['text'] === this.waiting_list[waitingListID]['session_length']['name']) {
            this.options.selected_session_length = this.options.session_lengths[x]['value']
          }
        }
        for (var y in this.options.session_types) {
          if (this.options.session_types[y]['text'] === this.waiting_list[waitingListID]['session_type']['name']) {
            this.options.selected_session_type = this.options.session_types[y]['value']
          }
        }
        if (this.waiting_list[waitingListID]['session_length']['name'] === 'Custom') {
          this.editWaitingList.custom_hours = formats.get_hours(this.waiting_list[waitingListID]['session_length']['minutes_length'])
          this.editWaitingList.custom_minutes = formats.get_minutes(this.waiting_list[waitingListID]['session_length']['minutes_length'])
        }
        this.$refs.waitinglistEdit.show()
      },
      editReservationCustomer (reservationID, ID) {
        this.reservation_edit_id = ID
        this.editReservation.client_name = this.waiting_list[reservationID]['client_name']
        this.editReservation.phone = this.waiting_list[reservationID]['phone']
        this.editReservation.paid_in_full = this.waiting_list[reservationID]['paid_in_full']
        this.editReservation.paid_partial = this.waiting_list[reservationID]['paid_partial']
        this.editReservation.paid_partial_amount = this.waiting_list[reservationID]['paid_partial_amount']
        this.$refs.reservationEdit.show()
      },
      confirmRemoveFromWaitingList (waitingListID) {
        this.waiting_list_remove_ID = waitingListID
        this.$refs.confirmDeleteWaitingList.show()
      },
      confirmRemoveReservation (reservationID) {
        this.waiting_list_remove_ID = reservationID
        this.$refs.confirmDeleteReservation.show()
      },
      removeWaitingListCustomer () {
        this.$refs.confirmDeleteWaitingList.hide()
        this.deleteWaitingListCustomer(this.waiting_list_remove_ID)
      },
      removeReservationCustomer () {
        this.$refs.confirmDeleteReservation.hide()
        this.deleteReservationCustomer(this.waiting_list_remove_ID)
      },
      confirmDeleteSessions () {
        this.$refs.confirmDeleteSessions.show()
      },
      confirmClearAllSessions () {
        this.$refs.confirmClearAllSessions.show()
      },
      selectAllLanes () {
        var lanes = this.configuration.lanes
        var x = 1
        while (x <= lanes) {
          this.addRemoveSelectedLane(x)
          x += 1
        }
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      },
      setCustomSessionLength (customHours, customMinutes) {
        if (customHours > 0 || customMinutes > 0) {
          var sessionMinutes = 0
          if (this.addSession.custom_hours > 0) {
            sessionMinutes = parseInt(customHours) * 60
          }
          if (this.extendSession.custom_hours > 0) {
            sessionMinutes = parseInt(customHours) * 60
          }
          if (this.addWaitingList.custom_hours > 0) {
            sessionMinutes = parseInt(customHours) * 60
          }
          if (this.editWaitingList.custom_hours > 0) {
            sessionMinutes = parseInt(customHours) * 60
          }
          sessionMinutes += parseInt(customMinutes)
          this.customSessionLength.minutes_length = sessionMinutes
          this.options.selected_session_length = this.customSessionLength
        }
      },
      clearPhone () {
        if (this.addWaitingList.phone === '(###) ###-####') {
          this.addWaitingList.phone = ''
        }
      },
      clearReservationPhone () {
        if (this.editReservation.phone === '(###) ###-####') {
          this.editReservation.phone = ''
        }
      },
      clearWaitingListPhone () {
        if (this.editWaitingList.phone === '(###) ###-####') {
          this.editWaitingList.phone = ''
        }
      },
      formatAmount (value) {
        return formats.format_rangepro_amount(value)
      },
      formatMinutes (value) {
        return formats.format_minutes_display(value)
      },
      formatTime (value) {
        return formats.format_ts(value)
      },
      setMemberAddSession (firstName, lastName, id) {
        this.addSession.client_name = firstName + ' ' + lastName
        this.addSession.member_id = id
      },
      setMemberAddWaitingList (firstName, lastName, id, phone) {
        this.addWaitingList.client_name = firstName + ' ' + lastName
        this.addWaitingList.member_id = id
        this.addWaitingList.phone = phone
      }
    },
    validations: {
      addSession: {
        client_name: {
          required
        },
        custom_hours: {
          required,
          numeric
        },
        custom_minutes: {
          required,
          numeric
        }
      },
      extendSession: {
        custom_hours: {
          required,
          numeric
        },
        custom_minutes: {
          required,
          numeric
        }
      },
      addWaitingList: {
        client_name: {
          required
        },
        phone: {
          required
        },
        custom_hours: {
          required,
          numeric
        },
        custom_minutes: {
          required,
          numeric
        },
        paid_partial_amount: {
          required
        }
      },
      editWaitingList: {
        client_name: {
          required
        },
        phone: {
          required
        },
        custom_hours: {
          required,
          numeric
        },
        custom_minutes: {
          required,
          numeric
        },
        paid_partial_amount: {
          required
        }
      },
      editReservation: {
        client_name: {
          required
        },
        phone: {
          required
        },
        paid_partial_amount: {
          required
        }
      }
    }
  }
</script>

<style scoped>
.dashboard-border-dark {
  border: 1px solid #0b0e0f;
}

.dashboard-border-light {
  border: 1px solid #f7f7f7;
}

.lane-wrapper {
  position: relative;
  overflow: hidden;
  margin-left: 0px;
  margin-right: 0px;
  padding-left: 2px !important;
  padding-right: 2px !important;
}

.lane {
  width: 100%;
  height: 45px;
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
}

.lane:hover{
  color: #eee !important;
  background-color: #222 !important;
}

.lane-nomargin {
  padding-top: 1px !important;
  padding-bottom: 1px !important;
  padding-left: 2px !important;
  padding-right: 2px !important;
  margin-left: 0px;
  margin-right: 0px;
}

.lane-number {
  font-size: 1.0em;
  position: relative;
  float: left;
  margin-top: 2px;
  margin-left: 2px;
  padding: 6px;
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
}

/* LANE SELECTION AND LANE MOVE */
.lane_selection {
  position: relative;
  float: right;
  margin-top: 10px;
  margin-right: 5px;
  border: 1px solid #5C5C5C;
}

.button_notselected {
  color: #eee;
  background-color: black;
}

.button_selected {
  color: #000 !important;
  background-color: #33CC33 !important;
}

.button_move_selected {
  color: #000 !important;
  background-color: #f9ee4f !important;
}

.btn-circle {
  width: 24px;
  height: 24px;
  text-align: center;
  padding: 4px;
  font-size: 10px;
  line-height: 1.42;
  border-radius: 15px;
}

/* LANE CUSTOMER INFORMATION */
.lane .time-info {
  position: relative;
  float: right;
  margin-right: 10px;
}

.lane .client-info {
  position: relative;
  float: left;
  margin-left: 15px;
}

.lane .top-info {
  position: relative;
  top: 2px;
  left: 0px;
  font-family: 'Oswald', sans-serif;
  font-size: 10px;
  color: inherit;
}

.lane .top-info-right {
  position: relative;
  float: left;
  margin-top: 4px;
  font-family: 'Oswald', sans-serif;
  font-size: 10px;
  color: inherit;
}

.lane .bottom-info-right {
  position: relative;
  float: left;
  margin-bottom: 0px;
  font-family: 'Oswald', sans-serif;
  font-size: 16px;
  color: inherit;
}

.lane .bottom-info {
  position: relative;
  bottom: 2px;
  left: 0px;
  font-family: 'Oswald', sans-serif;
  font-size: 16px;
  color: inherit;
}

.lane-label {
  display: block;
  margin-left: 40%;
  margin-right: auto;
  width: 50%;
}

.lane-label .label {
  position: relative;
  top: 6px;
  left: 0px;
  opacity: 0.3;
  font-family: 'Oswald', sans-serif;
  font-size: 20px;
  color: inherit;
}
</style>



