import Vue from 'vue'
import Vuex from 'vuex'
import persist from 'vuex-localstorage'
import auth from './auth.js'

Vue.use(Vuex)

const store = new Vuex.Store({
  plugins: [persist({
    namespace: 'aws-vuejs-cognito',
    initialState: {},
    expires: 7 * 24 * 60 * 60 * 1e3 // 1 week
  })],
  modules: {
    auth
  }
})

export default store



