<template>
  <div class="col-12">

    <b-card class="bg-white p-0" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Search</strong> Range History <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
        <span class="pull-right mt-2">
          <router-link :to="{ name: 'Store Range Dashboard', params: { id: rangeID }}"><b-button type="submit" size="sm" variant="primary"><i class="fa fa-th"></i> Dashboard</b-button></router-link>
        </span>
      </div>
      <div ref="card" class="text-dark">

        <b-form-group
          label="Number of Days"
          label-for="searchDays"
          :label-cols="3"
          :horizontal="true">
          <b-form-select id="searchDays"
                         :plain="true"
                         :options="[
                         { value: 1, text: '1 Day'}, { value: 7, text: '7 Days'}, {value: 14, text: '14 Days'}, { value: 30, text: '30 Days'},
                         { value: 60, text: '60 Days'}, { value: 90, text: '90 Days'}, { value: 180, text: '180 Days'}
                       ]" v-model="search_days">
          </b-form-select>
          <span class="form-group__message text-danger" v-if="!$v.search_days.required">Search Date is Required</span>
        </b-form-group>

      </div>
    </b-card>

    <b-card class="bg-white col-md-12">
        <h2 class="text-center">Target Usage</h2>
        <target-chart
          ref="targetUsageChart"
          :chart-data="target_chart_data"
          :options="{responsive: true, maintainAspectRatio: false}"
          :height="250">
        </target-chart>
    </b-card>

    <b-card-group deck class="mb-4">
      <b-card class="bg-white col-md-6">
        <h2 class="text-center">Sessions Per Month</h2>
        <sessions-per-month-chart
          ref="sessionsPerMonthChart"
          :chart-data="sessions_per_month_data"
          :options="{responsive: true, maintainAspectRatio: false}"
          :height="300">
        </sessions-per-month-chart>
      </b-card>

      <b-card class="bg-white col-md-6">
        <h2 class="text-center">Hours Per Month</h2>
        <hours-per-month-chart
          ref="hoursPerMonthChart"
          :chart-data="hours_per_month_data"
          :options="{responsive: true, maintainAspectRatio: false}"
          :height="300">
        </hours-per-month-chart>
      </b-card>
    </b-card-group>

    <b-card-group deck>
      <b-card class="bg-white col-md-6">
        <h2 class="text-center">Session Lengths</h2>
        <session-lengths-chart
          ref="sessionLengthsChart"
          :chart-data="session_lengths_data"
          :options="{responsive: true, maintainAspectRatio: false}"
          :height="400">
        </session-lengths-chart>
      </b-card>

      <b-card class="bg-white col-md-6">
        <h2 class="text-center">Session Types</h2>
        <session-lengths-chart
          ref="sessionTypesChart"
          :chart-data="session_types_data"
          :options="{responsive: true, maintainAspectRatio: false}"
          :height="400">
        </session-lengths-chart>
      </b-card>
    </b-card-group>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import { required } from 'vuelidate/lib/validators'
  import TargetChart from './RangeTargetUsageChart'
  import SessionsPerMonthChart from './RangeSessionsPerMonthChart'
  import HoursPerMonthChart from './RangeHoursPerMonthChart'
  import SessionLengthsChart from './RangeSessionLengthsChart'
  import SessionTypesChart from './RangeSessionTypesChart'

  export default {
    name: 'RangeHistory',
    components: {
      TargetChart,
      SessionsPerMonthChart,
      HoursPerMonthChart,
      SessionLengthsChart,
      SessionTypesChart
    },
    data () {
      return {
        rangeID: null,
        search_days: 30,
        target_chart_data: {
          labels: [],
          datasets: [
            {
              label: 'Usage Hours',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        },
        sessions_per_month_data: {
          labels: [],
          datasets: [
            {
              label: '# Sessions',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        },
        hours_per_month_data: {
          labels: [],
          datasets: [
            {
              label: '# Sessions',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        },
        session_lengths_data: {
          labels: [],
          datasets: [
            {
              label: '# Sessions',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        },
        session_types_data: {
          labels: [],
          datasets: [
            {
              label: '# Sessions',
              backgroundColor: '#1c6eb5',
              data: []
            }
          ]
        }
      }
    },
    mounted: function () {
      this.rangeID = this.$route.params.id
      this.getTargetUsage()
      this.getSessionsPerMonth()
      this.getHoursPerMonth()
      this.getSessionLengths()
      this.getSessionTypes()
    },
    watch: {
      search_days: function (val) {
        this.getTargetUsage()
        this.getSessionsPerMonth()
        this.getHoursPerMonth()
        this.getSessionLengths()
        this.getSessionTypes()
      }
    },
    methods: {
      getChart (chartName, callback) {
        this.loading = true
        var chartConfig = {
          chart: {
            name: chartName,
            days: this.search_days
          }
        }
        rangepro.post_secure('store/range/' + this.rangeID + '/chart', chartConfig, callback, 'SearchRangeHistory', null)
      },
      handleTargetChartResponse (data, success) {
        if (success) {
          var tmpLabels = data.labels
          var tmpDS = {
            label: 'Usage Hours',
            backgroundColor: '#F64F4F',
            data: data.data
          }
          this.target_chart_data = {
            labels: tmpLabels,
            datasets: [
              tmpDS
            ]
          }
          this.$refs.targetUsageChart.updateChart()
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleSessionsPerMonthResponse (data, success) {
        if (success) {
          var tmpLabels = data.labels
          var tmpDS = {
            label: '# Sessions',
            backgroundColor: '#1c6eb5',
            data: data.data
          }
          this.sessions_per_month_data = {
            labels: tmpLabels,
            datasets: [
              tmpDS
            ]
          }
          this.$refs.sessionsPerMonthChart.updateChart()
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleHoursPerMonthResponse (data, success) {
        if (success) {
          var tmpLabels = data.labels
          var tmpDS = {
            label: '# Hours',
            backgroundColor: '#0AB25E',
            data: data.data
          }
          this.hours_per_month_data = {
            labels: tmpLabels,
            datasets: [
              tmpDS
            ]
          }
          this.$refs.hoursPerMonthChart.updateChart()
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleSessionLengthsResponse (data, success) {
        if (success) {
          var tmpLabels = data.labels
          var tmpDS = {
            label: '# Sessions',
            backgroundColor: this.getPieChartBackgrounds(data.data.length),
            data: data.data
          }
          this.session_lengths_data = {
            labels: tmpLabels,
            datasets: [
              tmpDS
            ]
          }
          this.$refs.hoursPerMonthChart.updateChart()
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleSessionTypesResponse (data, success) {
        if (success) {
          var tmpLabels = data.labels
          var tmpDS = {
            label: '# Sessions',
            backgroundColor: this.getPieChartBackgrounds(data.data.length),
            data: data.data
          }
          this.session_types_data = {
            labels: tmpLabels,
            datasets: [
              tmpDS
            ]
          }
          this.$refs.hoursPerMonthChart.updateChart()
          this.loading = false
        } else {
          this.loading = false
        }
      },
      getTargetUsage () {
        this.getChart('target_usage', this.handleTargetChartResponse)
      },
      getSessionsPerMonth () {
        this.getChart('sessions_per_month', this.handleSessionsPerMonthResponse)
      },
      getHoursPerMonth () {
        this.getChart('hours_per_month', this.handleHoursPerMonthResponse)
      },
      getSessionLengths () {
        this.getChart('session_lengths', this.handleSessionLengthsResponse)
      },
      getSessionTypes () {
        this.getChart('session_types', this.handleSessionTypesResponse)
      },
      getPieChartBackgrounds (arrayLength) {
        var baseChoices = [
          'rgba(65, 184, 131, .8)',
          'rgba(228, 102, 81, .8)',
          'rgba(0, 216, 255, .8)',
          'rgba(155, 89, 182, .8)',
          'rgba(209, 169, 249, .8)',
          'rgba(169, 209, 249, .8)',
          'rgba(200, 13, 107, .8)',
          'rgba(200, 200, 13, .7)'
        ]
        return baseChoices.slice(0, arrayLength)
      }
    },
    validations: {
      search_days: {
        required
      }
    }
  }
</script>

<style scoped>

</style>



