export default {

  format_stripe_ts: function (ts) {
    return Date(ts * 1000)
  },
  format_stripe_ts_date: function (ts) {
    var d = new Date(ts * 1000)
    var month = d.getMonth() + 1
    return month + '/' + d.getDate() + '/' + d.getFullYear()
  },
  format_stripe_amount: function (amount) {
    if (amount > 1) {
      return '$' + amount / 100
    } else if (amount === 0) {
      return '$0.00'
    }
  },
  format_rangepro_amount: function (amount) {
    if (amount === '0') {
      return '$0.00'
    } else {
      return '$' + amount
    }
  },
  format_ts: function (ts) {
    if (ts) {
      var time = new Date(ts * 1000)
      return time.toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true })
    } else {
      return null
    }
  },
  get_date: function () {
    var d = new Date()
    var month = d.getMonth() + 1
    return month + '-' + d.getDate() + '-' + d.getFullYear()
  },
  format_yes_no: function (value) {
    if (value === true) {
      return 'Yes'
    } else {
      return 'No'
    }
  },
  format_null_zero: function (value) {
    if (value === null) {
      return 0
    }
  },
  getBadge: function (status) {
    return status === true ? 'success'
      : status === false ? 'danger' : 'primary'
  },
  format_mil_to_std_time (input) {
    // Take military time and convert to display time
    var rawInput = input.split(':')
    var inputCharOne = parseInt(rawInput[0].charAt(0))
    var inputCharTwo = parseInt(rawInput[0].charAt(1))
    if (inputCharOne === 1 && inputCharTwo > 2) {
      // greater than 12pm, - 12
      return (rawInput[0] - 12) + ':' + rawInput[1] + ' PM'
    } else if (inputCharOne === 1 && inputCharTwo === 2) {
      // if it's 12pm, format it as such
      return rawInput[0] + ':' + rawInput[1] + ' PM'
    } else if (inputCharOne === 2) {
      // if it's 2000, format as such
      return (rawInput[0] - 12) + ':' + rawInput[1] + ' PM'
    } else {
      if (inputCharOne === 0 && inputCharTwo === 0) {
        // if it's 00:XX then it's 12 AM
        return '12:' + rawInput[1] + ' AM'
      } else {
        return rawInput.join(':') + ' AM'
      }
    }
  },
  format_std_time_to_mil (input) {
    // Format time to hour and zero out minutes
    var rawInput = input.split(' ')
    var tod = rawInput[1]
    var timeSplit = rawInput[0].split(':')
    var hour = parseInt(timeSplit[0])
    var minutes = parseInt(timeSplit[1])
    if (tod === 'PM') {
      hour += 12
    }
    if (minutes < 10) {
      minutes = '0' + minutes.toString()
    } else {
      minutes = minutes.toString()
    }
    if (hour < 10) {
      hour = '0' + hour.toString()
    } else {
      hour = hour.toString()
    }
    return hour + ':' + minutes
  },
  format_time_to_hour (time) {
    // Format time to hour and zero out minutes
    var rawInput = time.split(' ')
    var tod = rawInput[1]
    var timeSplit = rawInput[0].split(':')
    var hour = parseInt(timeSplit[0])
    if (hour < 10) {
      hour = '0' + hour.toString()
    }
    return hour + ':' + '00 ' + tod
  },
  format_date (input) {
    // Format date from YYYY-MM-DD to MM/DD/YYYY
    var rawInput = input.split('-')
    return rawInput[1] + '/' + rawInput[2] + '/' + rawInput[0]
  },
  format_date_std (input) {
    // Format date from MM-DD-YYYY to MM/DD/YYYY
    var rawInput = input.split('-')
    return rawInput[0] + '/' + rawInput[1] + '/' + rawInput[2]
  },
  format_date_for_filter (input) {
    // Format date from YYYY-MM-DD to MM-DD-YYYY
    var rawInput = input.split('-')
    return rawInput[1] + '-' + rawInput[2] + '-' + rawInput[0]
  },
  format_minutes_display (input) {
    var remainder = input % 60
    var hours = (input - remainder) / 60
    if (remainder > 0 && hours > 0) {
      return hours + ' HR(s) ' + remainder + ' MIN(s)'
    } else if (remainder > 0 && hours < 1) {
      return remainder + ' MIN(s)'
    } else {
      return hours + ' HR(s)'
    }
  },
  get_hours (input) {
    var remainder = input % 60
    return (input - remainder) / 60
  },
  get_minutes (input) {
    return input % 60
  }
}



