<template>
  <div class="col-12">
    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Store</strong> Displays <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
        <b-button type="submit" size="sm" variant="success" class="pull-right" v-b-modal.displayAdd>
          <i class="fa fa-plus"></i> Add
        </b-button>
        <a href="https://docs.range-pro.com/store_account.html#displays" target="new" class="button">
          <b-button type="submit" size="sm" variant="primary" class="pull-right mr-1"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>
      <b-table striped hover
               :current-page="currentPage"
               :per-page="perPage"
               :items="item_list"
               :fields="fields">

        <template slot="mode" slot-scope="data">
          {{ data.item.mode.toUpperCase() }}
        </template>

        <template slot="code" slot-scope="data">
          <a v-bind:href="'http://display.range-pro.com/#/code/' + data.item.code" target="_blank"><i class="fa fa-external-link mr-1"></i> http://display.range-pro.com/#/code/{{data.item.code}} </a>
        </template>

        <template slot="range" slot-scope="data">
          {{ getRangeName(data.item.rangeID).toUpperCase() }}
        </template>

        <template slot="id" slot-scope="data">
          <b-button type="submit" size="sm" variant="warning" @click="selectDisplay(data.item.name, data.item.mode, data.item.rangeID, data.item.id )"><i class="fa fa-trash-o"></i> Edit</b-button>
          <b-button type="submit" size="sm" variant="danger" @click="confirmDelete(data.item.id)"><i class="fa fa-trash-o"></i> Delete</b-button>
        </template>
      </b-table>

      <nav>
        <b-pagination :total-rows="getRowCount(item_list)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
      </nav>

      <b-modal ref="confirmDelete" hide-footer title="Delete Display" class="modal-dark text-light">
        <div class="d-block text-left text-dark">
          <p>Please confirm to delete display.</p>
        </div>
        <b-btn class="mt-3" variant="outline-danger" block @click="deleteItem()">Confirm</b-btn>
      </b-modal>

    </b-card>


    <b-modal ref="displayAdd" id="displayAdd" hide-footer size="lg" centered hide-footer title="Add Display" class="modal-dark text-light">
      <div ref="card" class="text-dark">

        <b-form-group
          label="Display Name"
          label-for="displayName"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="displayName" type="text" v-model="addDisplay.name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.addDisplay.name.required">Display Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Display Mode"
          label-for="displayMode"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="displayMode"
                         :plain="true"
                         :options="mode_options"
                         v-model="addDisplay.mode">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Range"
          label-for="displayRange"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="displayRange"
                         :plain="true"
                         :options="range_options"
                         v-model="addDisplay.rangeID">
          </b-form-select>
          <span class="form-group__message text-danger" v-if="!$v.addDisplay.rangeID.required">Display Range is Required</span>
        </b-form-group>

        <b-btn class="mt-3" variant="outline-success" block @click="saveDisplay()" :disabled="$v.addDisplay.$invalid">Add Display</b-btn>

      </div>
    </b-modal>

    <b-modal ref="displayEdit" id="displayEdit" hide-footer size="lg" centered hide-footer title="Edit Display" class="modal-dark text-light">
      <div ref="card" class="text-dark">

        <b-form-group
          label="Display Name"
          label-for="displayName"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="displayName" type="text" v-model="editDisplay.name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.editDisplay.name.required">Display Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Display Mode"
          label-for="displayMode"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="displayMode"
                         :plain="true"
                         :options="mode_options"
                         v-model="editDisplay.mode">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Range"
          label-for="displayRange"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="displayRange"
                         :plain="true"
                         :options="range_options"
                         v-model="editDisplay.rangeID">
          </b-form-select>
          <span class="form-group__message text-danger" v-if="!$v.editDisplay.rangeID.required">Display Range is Required</span>
        </b-form-group>

        <b-btn class="mt-3" variant="outline-success" block @click="updateDisplay()" :disabled="$v.editDisplay.$invalid">Save Display</b-btn>

      </div>
    </b-modal>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'
  import { required } from 'vuelidate/lib/validators'

  export default {
    name: 'StoreDisplays',
    data () {
      return {
        item_list: [],
        ranges: [],
        range_options: [],
        mode_options: [
          {text: 'RANGE DISPLAY ONLY', value: 'range only'},
          {text: 'WAITING LIST ONLY', value: 'waiting list only'},
          {text: 'RANGE & WAITING LIST CYCLE', value: 'cycle range and waiting list'}
        ],
        delete_id: null,
        loading: true,
        currentPage: 1,
        perPage: 5,
        totalRows: 0,
        edit_id: null,
        editDisplay: {
          'name': null,
          'mode': 'range only',
          'rangeID': null
        },
        addDisplay: {
          'name': null,
          'mode': 'range only',
          'rangeID': null
        },
        fields: [
          {
            key: 'name',
            label: 'Display Name',
            sortable: true
          },
          {
            key: 'mode',
            label: 'Mode'
          },
          {
            key: 'code',
            label: 'Display Link'
          },
          {
            key: 'range',
            label: 'Range'
          },
          {
            key: 'id',
            label: 'Action'
          }
        ]
      }
    },
    mounted: function () {
      this.getData()
      this.getStoreRanges()
    },
    methods: {
      getData (response) {
        rangepro.get_secure('store/displays', this.handleResponse, 'GetStoreDisplays')
      },
      getStoreRanges (response) {
        rangepro.get_secure('store/ranges', this.handleRangesResponse, 'GetStoreRanges')
      },
      saveDisplay () {
        this.loading = true
        var display = {
          display: {
            name: this.addDisplay.name,
            mode: this.addDisplay.mode,
            rangeID: this.addDisplay.rangeID
          }
        }
        rangepro.post_secure('store/displays', display, this.handleAddDisplaysResponse, 'AddStoreDisplay', 'Successfully Added Display')
      },
      updateDisplay () {
        this.loading = true
        var display = {
          display: {
            name: this.editDisplay.name,
            mode: this.editDisplay.mode,
            rangeID: this.editDisplay.rangeID
          }
        }
        rangepro.put_secure('store/display/' + this.edit_id, display, this.handleAddDisplaysResponse, 'AddStoreDisplay', 'Successfully Added Display')
      },
      deleteItem () {
        this.loading = true
        this.$refs.confirmDelete.hide()
        rangepro.delete_secure('store/display', this.delete_id, this.getData, 'GetStoreDisplays', 'Removed Store Display')
      },
      confirmDelete (id) {
        this.$refs.confirmDelete.show()
        this.delete_id = id
      },
      handleAddDisplaysResponse (data, success) {
        if (success) {
          this.addDisplay.name = null
          this.addDisplay.mode = 'range only'
          this.addDisplay.rangeID = null
          this.editDisplay.name = null
          this.editDisplay.mode = 'range only'
          this.editDisplay.rangeID = null
          this.edit_id = null
          this.$refs.displayAdd.hide()
          this.$refs.displayEdit.hide()
          this.getData()
        } else {
          this.loading = false
        }
      },
      handleResponse (data, success) {
        if (success) {
          this.item_list = data.displays
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleRangesResponse (data, success) {
        if (success) {
          this.ranges = data.ranges
          this.range_options = []
          for (var index in this.ranges) {
            this.range_options.push({
              text: this.ranges[index]['name'],
              value: this.ranges[index]['id']
            })
          }
          this.loading = false
        } else {
          this.loading = false
        }
      },
      selectDisplay (name, mode, rangeID, id) {
        this.edit_id = id
        this.editDisplay.name = name
        this.editDisplay.mode = mode
        this.editDisplay.rangeID = rangeID
        this.$refs.displayEdit.show()
      },
      getRangeName (id) {
        for (var index in this.ranges) {
          if (this.ranges[index]['id'] === id) {
            return this.ranges[index]['name']
          }
        }
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      }
    },
    validations: {
      addDisplay: {
        name: {
          required
        },
        rangeID: {
          required
        }
      },
      editDisplay: {
        name: {
          required
        },
        rangeID: {
          required
        }
      }
    }
  }
</script>

<style scoped>

</style>



