<template>
  <div>
    <b-row>
      <b-col cols="6">
        <h1 class="pull-left text-primary"><i class="fa fa-th-large text-danger"></i> {{ getTitle() }}</h1>
      </b-col>
      <b-col></b-col>
      <b-col cols="2">
        <router-link :to="{ name: 'Kiosk Dashboard', params: { id: kioskID }}"><b-button type="submit" class="btn btn-md pull-right" variant="warning"><i class="fa fa-home"></i> Home</b-button></router-link>
      </b-col>
    </b-row>
    <div class="mt-5 col-md-12">
      <b-row v-if="stage  === 'information'">
        <b-col cols="12">

          <b-form-group
            label="First Name"
            label-for="firstName"
            :label-cols="2"
            :horizontal="true">
            <b-form-input id="firstName" type="text" v-model="customer_info.first_name"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.customer_info.first_name.required">First Name is Required</span>
          </b-form-group>

          <b-form-group
            label="Last Name"
            label-for="lastName"
            :label-cols="2"
            :horizontal="true">
            <b-form-input id="lastName" type="text" v-model="customer_info.last_name"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.customer_info.last_name.required">Last Name is Required</span>
          </b-form-group>

          <b-form-group
            label="Phone Number"
            label-for="customerPhone"
            :label-cols="2"
            :horizontal="true">
            <masked-input
              type="tel"
              name="phone"
              class="form-control"
              v-model="customer_info.phone"
              :mask="['(' , /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]"
              :guide="true"
              placeholderChar="#" @focus="clearPhone()">
            </masked-input>
            <span class="form-group__message text-danger" v-if="!$v.customer_info.phone.required">Phone is Required</span>
          </b-form-group>

          <b-form-group
            label="Email Address"
            label-for="emailAddress"
            :label-cols="2"
            :horizontal="true">
            <b-form-input id="emailAddress" type="text" v-model="customer_info.email"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.customer_info.email.required">Email is Required</span>
            <span class="form-group__message text-danger" v-if="!$v.customer_info.email.email">Invalid Email Address</span>
          </b-form-group>

          <b-form-group
            label="Date of Birth"
            label-for="dob"
            :label-cols="2"
            :horizontal="true">
            <b-form-input id="dob" type="date" v-model="customer_info.dob"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.customer_info.dob.required">Date of Birth is Required</span>
          </b-form-group>

        </b-col>
      </b-row>



      <b-row v-if="stage  === 'minors'">
        <b-col cols="6">

          <b-form-group
            label="First Name"
            label-for="firstName"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="firstName" type="text" v-model="addMinor.first_name"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.addMinor.first_name.required">First Name is Required</span>
          </b-form-group>
        </b-col>
        <b-col cols="6">

          <b-form-group
            label="Last Name"
            label-for="lastName"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="lastName" type="text" v-model="addMinor.last_name"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.addMinor.last_name.required">Last Name is Required</span>
          </b-form-group>

        </b-col>
        <b-col cols="6">

          <b-form-group
            label="Date of Birth"
            label-for="dob"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="dob" type="date" v-model="addMinor.dob"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.addMinor.dob.required">Date of Birth is Required</span>
          </b-form-group>

        </b-col>
        <b-col cols="6">

          <b-form-group
            label="Relationship"
            label-for="relationship"
            :label-cols="3"
            :horizontal="true">
            <b-form-select id="relationship"
                           :plain="true"
                           :options="[
                             { value: 'Mother', text: 'Mother'}, { value: 'Father', text: 'Father'}, {value: 'Parental Guardian', text: 'Parental Guardian'}
                           ]"
                           v-model="addMinor.relationship">
            </b-form-select>
            <span class="form-group__message text-danger" v-if="!$v.addMinor.relationship.required">Relationship is Required</span>
          </b-form-group>

          <b-button type="submit" class="btn btn-md pull-right" variant="primary" @click="addMinorToList()" :disabled="$v.addMinor.$invalid"><i class="fa fa-plus-square-o"></i> Add Minor</b-button>

        </b-col>

      </b-row>


      <b-row v-if="stage  === 'minor_question'">
        <b-col cols="12">
          <h2>Do you have any minors with you?</h2>
        </b-col>

      </b-row>


      <b-row class="mt-4" v-if="stage  === 'minors'">
        <b-col cols="12">
          <b-table striped hover :items="customer_minors" :fields="minor_fields">
            <template slot="id" slot-scope="data">
              <b-button type="submit" size="sm" variant="danger" @click="deleteListItem(customer_minors, data.index)"><i class="fa fa-trash-o"></i> Delete</b-button>
            </template>
          </b-table>
        </b-col>
      </b-row>



      <b-row v-if="stage  === 'questions'">
        <b-col cols="12">
          <h2>{{ questions[question_index]['question_text'] }}</h2>
        </b-col>

        <b-col cols="12 mt-4">
          <b-form-select id="answer"
                         v-if="questions[question_index]['question_type'] === 'boolean'"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="question_input">
          </b-form-select>

          <b-form-input id="Answer" v-if="questions[question_index]['question_type'] === 'input_text'" type="text" v-model="question_input"></b-form-input>
        </b-col>

      </b-row>



      <b-row v-if="stage  === 'signature'">
        <b-col cols="12" class="text-center" v-html="waiver.waiver_text"></b-col>
        <hr>
        <b-col cols="12" class="border border-gray-200">
          <vueSignature ref="signature" :sigOption="option" :h="'300px'" v-if="waiver.capture_signatures"></vueSignature>
          <h2 v-if="waiver.capture_signatures"><i class="fa fa-pencil-square-o"></i>Sign Here</h2>
          <hr v-if="waiver.capture_signatures">
          <b-button type="submit" class="btn btn-md mb-2" variant="danger" @click="clear()" v-if="waiver.capture_signatures"><i class="fa fa-close"></i> CLEAR</b-button>
          <b-button type="submit" class="btn btn-md mb-2" variant="success" @click="save()" v-if="waiver.capture_signatures"><i class="fa fa-check"></i> SAVE SIGNATURE</b-button>
        </b-col>
        <b-col cols="12 mt-2" v-if="!waiver.capture_signatures">
          <b-button type="submit" class="btn btn-lg btn-block" :class="{'btn-danger': (!customer_manual_accept), 'btn-success': (customer_manual_accept)}" @click="manualAcceptTerms()"><i class="fa" :class="{'fa-exclamation': (!customer_manual_accept), 'fa-check-circle': (customer_manual_accept)}"></i> {{ customer_manual_signature }}</b-button>
        </b-col>
      </b-row>



      <b-row v-if="stage  === 'success'">
        <b-col cols="12" class="text-center">
          <h2><img class="mr-3" src="@/assets/loading.gif"> Please wait, saving waiver...</h2>
        </b-col>
      </b-row>

      <b-row v-if="stage  === 'complete'">
        <b-col cols="12" class="text-center">
          <h2><i class="fa fa-check text-success"></i> Waiver complete! Please see the front desk.</h2>
        </b-col>
      </b-row>

      <b-row v-if="stage  === 'error'">
        <b-col cols="12" class="text-center">
          <h2><i class="fa fa-warning text-danger"></i> There was a problem saving your waiver.  Please see the front desk.</h2>
        </b-col>
      </b-row>

      <b-row class="mt-4" v-if="stage === 'information'">
        <b-col cols="6"></b-col>
        <b-col cols="6">
          <b-button type="submit" class="btn btn-md pull-right" variant="success" @click="setStage('minor_question')" :disabled="$v.customer_info.$invalid"><i class="fa fa-arrow-right"></i> NEXT</b-button>
        </b-col>
      </b-row>

      <b-row class="mt-4" v-if="stage === 'minor_question'">
        <b-col cols="6">
          <b-button type="submit" class="btn btn-md pull-left" variant="warning" @click="setStage('information')"><i class="fa fa-arrow-left"></i> BACK</b-button>
        </b-col>
        <b-col cols="6">
          <b-button type="submit" class="mr-1 ml-1 btn btn-md pull-right" variant="danger" @click="setStage('questions')"><i class="fa fa-exclamation-circle"></i> NO</b-button>
          <b-button type="submit" class="mr-1 ml-1 btn btn-md pull-right" variant="success" @click="setStage('minors')"><i class="fa fa-check"></i> YES</b-button>
        </b-col>
      </b-row>

      <b-row class="mt-4" v-if="stage === 'minors'">
        <b-col cols="6">
          <b-button type="submit" class="btn btn-md pull-left" variant="warning" @click="setStage('information')"><i class="fa fa-arrow-left"></i> BACK</b-button>
        </b-col>
        <b-col cols="6">
          <b-button type="submit" class="btn btn-md pull-right" variant="success" @click="setStage('questions')"><i class="fa fa-arrow-right"></i> NEXT</b-button>
        </b-col>
      </b-row>

      <b-row class="mt-4" v-if="stage === 'questions'">
        <b-col cols="6">
          <b-button type="submit" class="btn btn-md pull-left" variant="warning" @click="setStage('minors')" v-if="question_index === 0"><i class="fa fa-arrow-left"></i> BACK</b-button>
          <b-button type="submit" class="btn btn-md pull-left" variant="warning" @click="setQuestionIndex(question_index - 1)" v-if="question_index > 0"><i class="fa fa-arrow-left"></i> BACK</b-button>
        </b-col>
        <b-col cols="6">
          <b-button type="submit" class="btn btn-md pull-right" variant="success" @click="setQuestion(questions[question_index]['question_text'], question_input, question_index)" v-if="question_index < (questions.length - 1)"><i class="fa fa-arrow-right"></i> NEXT</b-button>
          <b-button type="submit" class="btn btn-md pull-right" variant="success" @click="setStage('signature')" v-if="question_index === (questions.length - 1)"><i class="fa fa-arrow-right"></i> NEXT</b-button>
        </b-col>
      </b-row>

      <b-row class="mt-4" v-if="stage === 'signature'">
        <b-col cols="6">
          <b-button type="submit" class="btn btn-md pull-left" variant="warning" @click="setStage('questions')"><i class="fa fa-arrow-left"></i> BACK</b-button>
        </b-col>
        <b-col cols="6">
          <b-button type="submit" class="btn btn-md pull-right" variant="success" @click="setStage('success')" :disabled="checkAccepted()"><i class="fa fa-check-circle-o"></i> SUBMIT</b-button>
        </b-col>
      </b-row>





    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import router from '@/router'
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'
  import MaskedInput from 'vue-text-mask'
  import { required, email } from 'vuelidate/lib/validators'
  import vueSignature from 'vue-signature'

  Vue.use(vueSignature)

  export default {
    name: 'WaiverForm',
    components: {
      MaskedInput
    },
    data () {
      return {
        kioskID: null,
        waiverID: null,
        loading: false,
        stage: 'information',
        waiver: null,
        questions: [],
        question_index: 0,
        question_input: null,
        customer_info: {
          first_name: null,
          last_name: null,
          phone: '#',
          email: null,
          dob: null
        },
        customer_questions: [],
        customer_minors: [],
        customer_signature: null,
        customer_manual_signature: 'By clicking this button I hereby certify that I accept the above terms and conditions',
        customer_manual_accept: false,
        addMinor: {
          first_name: null,
          last_name: null,
          dob: null,
          relationship: null
        },
        minor_fields: [
          {
            key: 'first_name',
            label: 'First Name',
            sortable: true
          },
          {
            key: 'last_name',
            label: 'Last Name'
          },
          {
            key: 'dob',
            label: 'Date of Birth'
          },
          {
            key: 'id',
            label: ''
          }
        ],
        option: {
          penColor: 'rgb(0, 0, 0)',
          backgroundColor: 'rgb(255,255,255)'
        }
      }
    },
    mounted: function () {
      this.kioskID = this.$route.params.id
      this.waiverID = this.$route.params.formid
      this.getWaiver()
      this.getWaiverQuestions()
    },
    methods: {
      getWaiver () {
        this.loading = true
        rangepro.get_secure('store/waiver/' + this.waiverID, this.handleWaiverResponse, 'GetStoreWaiver')
      },
      getWaiverQuestions () {
        this.loading = true
        rangepro.get_secure('store/waiver/' + this.waiverID + '/questions', this.handleQuestionsResponse, 'GetStoreWaiverQuestions')
      },
      saveWaiver () {
        this.loading = true
        var waiverData = {
          info: {
            first_name: this.customer_info.first_name,
            last_name: this.customer_info.last_name,
            phone: this.customer_info.phone,
            email: this.customer_info.email,
            dob: this.customer_info.dob
          },
          waiver: this.waiver.waiver_text,
          minors: this.customer_minors,
          questions: this.customer_questions,
          signature: {
            manual_signature: this.customer_manual_signature,
            manual_accept: this.customer_manual_accept,
            signature_png: this.customer_signature
          }
        }
        var signature = {
          signature: {
            first_name: this.customer_info.first_name,
            last_name: this.customer_info.last_name,
            waiver_name: this.waiver.name,
            date: formats.get_date(),
            waiver_data: JSON.stringify(waiverData)
          }
        }
        rangepro.post_secure('store/waiver/signatures', signature, this.handleSaveWaiverResponse, 'AddStoreWaiver', 'Saved Waiver')
      },
      handleWaiverResponse (data, success) {
        if (success) {
          this.waiver = data.waiver
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleQuestionsResponse (data, success) {
        if (success) {
          this.customer_questions = null
          this.questions = data.questions
          this.initQuestions()
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleSaveWaiverResponse (data, success) {
        if (success) {
          this.setStage('complete')
          var id = this.kioskID
          setTimeout(function () { router.push({name: 'Kiosk Dashboard', params: {id: id}}) }, 5000)
        } else {
          this.setStage('error')
        }
      },
      addMinorToList () {
        var minor = {
          first_name: this.addMinor.first_name,
          last_name: this.addMinor.last_name,
          dob: formats.format_date(this.addMinor.dob),
          relationship: this.addMinor.relationship
        }
        this.customer_minors.push(minor)
        this.addMinor.first_name = null
        this.addMinor.last_name = null
        this.addMinor.dob = null
        this.addMinor.relationship = null
      },
      deleteListItem (list, index) {
        delete list.splice(index, 1)
      },
      getTitle () {
        if (this.stage === 'information') {
          return 'BASIC INFORMATION'
        } else if (this.stage === 'minors') {
          return 'ADD MINORS'
        } else if (this.stage === 'questions') {
          return 'ANSWER QUESTIONS'
        } else if (this.stage === 'signature') {
          return 'ACCEPT TERMS'
        }
      },
      clearPhone () {
        if (this.customer_info.phone === '(###) ###-####') {
          this.customer_info.phone = ''
        }
      },
      setStage (stage) {
        if (stage === 'questions') {
          if (this.questions.length < 1) {
            if (this.stage === 'signature') {
              this.stage = 'information'
            } else {
              this.stage = 'signature'
            }
          } else {
            this.stage = stage
          }
        } else if (stage === 'success') {
          this.stage = stage
          this.saveWaiver()
        } else {
          this.stage = stage
        }
      },
      initQuestions () {
        var tmpQuestions = []
        for (var index in this.questions) {
          tmpQuestions.push({
            question: this.questions[index]['question_text'],
            answer: null
          })
        }
        this.customer_questions = tmpQuestions
      },
      setQuestion (question, answer, index) {
        this.customer_questions[index]['question'] = question
        this.customer_questions[index]['answer'] = answer
        this.question_input = null
        this.setQuestionIndex(index + 1)
      },
      setQuestionIndex (index) {
        this.question_index = index
        this.question_input = this.customer_questions[index]['answer']
      },
      manualAcceptTerms () {
        this.customer_manual_accept = true
        this.customer_manual_signature = 'I have accepted the terms and conditions contained in this waiver'
        this.setStage('success')
      },
      checkAccepted () {
        if (this.waiver.capture_signatures) {
          if (this.customer_signature === null) {
            return true
          } else {
            return false
          }
        } else {
          if (this.customer_manual_accept) {
            return false
          } else {
            return true
          }
        }
      },
      save () {
        this.customer_signature = this.$refs.signature.save()
      },
      clear () {
        this.$refs.signature.clear()
        this.customer_signature = null
      }
    },
    validations: {
      customer_info: {
        first_name: {
          required
        },
        last_name: {
          required
        },
        phone: {
          required
        },
        email: {
          required,
          email
        },
        dob: {
          required
        }
      },
      addMinor: {
        first_name: {
          required
        },
        last_name: {
          required
        },
        dob: {
          required
        },
        relationship: {
          required
        }
      },
      question_input: {
        required
      }
    }
  }
</script>

<style scoped>

</style>



