<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="8">
          <b-card-group>
            <b-card no-body class="p-4">
              <b-card-body>
                <h1><img src="@/assets/icon.png" width="50" height="50"> Store Admin</h1>
                <p class="text-muted">Forgot Password</p>

                <span class="form-group__message text-danger" v-if="!$v.username">Username is Required</span>
                <b-input-group class="mb-3">
                  <div class="input-group-prepend"><span class="input-group-text"><i class="icon-user"></i></span></div>
                  <input type="text" class="form-control" placeholder="Username" v-model="username">
                </b-input-group>
                <b-row>
                  <Alerts />
                </b-row>
                <b-row>
                  <b-col cols="6">
                    <b-button @click="requestCode(username)" variant="primary" class="px-4 mt-2" :disabled="$v.$invalid">Request Code</b-button>
                  </b-col>
                  <b-col cols="6" class="text-right">
                  </b-col>
                </b-row>
              </b-card-body>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>
<script>
  import auth from '../services/auth'
  import { required } from 'vuelidate/lib/validators'
  import { Alerts } from '../components/'

  export default {
    name: 'ForgotPasswordStart',
    components: {
      Alerts
    },
    data () {
      return {
        username: ''
      }
    },
    methods: {
      requestCode (username) {
        return auth.forgotPasswordStart(username)
      }
    },
    validations: {
      username: {
        required
      }
    }
  }
</script>



