<template>
  <div class="col-12">

    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Member</strong> Information<img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
        <a href="https://docs.range-pro.com/store_account.html#memberships" target="new" class="button">
          <b-button type="submit" size="sm" variant="primary" class="pull-right"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>
      <span class="col-md-12 pull-left">
        <b-row class="mb-2">
          <b-col sm="4" class="text-sm-left"><b><i class="fa fa-user mr-2"></i>Name:</b> {{ member_detail.first_name }} {{ member_detail.last_name }}</b-col>
          <b-col sm="4" class="text-sm-left"><b><i class="fa fa-phone mr-2"></i>Phone:</b> {{ member_detail.phone }}</b-col>
          <b-col sm="4" class="text-sm-left"><b><i class="fa fa-envelope mr-2"></i>Email:</b> {{ member_detail.email }}</b-col>
        </b-row>
      </span>

    </b-card>

    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Customer</strong> Subscriptions<img class="ml-3" src="@/assets/loading.gif" v-if="loading === true"> <b-button type="submit" size="sm" variant="success" class="pull-right" v-b-modal.addSubscription><i class="fa fa-plus"></i> Add</b-button>
      </div>

      <b-table striped hover :items="subscriptions" :fields="subscriptions_fields">

        <template slot="plan_name" slot-scope="data">
          {{ data.item.detail.plan_name }}
        </template>

        <template slot="plan_frequency" slot-scope="data">
          ${{ data.item.detail.plan_amount }} Every {{ data.item.detail.plan_interval_count }} {{ data.item.detail.plan_interval | capitalize }}(s)
        </template>

        <template slot="status" slot-scope="data">
          {{ data.item.detail.status.toUpperCase() }}
        </template>

        <template slot="billing" slot-scope="data">
          <span v-if="data.item.detail.billing === 'send_invoice'">SEND INVOICE</span><span v-else>AUTO</span>
        </template>

        <template slot="current_period_end" slot-scope="data">
          {{ formatTimestampDate(data.item.detail.current_period_end) }}
        </template>

        <template slot="id" slot-scope="data">
          <b-button class="pull-left" type="submit" size="sm" variant="primary" @click="updateBilling(data.item.id, 'charge_automatically')" v-if="data.item.detail.billing === 'send_invoice'" :disabled="cards.length < 1"><i class="fa fa-refresh"></i> Auto Pay</b-button>
          <b-button class="pull-left" type="submit" size="sm" variant="primary" @click="updateBilling(data.item.id, 'send_invoice')" v-if="data.item.detail.billing === 'charge_automatically'"><i class="fa fa-file-pdf-o"></i> Manual Pay</b-button>
          <b-button class="pull-left ml-1" type="submit" size="sm" variant="danger" @click="confirmCancelMembership(data.item.id)"><i class="fa fa-trash-o"></i> Cancel</b-button>
        </template>

        <template slot="show_details" slot-scope="row">
          <!-- we use @click.stop here to prevent emitting of a 'row-clicked' event  -->
          <b-button size="sm" @click.stop="row.toggleDetails" class="mr-2">
            {{ row.detailsShowing ? 'Hide' : 'Show'}} Details
          </b-button>
        </template>
        <template slot="row-details" slot-scope="row">
          <b-card>
            <b-row class="mb-2" v-for="item in row.item.detail.additional_members">
              <b-col sm="3" class="text-sm-right"><b>Member Name:</b></b-col>
              <b-col>{{ item.first_name }} {{ item.last_name }}</b-col>
            </b-row>
          </b-card>
        </template>

      </b-table>

      <nav>
        <b-pagination :total-rows="getRowCount(subscriptions)" :per-page="subscriptions_perPage" v-model="subscriptions_currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
      </nav>

    </b-card>

    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Customer</strong> Payment Methods<img class="ml-3" src="@/assets/loading.gif" v-if="loading === true"> <b-button type="submit" size="sm" variant="success" class="pull-right" v-b-modal.ccModal @click="initStripe()"><i class="fa fa-plus"></i> Add</b-button>
      </div>

      <b-table striped hover :items="cards" :fields="cards_fields">

        <template slot="last4" slot-scope="data">
          ****{{ data.item.detail.last4 }}
        </template>

        <template slot="brand" slot-scope="data">
          {{ data.item.detail.brand }}
        </template>

        <template slot="id" slot-scope="data">
          <b-button type="submit" size="sm" variant="danger" @click="confirmDeleteCard(data.value)"><i class="fa fa-trash-o"></i> Delete</b-button>
        </template>

      </b-table>

      <nav>
        <b-pagination :total-rows="getRowCount(cards)" :per-page="cards_perPage" v-model="cards_currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
      </nav>

    </b-card>

    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Customer</strong> Invoices<img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
      </div>

      <b-table striped hover :items="invoices" :fields="invoices_fields">

        <template slot="date" slot-scope="data">
          {{ formatTimestamp(data.item.detail.date) }}
        </template>

        <template slot="amount_due" slot-scope="data">
          {{ formatStripeAmount(data.item.detail.amount_due) }}
        </template>

        <template slot="paid" slot-scope="data">
          <b-badge :variant="getBadge(data.item.detail.paid)">{{formatYesNo(data.item.detail.paid)}}</b-badge>
        </template>

        <template slot="attempted" slot-scope="data">
          <b-badge :variant="getBadge(data.item.detail.attempted)">{{formatYesNo(data.item.detail.attempted)}}</b-badge>
        </template>

        <template slot="attempt_count" slot-scope="data">
          {{ data.item.detail.attempt_count }}
        </template>

        <template slot="id" slot-scope="data">
          <b-button type="submit" size="sm" variant="danger" @click="confirmMarkInvoicePaid(data.value)" v-if="!data.item.detail.paid"><i class="fa fa-check-circle-o"></i> Mark Paid</b-button>
        </template>

      </b-table>

      <nav>
        <b-pagination :total-rows="getRowCount(invoices)" :per-page="invoices_perPage" v-model="invoices_currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
      </nav>

    </b-card>




    <b-modal ref="confirmDeleteCard" hide-footer title="Delete Payment Method" class="modal-dark text-light">
      <div class="d-block text-left text-dark">
        <p>Please confirm to delete payment method.</p>
      </div>
      <b-btn class="mt-3" variant="outline-danger" block @click="deleteCard()">Confirm</b-btn>
    </b-modal>

    <b-modal ref="ccModal" id="ccModal" size="lg" centered hide-footer title="Add Payment Method" class="modal-dark text-light">
      <div ref="creditCard" class="text-dark p-2"></div>
      <b-btn class="mt-3" variant="outline-success" block @click="addStripeCard()">Add Card</b-btn>
    </b-modal>

    <b-modal ref="confirmCancelSubscription" hide-footer title="Delete Subscription" class="modal-dark text-light">
      <div class="d-block text-left text-dark">
        <p>Please confirm to delete subscription.</p>
      </div>
      <b-btn class="mt-3" variant="outline-danger" block @click="deleteSubscription()">Confirm</b-btn>
    </b-modal>

    <b-modal ref="confirmMarkInvoicePaid" hide-footer title="Mark Invoice Paid" class="modal-dark text-light">
      <div class="d-block text-left text-dark">
        <p>Please confirm you would like to mark invoice as paid.  Doing so means you've taken payment already.</p>
      </div>
      <b-btn class="mt-3" variant="outline-danger" block @click="markInvoicePaid()">Confirm</b-btn>
    </b-modal>






    <b-modal ref="addSubscription" id="addSubscription" size="lg" centered hide-footer title="Add Subscription" class="modal-dark text-light">
      <div ref="card" class="text-dark">

        <b-form-group horizontal
                      breakpoint="lg"
                      label="Subscription"
                      label-size="lg"
                      label-class="font-weight-bold pt-0"
                      class="mb-0">

          <b-form-group
            label="Plan"
            label-for="subscriptionPlan"
            :label-cols="2"
            :horizontal="true">
            <b-form-select id="subscriptionPlan"
                           :plain="true"
                           :options="subscription_plans"
                           v-model="addSubscriptionPlanID">
            </b-form-select>
            <span class="mt-1 text-primary pull-right"><i>{{ addSubscriptionDetail }}</i></span>
          </b-form-group>

          <b-form-group
            label="Payment"
            label-for="subscriptionPayment"
            :label-cols="2"
            :horizontal="true">
            <b-form-select id="subscriptionPayment"
                           :plain="true"
                           :options="[{ value: 'send_invoice', text: 'Send Invoice to Customer'}, { value: 'charge_automatically', text: 'Automatically Bill - Requires Payment on File'}]"
                           v-model="addSubscription.method">
            </b-form-select>
            <span v-if="addSubscription.method === 'charge_automatically' && cards.length < 1"><i class="fa fa-exclamation-triangle text-danger mr-1"></i>No payment method on file. Please add a payment method first.</span>
          </b-form-group>

          <b-form-group
            label="Discount"
            label-for="applyDiscount"
            :label-cols="2"
            :horizontal="true">
            <b-form-select id="applyDiscount"
                           :plain="true"
                           :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                           v-model="addSubscriptionDiscount">
            </b-form-select>
          </b-form-group>

        </b-form-group>

        <b-form-group v-if="addSubscriptionDiscount"
                      horizontal
                      breakpoint="lg"
                      label="Discount"
                      label-size="lg"
                      label-class="font-weight-bold pt-0"
                      class="mb-0">

          <b-form-group
            label="Type"
            label-for="discountType"
            :label-cols="2"
            :horizontal="true">
            <b-form-select id="discountType"
                           :plain="true"
                           :options="[{ value: 'fixed', text: 'Fixed Amount'}, { value: 'percent', text: 'Percentage'}]"
                           v-model="addSubscription.discount.type">
            </b-form-select>
          </b-form-group>

          <b-form-group v-if="addSubscription.discount.type === 'fixed'"
            label="Amount"
            label-for="amount"
            :label-cols="2"
            :horizontal="true">
            <b-form-input id="amount" type="text" v-model="addSubscription.discount.amount"></b-form-input>
          </b-form-group>

          <b-form-group v-if="addSubscription.discount.type === 'percent'"
                        label="Percent"
                        label-for="amount"
                        :label-cols="2"
                        :horizontal="true">
            <b-form-input id="amount" type="text" v-model="addSubscription.discount.percent"></b-form-input>
          </b-form-group>

          <b-form-group
            label="Duration"
            label-for="discountDuration"
            :label-cols="2"
            :horizontal="true">
            <b-form-select id="discountDuration"
                           :plain="true"
                           :options="[{ value: 'once', text: 'One Time'}, { value: 'repeating', text: 'Repeating'}]"
                           v-model="addSubscription.discount.duration">
            </b-form-select>
          </b-form-group>

          <b-form-group v-if="addSubscription.discount.duration === 'repeating'"
            label="Months"
            label-for="discountDurationMonths"
            :label-cols="2"
            :horizontal="true">
            <b-form-select id="discountDurationMonths"
                           :plain="true"
                           :options="[
                           { value: 1, text: '1'}, { value: 2, text: '2'}, {value: 3, text: '3'}, { value: 4, text: '4'},
                           { value: 5, text: '5'}, { value: 6, text: '6'}, { value: 7, text: '7'}, { value: 8, text: '8'},
                           { value: 9, text: '9'}, { value: 10, text: '10'}, { value: 11, text: '11'}, { value: 12, text: '12'},
                         ]"
                           v-model="addSubscription.discount.duration_in_months">
            </b-form-select>
          </b-form-group>

        </b-form-group>

        <b-form-group horizontal
                      breakpoint="lg"
                      label="Add Authorized Members"
                      label-size="lg"
                      label-class="font-weight-bold pt-0"
                      class="mb-0">

          <b-form-group
            label="First Name"
            label-for="first_name"
            :label-cols="2"
            :horizontal="true">
            <b-form-input id="first_name" type="text" v-model="addSubscriptionMember.first_name"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.addSubscriptionMember.first_name.required">First Name is Required</span>
          </b-form-group>

          <b-form-group
            label="Last Name"
            label-for="last_name"
            :label-cols="2"
            :horizontal="true">
            <b-form-input id="last_name" type="text" v-model="addSubscriptionMember.last_name"></b-form-input>
            <span class="form-group__message text-danger" v-if="!$v.addSubscriptionMember.last_name.required">Last Name is Required</span>
          </b-form-group>

          <span class="pull-right align-middle"><span class="mr-4 h4">ALLOWED MEMBERS: {{ additionalMembers.length }} of {{ maxAdditionalMembers }}</span><b-button class="mb-2" type="submit" size="sm" variant="success" @click="addToAdditionalMembers()"><i class="fa fa-plus-circle"></i> Add</b-button></span>
        </b-form-group>

        <b-form-group horizontal
                      breakpoint="lg"
                      label="Authorized Members"
                      label-size="lg"
                      label-class="font-weight-bold pt-0"
                      class="mb-0">
          <b-table striped hover :items="additionalMembers" :fields="additionalMembersFields">
            <template slot="id" slot-scope="data">
              <b-button type="submit" size="sm" variant="danger" @click="deleteAdditionalMember(data.index)"><i class="fa fa-trash-o"></i> Delete</b-button>
            </template>
          </b-table>
        </b-form-group>

      </div>
      <b-btn class="mt-3" variant="outline-success" block @click="addMemberSubscription()" :disabled="addSubscription.method === 'charge_automatically' && cards.length < 1">Subscribe</b-btn>
    </b-modal>


  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'
  import { required, numeric } from 'vuelidate/lib/validators'

  export default {
    name: 'MemberDetail',
    data () {
      return {
        memberID: null,
        key: null,
        loading: false,
        member_detail: null,
        subscriptions: [],
        subscriptions_filter: null,
        subscriptions_fields: [
          {
            key: 'plan_name',
            label: 'Plan'
          },
          {
            key: 'plan_frequency',
            label: 'Plan'
          },
          {
            key: 'status',
            label: 'Status'
          },
          {
            key: 'billing',
            label: 'Method'
          },
          {
            key: 'current_period_end',
            label: 'Next Billing Cycle'
          },
          {
            key: 'show_details',
            label: 'Authorized Members'
          },
          {
            key: 'id',
            label: 'Action'
          }
        ],
        subscriptions_currentPage: 1,
        subscriptions_perPage: 25,
        subscriptions_totalRows: 0,
        subscription_plans: [],
        plans: [],
        maxAdditionalMembers: 0,
        additionalMembers: [],
        addSubscriptionMember: {
          first_name: null,
          last_name: null
        },
        addSubscriptionPlanID: null,
        addSubscriptionDetail: null,
        cancelSubscriptionPlanID: null,
        addSubscriptionDiscount: false,
        addSubscription: {
          method: 'send_invoice',
          discount: {
            type: 'fixed',
            amount: null,
            percent: null,
            duration: 'once',
            duration_in_months: 1
          }
        },
        additionalMembersFields: [
          {
            key: 'first_name',
            label: 'First Name',
            sortable: true
          },
          {
            key: 'last_name',
            label: 'Last Name'
          },
          {
            key: 'id',
            label: 'Remove'
          }
        ],
        card: null,
        stripe: null,
        cards: [],
        cards_filter: null,
        cards_fields: [
          {
            key: 'last4',
            label: 'Card Number',
            sortable: true
          },
          {
            key: 'brand',
            label: 'CC Type',
            sortable: true
          },
          {
            key: 'id',
            label: 'Action'
          }
        ],
        cards_currentPage: 1,
        cards_perPage: 25,
        cards_totalRows: 0,
        delete_card_id: null,
        invoices: [],
        invoices_filter: null,
        invoices_fields: [
          {
            key: 'date',
            label: 'Invoice Date',
            sortable: true
          },
          {
            key: 'amount_due',
            label: 'Amount Due'
          },
          {
            key: 'paid',
            label: 'Invoice Paid',
            sortable: true
          },
          {
            key: 'attempted',
            label: 'Attempted Charge',
            sortable: true
          },
          {
            key: 'attempt_count',
            label: 'Number of Attempts',
            sortable: true
          },
          {
            key: 'id',
            label: 'Action'
          }
        ],
        invoices_currentPage: 1,
        invoices_perPage: 25,
        invoices_totalRows: 0,
        markInvoicePaidID: null
      }
    },
    mounted: function () {
      this.memberID = this.$route.params.id
      this.getMember()
      this.getPlans()
    },
    filters: {
      capitalize: function (value) {
        if (!value) return ''
        value = value.toString()
        return value.charAt(0).toUpperCase() + value.slice(1)
      }
    },
    watch: {
      addSubscriptionPlanID: function (val) {
        for (var index in this.plans) {
          if (this.plans[index]['id'] === val) {
            this.maxAdditionalMembers = this.plans[index]['max_participants']
            this.addSubscriptionDetail = '$' + this.plans[index]['amount'] + ' every ' + this.plans[index]['interval_count'] + ' ' + this.plans[index]['interval']
          }
        }
      }
    },
    methods: {
      getMember () {
        this.loading = true
        rangepro.get_secure('store/member/' + this.memberID, this.handleMemberResponse, 'GetStoreActiveMembers')
      },
      getPlans () {
        this.loading = true
        rangepro.get_secure('store/members/plans', this.handlePlansResponse, 'GetStoreActivePlans')
      },
      deleteCard () {
        this.loading = true
        this.$refs.confirmDeleteCard.hide()
        rangepro.delete_secure('store/member/' + this.memberID + '/card', this.delete_card_id, this.getMember, 'ErrorStoreMemberCardsDelete', 'Removed Payment Method')
      },
      deleteSubscription () {
        this.loading = true
        rangepro.delete_secure('store/member/' + this.memberID + '/subscription', this.cancelSubscriptionPlanID, this.getMember, 'ErrorStoreMemberSubscriptionDelete', 'Cancelled Subsscription')
        this.$refs.confirmCancelSubscription.hide()
      },
      addStripeCard () {
        this.loading = true
        this.$refs.ccModal.hide()
        this.stripe.createToken(this.card).then(result => {
          var data = {
            'card': {
              'token': result.token.id
            }
          }
          rangepro.post_secure('store/member/' + this.memberID + '/cards', data, this.getMember, 'ErrorStoreMemberCards', 'Added Payment Method')
        })
      },
      addMemberSubscription () {
        this.loading = true
        this.$refs.addSubscription.hide()
        var plan = {
          'subscription': {
            'plan_id': this.addSubscriptionPlanID,
            'method': this.addSubscription.method,
            'additional_members': this.additionalMembers
          }
        }
        if (this.addSubscriptionDiscount) {
          plan.subscription.discount = {
            type: this.addSubscription.discount.type,
            amount: parseInt(this.addSubscription.discount.amount),
            percent: parseFloat(this.addSubscription.discount.percent),
            duration: this.addSubscription.discount.duration,
            duration_in_months: this.addSubscription.discount.duration_in_months
          }
        }
        rangepro.post_secure('store/member/' + this.memberID + '/subscription', plan, this.handleSubscriptionResponse, 'ErrorStoreMemberSubscriptionAdd', 'Added Subscription')
      },
      markInvoicePaid () {
        this.loading = true
        this.$refs.confirmMarkInvoicePaid.hide()
        rangepro.put_secure('store/member/invoice/' + this.markInvoicePaidID + '/paid', {}, this.getMember, 'ErrorMarkInvoicePaid', 'Marked Invoice Paid')
      },
      updateBilling (id, method) {
        this.loading = true
        var data = {
          subscription: {
            billing: method
          }
        }
        rangepro.put_secure('store/member/' + this.memberID + '/subscription/' + id + '/billing', data, this.getMember, 'ErrorUpdateSubscriptionBilling', 'Updating Billing Method')
      },
      handleMemberResponse (data, success) {
        if (success) {
          this.member_detail = data.member
          this.key = data.key
          this.subscriptions = data.subscriptions
          this.cards = data.cards
          this.invoices = data.invoices
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handlePlansResponse (data, success) {
        if (success) {
          this.plans = []
          this.subscription_plans = []
          this.plans = data.plans
          for (var index in data.plans) {
            this.subscription_plans.push({
              'text': data.plans[index]['name'],
              'value': data.plans[index]['id']
            })
          }
        }
        this.loading = false
      },
      handleSubscriptionResponse (data, success) {
        if (success) {
          this.additionalMembers = []
          this.addSubscriptionPlanID = null
          this.addSubscriptionDiscount = false
          this.addSubscriptionDetail = null
          this.addSubscription = {
            method: 'send_invoice',
            discount: {
              type: 'fixed',
              amount: null,
              percent: null,
              duration: 'once',
              duration_in_months: 1
            }
          }
          this.getMember()
        }
        this.loading = false
      },
      initStripe () {
        this.stripe = Stripe(this.key)
        var elements = this.stripe.elements()
        this.card = elements.create('card')
        this.card.mount(this.$refs.creditCard)
      },
      confirmDeleteCard (id) {
        this.$refs.confirmDeleteCard.show()
        this.delete_card_id = id
      },
      confirmCancelMembership (id) {
        this.$refs.confirmCancelSubscription.show()
        this.cancelSubscriptionPlanID = id
      },
      confirmMarkInvoicePaid (id) {
        this.$refs.confirmMarkInvoicePaid.show()
        this.markInvoicePaidID = id
      },
      addToAdditionalMembers () {
        if (this.additionalMembers.length < this.maxAdditionalMembers) {
          this.additionalMembers.push({
            first_name: this.addSubscriptionMember.first_name,
            last_name: this.addSubscriptionMember.last_name
          })
        }
        this.addSubscriptionMember.first_name = null
        this.addSubscriptionMember.last_name = null
      },
      deleteAdditionalMember (index) {
        delete this.additionalMembers.splice(index, 1)
      },
      getRowCount (items) {
        return items.length
      },
      formatTimestamp (value) {
        return formats.format_stripe_ts(value)
      },
      formatTimestampDate (value) {
        return formats.format_stripe_ts_date(value)
      },
      formatStripeAmount (value) {
        return formats.format_stripe_amount(value)
      },
      formatYesNo (value) {
        return formats.format_yes_no(value)
      },
      getBadge (status) {
        return formats.getBadge(status)
      }
    },
    validations: {
      addSubscriptionMember: {
        first_name: {
          required
        },
        last_name: {
          required
        }
      }
    }
  }
</script>

<style scoped>

</style>



