<template>
  <div class="col-12">
    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Store</strong> Ranges <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
        <router-link :to="{name: 'Store Range Configuration Add'}">
          <b-button type="submit" size="sm" variant="success" class="pull-right"><i class="fa fa-plus"></i> Add</b-button>
        </router-link>
        <a href="https://docs.range-pro.com/store_account.html#create-configure-range" target="new" class="button">
          <b-button type="submit" size="sm" variant="primary" class="pull-right mr-1"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>
      <b-table striped hover
               :current-page="currentPage"
               :per-page="perPage"
               :items="item_list"
               :fields="fields">

        <template slot="pay_per_use" slot-scope="data">
          <b-badge :variant="getBadge(data.item.pay_per_use)">{{data.value}}</b-badge>
        </template>
        <template slot="no_lane_assignments" slot-scope="data">
          <b-badge :variant="getBadge(data.item.no_lane_assignments)">{{data.value}}</b-badge>
        </template>
        <template slot="enabled" slot-scope="data">
          <b-badge :variant="getBadge(data.item.enabled)">{{data.value}}</b-badge>
        </template>

        <template slot="id" slot-scope="data">
          <router-link :to="{ name: 'Store Range Dashboard', params: { id:  data.item.id}}">
            <b-button type="submit" size="sm" variant="secondary"><i class="fa fa-th-large"></i> Dashboard</b-button>
          </router-link>
          <router-link :to="{ name: 'Store Range Configuration Update', params: { id:  data.item.id}}">
            <b-button type="submit" size="sm" variant="warning"><i class="fa fa-trash-o"></i> Edit</b-button>
          </router-link>
          <b-button type="submit" size="sm" variant="danger" @click="confirmDelete(data.item.id)"><i class="fa fa-trash-o"></i> Delete</b-button>
        </template>
      </b-table>

      <nav>
        <b-pagination :total-rows="getRowCount(item_list)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
      </nav>

      <b-modal ref="confirmDelete" hide-footer title="Delete Range" class="modal-dark text-light">
        <div class="d-block text-left text-dark">
          <p>Please confirm to delete range.</p>
        </div>
        <b-btn class="mt-3" variant="outline-danger" block @click="deleteItem()">Confirm</b-btn>
      </b-modal>
    </b-card>
  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'

  export default {
    name: 'RangeConfiguration',
    data () {
      return {
        item_list: [],
        delete_id: null,
        loading: true,
        currentPage: 1,
        perPage: 5,
        totalRows: 0,
        fields: [
          {
            key: 'name',
            label: 'Name',
            sortable: true
          },
          {
            key: 'lanes',
            label: '# Lanes'
          },
          {
            key: 'no_lane_assignments',
            label: 'Lane Assignments Disabled',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'enabled',
            label: 'Range Enabled',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'id',
            label: 'Action'
          }
        ]
      }
    },
    mounted: function () {
      this.getData(null)
    },
    methods: {
      getData (response) {
        rangepro.get_secure('store/ranges', this.handleResponse, 'GetStoreRanges')
      },
      deleteItem () {
        this.loading = true
        this.$refs.confirmDelete.hide()
        rangepro.delete_secure('store/range', this.delete_id, this.getData, 'GetStoreRanges', 'Removed Store Range')
      },
      confirmDelete (id) {
        this.$refs.confirmDelete.show()
        this.delete_id = id
      },
      handleResponse (data, success) {
        if (success) {
          this.item_list = data.ranges
          this.loading = false
        } else {
          this.loading = false
        }
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      }
    }
  }
</script>

<style scoped>

</style>



