<template>
  <div>
    <b-row>
      <b-col cols="2">
        <img class="pull-left" src="@/assets/logo.png" height="40" width="110">
      </b-col>
      <b-col></b-col>
      <b-col cols="2">
        <router-link :to="{ name: 'Kiosk Dashboard', params: { id: kioskID }}"><b-button type="submit" class="btn btn-sm pull-right" variant="warning"><i class="fa fa-home"></i> Home</b-button></router-link>
      </b-col>
    </b-row>
    <div class="mt-5 col-md-12">
      <b-row class="border-gray-200 border p-4">
        <b-col cols="1" class="align-middle">
          <i class="fa fa-pencil-square-o fa-4x mt-3 text-gray-400"></i>
        </b-col>
        <b-col class="text-left align-middle">
          <h1 class="mt-2"><router-link :to="{ name: 'Waivers Dashboard', params: { id: kioskID }}">Sign Waivers</router-link></h1>
          <p>Sign waivers to utilize all range services</p>
        </b-col>
        <b-col cols="2" class="align-middle">
          <router-link :to="{ name: 'Waivers Dashboard', params: { id: kioskID }}"><i class="fa fa-arrow-circle-right fa-3x text-primary pull-right mt-4"></i></router-link>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
    export default {
      name: 'Dashboard',
      data () {
        return {
          kioskID: null
        }
      },
      mounted: function () {
        this.kioskID = this.$route.params.id
      }
    }
</script>

<style scoped>

</style>



