<template>
  <div>
    <aside class="aside-menu text-center">
      <b-card no-body class="bg-dark mb-1" bg-variant="dark" text-variant="light">
        <p class="m-1">LAST 10 WAIVERS</p>
        <b-button :class="{'btn-success': (loading)}" type="submit" variant="primary" size="sm" @click="getSignatures()" :disabled="loading"><i class="fa fa-refresh"></i> REFRESH</b-button>
      </b-card>
      <b-card no-body class="m-1" v-for="item in signatures.slice(0, 10)">
        <h6 class="m-1">{{ item.first_name }} {{ item.last_name }}</h6>
        <p class="text-primary m-1">{{ item.waiver_name.toUpperCase() }}</p>
        <b-button class="m-1" type="submit" size="sm" variant="warning" @click="getSignature(item.id)"><i class="fa fa-file"></i> VIEW</b-button>
      </b-card>
    </aside>

    <b-modal ref="waiverDetail" id="waiverDetail" hide-footer size="lg" centered hide-footer title="Waiver Summary" class="modal-dark text-light">
      <div ref="card" class="text-dark">

        <h5><i class="fa fa-id-card text-primary mr-2"></i> INFORMATION: </h5>
        <b-row class="mb-1">
          <b-col sm="3" class="text-sm-right"><b>Customer Name:</b></b-col>
          <b-col sm="3">{{ waiverData['info']['first_name'] }} {{ waiverData['info']['last_name'] }}</b-col>
          <b-col sm="3" class="text-sm-right"><b>Phone:</b></b-col>
          <b-col sm="3">{{ waiverData['info']['phone'] }}</b-col>
        </b-row>
        <b-row class="mb-1 mt-2">
          <b-col sm="3" class="text-sm-right"><b>Email:</b></b-col>
          <b-col sm="3">{{ waiverData['info']['email'] }}</b-col>
          <b-col sm="3" class="text-sm-right"><b>DOB:</b></b-col>
          <b-col sm="3">{{ formatDate(waiverData['info']['dob']) }}</b-col>
        </b-row>
        <hr>

        <div v-if="waiverData['minors'].length > 0">
          <h5><i class="fa fa-group text-primary mr-2"></i> MINORS: </h5>
          <b-row class="mb-1 mt-2" v-for="item in waiverData['minors']">
            <b-col sm="2" class="text-sm-right"><b>Name: </b></b-col>
            <b-col sm="3">{{ item['first_name'] }} {{ item['last_name'] }}</b-col>
            <b-col sm="2" class="text-sm-right"><b>Relationship: </b></b-col>
            <b-col sm="2">{{ item['relationship'] }}</b-col>
            <b-col sm="1" class="text-sm-right"><b>DOB: </b></b-col>
            <b-col sm="2">{{ item['dob'] }}</b-col>
          </b-row>
          <hr>
        </div>

        <div v-if="waiverData['questions'].length > 0">
          <h5><i class="fa fa-question-circle-o text-primary mr-2"></i> QUESTIONS: </h5>
          <b-row class="mb-1 mt-2" v-for="item in waiverData['questions']">
            <b-col sm="6" class="text-sm-right"><b>{{ item.question }}</b></b-col>
            <b-col sm="6">{{ item.answer }}</b-col>
          </b-row>
          <hr>
        </div>

        <h5><i class="fa fa-pencil text-primary mr-2"></i> SIGNATURE: </h5>
        <b-row class="mb-1 mt-2" v-if="waiverData['signature']['manual_accept']">
          <b-col sm="6" class="text-sm-right"><b>E-Signature: </b></b-col>
          <b-col sm="6">{{ waiverData['signature']['manual_accept'] }}</b-col>
        </b-row>
        <b-row class="mb-1 mt-2" v-if="waiverData['signature']['manual_accept']">
          <b-col sm="6" class="text-sm-right"><b>Signature: </b></b-col>
          <b-col sm="6">{{ waiverData['signature']['manual_signature'] }}</b-col>
        </b-row>
        <b-row class="mb-1 mt-2" v-if="!waiverData['signature']['manual_accept']">
          <img width="100%" :src="waiverData['signature']['signature_png']">
        </b-row>
        <hr>

      </div>
    </b-modal>
  </div>
</template>

<script>
  import Vue from 'vue'
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'
  import vueSignature from 'vue-signature'

  Vue.use(vueSignature)

  export default {
    name: 'c-aside',
    data () {
      return {
        loading: false,
        signature_date: formats.get_date(),
        signatures: [],
        waiverData: {
          info: {
            first_name: null,
            last_name: null,
            dob: null,
            email: null,
            phone: null
          },
          minors: [],
          questions: [],
          signature: {
            manual_signature: true,
            manual_accept: null,
            signature_png: null
          }
        }
      }
    },
    methods: {
      getSignatures (response) {
        this.loading = true
        this.signatures = []
        rangepro.get_secure('store/waiver/signatures/' + this.signature_date, this.handleSignatureResponse, 'GetWaiverSignatures')
      },
      getSignature (id) {
        this.loading = true
        rangepro.get_secure('store/waiver/signature/' + id, this.handleOpenSignature, 'GetWaiverSignature')
      },
      handleSignatureResponse (data, success) {
        if (success) {
          this.signatures = data.signatures
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleOpenSignature (data, success) {
        this.loading = false
        if (success) {
          this.waiverData = JSON.parse(data.signature['waiver_data'])
          this.$refs.waiverDetail.show()
        }
      },
      formatDate (value) {
        if (value) {
          return formats.format_date(value)
        }
      }
    }
  }
</script>



