<template>
      <b-nav-item-dropdown right no-caret>
        <template slot="button-content">
          <i class="icon-bell"></i><b-badge pill variant="success">{{ count }}</b-badge>
        </template>
        <b-dropdown-header tag="div" class="dropdown-menu-lg text-center"><strong>You have {{ count }} notifications</strong></b-dropdown-header>
          <b-dropdown-item v-for="notification in notifications">
            <i v-bind:class="[ notification.styling, notification.icon]"></i>{{ notification.message }}
          </b-dropdown-item>
      </b-nav-item-dropdown>
</template>
<script>
  import { mapGetters } from 'vuex'

  export default {
    name: 'header-dropdown-notif',
    data: () => {
      return {}
    },
    computed: {
      ...mapGetters({
        notifications: 'getNotifications',
        count: 'countNotifications'
      })
    }
  }
</script>



