<template>
  <div class="app">
    <AppHeader/>
    <div class="app-body">
      <main class="main">
        <div class="container-fluid p-2">
          <Alerts/>
          <b-row class="justify-content-center">
            <b-col md="12">
              <b-card-group>
                <b-card no-body class="p-4">
                  <b-card-body>
                    <router-view></router-view>
                  </b-card-body>
                </b-card>
              </b-card-group>
            </b-col>
          </b-row>
        </div>
      </main>
    </div>
    <AppFooter/>
  </div>
</template>

<script>
import { HeaderCollapsed as AppHeader, Footer as AppFooter, Alerts } from '../components/'

export default {
  name: 'display',
  data () {
    return {
      kioskID: null
    }
  },
  components: {
    Alerts,
    AppHeader,
    AppFooter
  },
  computed: {
    name () {
      return this.$route.name
    },
    list () {
      return this.$route.matched
    }
  }
}
</script>



