<template>
  <div class="col-12">
    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>RangePro</strong> Change Password
      </div>
      <b-form-group v-bind:class="{ 'form-group--error': $v.oldPassword.$error }"
        description="Please enter your current password"
        label="Current Password"
        label-for="currentPassword"
        :label-cols="3"
        :horizontal="true">
        <b-form-input id="currentPassword" type="password" placeholder="Enter your current password" v-model="oldPassword"></b-form-input>
        <span class="form-group__message text-danger" v-if="!$v.oldPassword.required">Current Password Required.</span>
      </b-form-group>
      <b-form-group
        description="Please enter new password"
        label="New Password"
        label-for="newPassword"
        :label-cols="3"
        :horizontal="true">
        <b-form-input id="newPassword" type="password" placeholder="Enter new password" v-model="newPassword"></b-form-input>
        <span class="form-group__message text-danger" v-if="!$v.newPassword.minLength">Passwords Must Be At Least 8 Characters.</span>
        <b-input-group-append class="pull-right">
          <b-btn variant="danger" v-if="score < 3">Too Weak</b-btn>
          <b-btn variant="success" v-if="score === 3">Perfect</b-btn>
        </b-input-group-append>
      </b-form-group>
      <b-form-group v-bind:class="{ 'form-group--error': $v.newRepeat.$error }"
        description="Please enter new password again"
        label="Repeat Password"
        label-for="repeatPassword"
        :label-cols="3"
        :horizontal="true">
        <b-form-input id="repeatPassword" type="password" placeholder="Enter current password again" v-model="newRepeat"></b-form-input>
        <span class="form-group__message text-danger" v-if="!$v.newRepeat.sameAsPassword">Passwords Must Be Identical.</span>
      </b-form-group>
      <div slot="footer">
        <b-button type="submit" size="sm" variant="primary" @click="change(oldPassword, newPassword)" :disabled="$v.$invalid || score < 3"><i class="fa fa-dot-circle-o"></i> Submit</b-button>
        <b-button type="reset" size="sm" variant="danger" @click="clear()"><i class="fa fa-ban"></i> Reset</b-button>
      </div>
    </b-card>
  </div>
</template>

<script>
  import auth from '../services/auth'
  import { required, sameAs, minLength } from 'vuelidate/lib/validators'

  export default {
    name: 'AccountChangePassword',
    data () {
      return {
        score: 0,
        oldPassword: '',
        newPassword: '',
        newRepeat: ''
      }
    },
    watch: {
      newPassword: function (value) {
        this.score = 0
        var hasNumber = /\d/.test(value)
        var hasLowercase = /[a-z]/.test(value)
        var hasUppercase = /[A-Z]/.test(value)
        if (hasNumber) {
          this.score += 1
        }
        if (hasLowercase) {
          this.score += 1
        }
        if (hasUppercase) {
          this.score += 1
        }
      }
    },
    methods: {
      change (oldPassword, newPassword) {
        return auth.authChange(oldPassword, newPassword)
      },
      clear () {
        this.oldPassword = ''
        this.newPassword = ''
        this.newRepeat = ''
      }
    },
    validations: {
      oldPassword: {
        required
      },
      newPassword: {
        minLength: minLength(8)
      },
      newRepeat: {
        sameAsPassword: sameAs('newPassword')
      }
    }
  }
</script>



