<template>
  <div class="col-12">
    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Store</strong> Waivers <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
        <router-link :to="{name: 'Store Waivers Add'}">
          <b-button type="submit" size="sm" variant="success" class="pull-right">
            <i class="fa fa-plus"></i> Add
          </b-button>
        </router-link>
        <a href="https://docs.range-pro.com/store_account.html#waivers" target="new" class="button">
          <b-button type="submit" size="sm" variant="primary" class="pull-right mr-1"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>
      <b-table striped hover
               :current-page="currentPage"
               :per-page="perPage"
               :items="item_list"
               :fields="fields">

        <template slot="enabled" slot-scope="data">
          <b-badge :variant="getBadge(data.item.enabled)">{{data.value}}</b-badge>
        </template>
        <template slot="capture_signatures" slot-scope="data">
          <b-badge :variant="getBadge(data.item.capture_signatures)">{{data.value}}</b-badge>
        </template>

        <template slot="id" slot-scope="data">
          <router-link :to="{ name: 'Store Waivers Update', params: { id:  data.item.id}}">
            <b-button type="submit" size="sm" variant="warning"><i class="fa fa-trash-o"></i> Edit</b-button>
          </router-link>
          <b-button type="submit" size="sm" variant="danger" @click="confirmDelete(data.item.id)"><i class="fa fa-trash-o"></i> Delete</b-button>
        </template>
      </b-table>

      <nav>
        <b-pagination :total-rows="getRowCount(item_list)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
      </nav>

      <b-modal ref="confirmDelete" hide-footer title="Delete Waiver" class="modal-dark text-light">
        <div class="d-block text-left text-dark">
          <p>Please confirm to delete this waiver.</p>
        </div>
        <b-btn class="mt-3" variant="outline-danger" block @click="deleteItem()">Confirm</b-btn>
      </b-modal>
    </b-card>


    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Search</strong> Waiver Signatures <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
      </div>
      <div ref="card" class="text-dark">

        <b-form-group
          label="Search Date"
          label-for="searchDate"
          :label-cols="3"
          :horizontal="true">
          <b-form-input id="searchDate" type="date" v-model="signature_date_search"></b-form-input>
        </b-form-group>

      </div>
    </b-card>


    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Waiver</strong> Signatures - {{ formatDate(signature_date) }}<img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
      </div>
      <b-row>
        <b-col md="6" class="my-1">
          <b-form-group horizontal label="Search" class="mb-3">
            <b-input-group>
              <b-form-input v-model="filter" placeholder="Type to Search" />
              <b-input-group-append>
                <b-btn :disabled="!filter" @click="filter = ''">Clear</b-btn>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>
      </b-row>
      <b-table striped hover
               :current-page="signaturesCurrentPage"
               :per-page="signaturesPerPage"
               :filter="filter"
               :items="signatures"
               :fields="signature_fields">

        <template slot="waiver_name" slot-scope="data">
          {{ data.item.waiver_name.toUpperCase() }}
        </template>
      </b-table>

      <nav>
        <b-pagination :total-rows="getRowCount(item_list)" :per-page="signaturesPerPage" v-model="signaturesCurrentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
      </nav>

    </b-card>
  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'

  export default {
    name: 'RangeConfiguration',
    data () {
      return {
        item_list: [],
        signatures: [],
        signature_date_search: null,
        signature_date: formats.get_date(),
        filter: null,
        delete_id: null,
        loading: true,
        currentPage: 1,
        perPage: 30,
        signaturesCurrentPage: 1,
        signaturesPerPage: 30,
        totalRows: 0,
        fields: [
          {
            key: 'name',
            label: 'Name',
            sortable: true
          },
          {
            key: 'enabled',
            label: 'Enabled',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'capture_signatures',
            label: 'Signature Pad',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'id',
            label: 'Action'
          }
        ],
        signature_fields: [
          {
            key: 'first_name',
            label: 'First Name',
            sortable: true
          },
          {
            key: 'last_name',
            label: 'Last Name',
            sortable: true
          },
          {
            key: 'waiver_name',
            label: 'Waiver',
            sortable: true
          },
          {
            key: 'signature_ts',
            label: 'Timestamp',
            formatter: (value) => formats.format_ts(value)
          }
        ]
      }
    },
    mounted: function () {
      this.getData(null)
      this.getSignatures(null)
    },
    watch: {
      signature_date_search: function (val) {
        this.signature_date = formats.format_date_for_filter(val)
        this.getSignatures()
      }
    },
    methods: {
      getData (response) {
        rangepro.get_secure('store/waivers', this.handleResponse, 'GetStoreWaivers')
      },
      getSignatures (response) {
        rangepro.get_secure('store/waiver/signatures/' + this.signature_date, this.handleSignatureResponse, 'GetWaiverSignatures')
      },
      deleteItem () {
        this.loading = true
        this.$refs.confirmDelete.hide()
        rangepro.delete_secure('store/waiver', this.delete_id, this.getData, 'GetStoreWaivers', 'Removed Store Waiver')
      },
      confirmDelete (id) {
        this.$refs.confirmDelete.show()
        this.delete_id = id
      },
      handleResponse (data, success) {
        if (success) {
          this.item_list = data.waivers
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleSignatureResponse (data, success) {
        if (success) {
          this.signatures = data.signatures
          this.loading = false
        } else {
          this.loading = false
        }
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      },
      formatDate (date) {
        return formats.format_date_std(date)
      }
    }
  }
</script>

<style scoped>

</style>



