<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="8">
          <b-card-group>
            <b-card no-body class="p-4">
              <b-card-body>
                <h1><img src="@/assets/icon.png" width="50" height="50"> Store Login</h1>
                <p class="text-muted">Sign In to your RangePro store account</p>
                <b-input-group class="mb-3">
                  <div class="input-group-prepend"><span class="input-group-text"><i class="icon-user"></i></span></div>
                  <input type="text" class="form-control" placeholder="Username" v-model="credentials.username">
                </b-input-group>
                <b-input-group class="">
                  <div class="input-group-prepend"><span class="input-group-text"><i class="icon-lock"></i></span></div>
                  <input type="password" class="form-control" placeholder="Password" v-model="credentials.password">
                </b-input-group>
                <b-row>
                  <br />
                  <Alerts />
                </b-row>
                <b-row>
                  <b-col cols="2">

                  </b-col>
                  <b-col cols="10">
                    <span class="text-danger">{{ this.$route.params.error }}</span>
                  </b-col>
                </b-row>
                <b-row>
                  <b-col cols="6">
                    <b-button @click="authenticate(credentials.username, credentials.password)" variant="primary" class="px-4 mt-2">Login</b-button>
                  </b-col>
                  <b-col cols="6" class="text-right">
                    <router-link :to="{name: 'Forgot'}">
                      <b-button variant="link" class="px-0">Forgot password?</b-button>
                    </router-link>
                  </b-col>
                </b-row>
              </b-card-body>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>
<script>
  import auth from '../services/auth'
  import { Alerts } from '../components/'

  export default {
    name: 'Login',
    components: {
      Alerts
    },
    data () {
      return {
        credentials: {
          username: '',
          password: ''
        }
      }
    },
    methods: {
      authenticate (username, password) {
        return auth.authenticate(username, password)
      }
    }
  }
</script>



