<template>
  <div>
    <b-row>
      <b-col cols="6">
        <h1 class="pull-left text-primary"><i class="fa fa-th-large text-danger"></i> SELECT A WAIVER</h1>
      </b-col>
      <b-col></b-col>
      <b-col cols="2">
        <router-link :to="{ name: 'Kiosk Dashboard', params: { id: kioskID }}"><b-button type="submit" class="btn btn-md pull-right" variant="warning"><i class="fa fa-home"></i> Home</b-button></router-link>
      </b-col>
    </b-row>
    <div class="mt-5 col-md-12">
      <b-row class="border-gray-200 border p-4" v-for="item in waivers" v-if="item.enabled">
        <b-col cols="1" class="align-middle">
          <i class="fa fa-pencil-square-o fa-4x mt-3 text-gray-400"></i>
        </b-col>
        <b-col class="text-left align-middle">
          <h1 class="mt-2"><router-link :to="{ name: 'Waiver Form', params: { id: kioskID, formid: item.id }}">{{ item.name }}</router-link></h1>
          <p>{{ item.description }}</p>
        </b-col>
        <b-col cols="2" class="align-middle">
          <router-link :to="{ name: 'Waiver Form', params: { id: kioskID, formid: item.id }}"><i class="fa fa-arrow-circle-right fa-3x text-primary pull-right mt-4"></i></router-link>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'

  export default {
    name: 'WaiverDashboard',
    data () {
      return {
        kioskID: null,
        loading: false,
        waivers: []
      }
    },
    mounted: function () {
      this.kioskID = this.$route.params.id
      this.getData()
    },
    methods: {
      getData () {
        this.loading = true
        rangepro.get_secure('store/waivers', this.handleResponse, 'GetStoreWaivers')
      },
      handleResponse (data, success) {
        if (success) {
          this.waivers = data.waivers
          this.loading = false
        } else {
          this.loading = false
        }
      }
    }
  }
</script>

<style scoped>

</style>



