<template>
  <div class="col-12">

    <b-card no-body class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Store</strong> Members and Accounts <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
        <a href="https://docs.range-pro.com/store_account.html#memberships" target="new" class="button">
          <b-button type="submit" size="sm" variant="primary" class="pull-right ml-1"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
        <b-button type="submit" size="sm" variant="warning" class="pull-right ml-1" @click="refreshMemberships()">
          <i class="fa fa-refresh"></i> Update Memberships
        </b-button>
        <b-button type="submit" size="sm" variant="success" class="pull-right" v-b-modal.customerAdd>
          <i class="fa fa-plus"></i> Add
        </b-button>
      </div>
      <b-tabs pills card>
        <b-tab title="Active Members" active>

          <b-row>
            <b-col md="6" class="my-1">
              <b-form-group horizontal label="Search" class="mb-3 h4">
                <b-input-group>
                  <b-form-input v-model="active_filter" placeholder="Type to Search" />
                  <b-input-group-append>
                    <b-btn :disabled="!active_filter" @click="active_filter = ''">Clear</b-btn>
                  </b-input-group-append>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-row>

          <b-table striped hover
                   :current-page="active_currentPage"
                   :per-page="active_perPage"
                   :filter="active_filter"
                   :items="active_members"
                   :fields="active_member_fields">

            <template slot="first_name" slot-scope="data">
              <router-link :to="{ name: 'Member Detail', params: { id: data.item.id }}">{{ data.item.first_name}}</router-link>
            </template>

            <template slot="last_name" slot-scope="data">
              <router-link :to="{ name: 'Member Detail', params: { id: data.item.id }}">{{ data.item.last_name}}</router-link>
            </template>

            <template slot="status" slot-scope="data">
              {{ data.item.status.toUpperCase() }}
            </template>

            <template slot="show_details" slot-scope="row">
              <!-- we use @click.stop here to prevent emitting of a 'row-clicked' event  -->
              <b-button size="sm" @click.stop="row.toggleDetails" class="mr-2">
                {{ row.detailsShowing ? 'Hide' : 'Show'}} Details
              </b-button>
            </template>
            <template slot="row-details" slot-scope="row">
              <b-card>
                <b-row class="mb-2" v-for="item in row.item.additional_members">
                  <b-col sm="3" class="text-sm-right"><b>Member Name:</b></b-col>
                  <b-col>{{ item.first_name }} {{ item.last_name }}</b-col>
                </b-row>
              </b-card>
            </template>
          </b-table>

          <nav>
            <b-pagination :total-rows="getRowCount(active_members)" :per-page="active_perPage" v-model="active_currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
          </nav>

        </b-tab>
        <b-tab title="Unpaid Members">

          <b-row>
            <b-col md="6" class="my-1">
              <b-form-group horizontal label="Search" class="mb-3 h4">
                <b-input-group>
                  <b-form-input v-model="other_filter" placeholder="Type to Search" />
                  <b-input-group-append>
                    <b-btn :disabled="!other_filter" @click="other_filter = ''">Clear</b-btn>
                  </b-input-group-append>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-row>

          <b-table striped hover
                   :current-page="other_currentPage"
                   :per-page="other_perPage"
                   :filter="other_filter"
                   :items="other_members"
                   :fields="other_member_fields">

            <template slot="first_name" slot-scope="data">
              <router-link :to="{ name: 'Member Detail', params: { id: data.item.id }}">{{ data.item.first_name}}</router-link>
            </template>

            <template slot="last_name" slot-scope="data">
              <router-link :to="{ name: 'Member Detail', params: { id: data.item.id }}">{{ data.item.last_name}}</router-link>
            </template>

            <template slot="status" slot-scope="data">
              {{ data.item.status.toUpperCase() }}
            </template>

            <template slot="show_details" slot-scope="row">
              <!-- we use @click.stop here to prevent emitting of a 'row-clicked' event  -->
              <b-button size="sm" @click.stop="row.toggleDetails" class="mr-2">
                {{ row.detailsShowing ? 'Hide' : 'Show'}} Details
              </b-button>
            </template>
            <template slot="row-details" slot-scope="row">
              <b-card>
                <b-row class="mb-2" v-for="item in row.item.additional_members">
                  <b-col sm="3" class="text-sm-right"><b>Member Name:</b></b-col>
                  <b-col>{{ item.first_name }} {{ item.last_name }}</b-col>
                </b-row>
              </b-card>
            </template>
          </b-table>

          <nav>
            <b-pagination :total-rows="getRowCount(active_members)" :per-page="active_perPage" v-model="active_currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
          </nav>

        </b-tab>
        <b-tab title="All Registered Users">

          <b-row>
            <b-col md="6" class="my-1">
              <b-form-group horizontal label="Search" class="mb-3 h4">
                <b-input-group>
                  <b-form-input v-model="all_filter" placeholder="Type to Search" />
                  <b-input-group-append>
                    <b-btn :disabled="!all_filter" @click="all_filter = ''">Clear</b-btn>
                  </b-input-group-append>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-row>

          <b-table striped hover
                   :current-page="all_currentPage"
                   :per-page="all_perPage"
                   :filter="all_filter"
                   :items="all_members"
                   :fields="all_member_fields">

            <template slot="first_name" slot-scope="data">
              <router-link :to="{ name: 'Member Detail', params: { id: data.item.id }}">{{ data.item.first_name}}</router-link>
            </template>

            <template slot="last_name" slot-scope="data">
              <router-link :to="{ name: 'Member Detail', params: { id: data.item.id }}">{{ data.item.last_name}}</router-link>
            </template>

          </b-table>

          <nav>
            <b-pagination :total-rows="getRowCount(all_members)" :per-page="all_perPage" v-model="all_currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
          </nav>

        </b-tab>
      </b-tabs>
    </b-card>

    <b-modal ref="customerAdd" id="customerAdd" hide-footer size="lg" centered hide-footer title="Add Customer" class="modal-dark text-light">
      <div ref="card" class="text-dark">

        <b-form-group
          label="First Name"
          label-for="firstName"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="firstName" type="text" v-model="addMember.first_name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.addMember.first_name.required">First Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Last Name"
          label-for="lastName"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="firstName" type="text" v-model="addMember.last_name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.addMember.last_name.required">Last Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Phone Number"
          label-for="customerPhone"
          :label-cols="2"
          :horizontal="true">
          <masked-input
            type="tel"
            name="phone"
            class="form-control"
            v-model="addMember.phone"
            :mask="['(' , /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]"
            :guide="true"
            placeholderChar="#" @focus="clearPhone()">
          </masked-input>
          <span class="form-group__message text-danger" v-if="!$v.addMember.phone.required">Phone is Required</span>
        </b-form-group>

        <b-form-group
          label="Email Address"
          label-for="emailAddress"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="emailAddress" type="text" v-model="addMember.email"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.addMember.email.required">Email is Required</span>
          <span class="form-group__message text-danger" v-if="!$v.addMember.email.email">Invalid Email Address</span>
        </b-form-group>

        <b-btn class="mt-3" variant="outline-success" block @click="createCustomer()" :disabled="$v.addMember.$invalid">Add Customer</b-btn>

      </div>
    </b-modal>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import router from '@/router'
  import MaskedInput from 'vue-text-mask'
  import { required, email } from 'vuelidate/lib/validators'

  export default {
    name: 'Members',
    components: {
      MaskedInput
    },
    data () {
      return {
        loading: false,
        active_members: [],
        active_filter: null,
        other_members: [],
        other_filter: null,
        all_members: [],
        all_filter: null,
        active_member_fields: [
          {
            key: 'first_name',
            label: 'First Name',
            sortable: true
          },
          {
            key: 'last_name',
            label: 'Last Name',
            sortable: true
          },
          {
            key: 'status',
            label: 'Status'
          },
          {
            key: 'plan_name',
            label: 'Plan'
          },
          {
            key: 'email',
            label: 'Email'
          },
          {
            key: 'phone',
            label: 'Phone'
          },
          {
            key: 'show_details',
            label: 'Authorized Members'
          }
        ],
        other_member_fields: [
          {
            key: 'first_name',
            label: 'First Name',
            sortable: true
          },
          {
            key: 'last_name',
            label: 'Last Name',
            sortable: true
          },
          {
            key: 'status',
            label: 'Status'
          },
          {
            key: 'plan_name',
            label: 'Plan'
          },
          {
            key: 'email',
            label: 'Email'
          },
          {
            key: 'phone',
            label: 'Phone'
          },
          {
            key: 'show_details',
            label: 'Authorized Members'
          }
        ],
        all_member_fields: [
          {
            key: 'first_name',
            label: 'First Name',
            sortable: true
          },
          {
            key: 'last_name',
            label: 'Last Name',
            sortable: true
          },
          {
            key: 'email',
            label: 'Email'
          },
          {
            key: 'phone',
            label: 'Phone'
          }
        ],
        addMember: {
          first_name: null,
          last_name: null,
          phone: null,
          email: null
        },
        active_currentPage: 1,
        active_perPage: 25,
        active_totalRows: 0,
        other_currentPage: 1,
        other_perPage: 25,
        other_totalRows: 0,
        all_currentPage: 1,
        all_perPage: 25,
        all_totalRows: 0
      }
    },
    mounted: function () {
      this.getActiveMembers()
      this.getOtherMembers()
      this.getAllMembers()
    },
    methods: {
      getActiveMembers () {
        this.loading = true
        rangepro.get_secure('store/members/active', this.handleActiveMemberResponse, 'GetStoreActiveMembers')
      },
      getOtherMembers () {
        this.loading = true
        rangepro.get_secure('store/members/other', this.handleOtherMemberResponse, 'GetStoreOtherMembers')
      },
      getAllMembers () {
        this.loading = true
        rangepro.get_secure('store/members/all', this.handleAllMemberResponse, 'GetStoreAllMembers')
      },
      refreshMemberships () {
        this.loading = true
        rangepro.delete_bulk_secure('store/members/refresh', {}, this.handleRefresh, 'GetRefreshMemberships', 'Memberships Updated')
      },
      createCustomer () {
        this.loading = true
        this.$refs.customerAdd.hide()
        var member = {
          member: this.addMember
        }
        rangepro.post_secure('store/members', member, this.handleCreateCustomerResponse, 'AddStoreMember', 'Successfully Added Customer')
      },
      handleActiveMemberResponse (data, success) {
        if (success) {
          this.active_members = data.members
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleOtherMemberResponse (data, success) {
        if (success) {
          this.other_members = data.members
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleAllMemberResponse (data, success) {
        if (success) {
          this.all_members = data.members
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleCreateCustomerResponse (data, success) {
        if (success) {
          this.addMember.first_name = null
          this.addMember.last_name = null
          this.addMember.email = null
          this.addMember.phone = null
          router.push({name: 'Member Detail', params: {id: data.id}})
        }
        this.loading = false
      },
      handleRefresh () {
        this.getActiveMembers()
        this.getOtherMembers()
        this.getAllMembers()
      },
      clearPhone () {
        if (this.addMember.phone === '(###) ###-####') {
          this.addMember.phone = ''
        }
      },
      getRowCount (items) {
        return items.length
      }
    },
    validations: {
      addMember: {
        first_name: {
          required
        },
        last_name: {
          required
        },
        phone: {
          required
        },
        email: {
          required,
          email
        }
      }
    }
  }
</script>

<style scoped>

</style>



