<template>
  <div class="col-12">
    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Store</strong> Kiosks <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
        <b-button type="submit" size="sm" variant="success" class="pull-right" v-b-modal.kioskAdd>
          <i class="fa fa-plus"></i> Add
        </b-button>
        <a href="https://docs.range-pro.com/store_account.html#kiosks" target="new" class="button">
          <b-button type="submit" size="sm" variant="primary" class="pull-right mr-1"><i class="fa fa-question-circle"></i> Help</b-button>
        </a>
      </div>
      <b-table striped hover
               :current-page="currentPage"
               :per-page="perPage"
               :items="item_list"
               :fields="fields">

        <template slot="waivers" slot-scope="data">
          <b-badge :variant="getBadge(data.item.waivers)">{{data.value}}</b-badge>
        </template>

        <template slot="waitinglist" slot-scope="data">
          <b-badge :variant="getBadge(data.item.waitinglist)">{{data.value}}</b-badge>
        </template>

        <template slot="selfservice" slot-scope="data">
          <b-badge :variant="getBadge(data.item.selfservice)">{{data.value}}</b-badge>
        </template>

        <template slot="id" slot-scope="data">
          <router-link :to="{ name: 'Kiosk Dashboard', params: { id:  data.item.id}}" target="_blank">
            <b-button type="submit" size="sm" variant="secondary"><i class="fa fa-th-large"></i> Dashboard</b-button>
          </router-link>
          <b-button type="submit" size="sm" variant="warning" @click="selectKiosk(data.item.name, data.item.waivers, data.item.waitinglist, data.item.selfservice, data.item.id )"><i class="fa fa-trash-o"></i> Edit</b-button>
          <b-button type="submit" size="sm" variant="danger" @click="confirmDelete(data.item.id)"><i class="fa fa-trash-o"></i> Delete</b-button>
        </template>

      </b-table>

      <nav>
        <b-pagination :total-rows="getRowCount(item_list)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
      </nav>

      <b-modal ref="confirmDelete" hide-footer title="Delete Kiosk" class="modal-dark text-light">
        <div class="d-block text-left text-dark">
          <p>Please confirm to delete kiosk.</p>
        </div>
        <b-btn class="mt-3" variant="outline-danger" block @click="deleteItem()">Confirm</b-btn>
      </b-modal>

    </b-card>


    <b-modal ref="kioskAdd" id="kioskAdd" hide-footer size="lg" centered hide-footer title="Add Kiosk" class="modal-dark text-light">
      <div ref="card" class="text-dark">

        <b-form-group
          label="Display Name"
          label-for="displayName"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="displayName" type="text" v-model="addKiosk.name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.addKiosk.name.required">Kiosk Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Waivers"
          label-for="waiverEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="waiverEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="addKiosk.waivers">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Waiting List"
          label-for="wlEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="wlEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="addKiosk.waitinglist">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Self-Service"
          label-for="ssEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="ssEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="addKiosk.selfservice">
          </b-form-select>
        </b-form-group>

        <b-btn class="mt-3" variant="outline-success" block @click="saveKiosk()" :disabled="$v.addKiosk.$invalid">Add Kiosk</b-btn>

      </div>
    </b-modal>

    <b-modal ref="kioskEdit" id="kioskEdit" hide-footer size="lg" centered hide-footer title="Edit Kiosk" class="modal-dark text-light">
      <div ref="card" class="text-dark">

        <b-form-group
          label="Display Name"
          label-for="displayName"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="displayName" type="text" v-model="editKiosk.name"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.editKiosk.name.required">Kiosk Name is Required</span>
        </b-form-group>

        <b-form-group
          label="Waivers"
          label-for="waiverEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="waiverEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="editKiosk.waivers">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Waiting List"
          label-for="wlEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="wlEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="editKiosk.waitinglist">
          </b-form-select>
        </b-form-group>

        <b-form-group
          label="Self-Service"
          label-for="ssEnabled"
          :label-cols="2"
          :horizontal="true">
          <b-form-select id="ssEnabled"
                         :plain="true"
                         :options="[{ value: true, text: 'Yes'}, { value: false, text: 'No'}]"
                         v-model="editKiosk.selfservice">
          </b-form-select>
        </b-form-group>

        <b-btn class="mt-3" variant="outline-success" block @click="updateKiosk()" :disabled="$v.editKiosk.$invalid">Save Kiosk</b-btn>

      </div>
    </b-modal>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'
  import { required } from 'vuelidate/lib/validators'

  export default {
    name: 'StoreDisplays',
    data () {
      return {
        item_list: [],
        delete_id: null,
        loading: true,
        currentPage: 1,
        perPage: 5,
        totalRows: 0,
        edit_id: null,
        editKiosk: {
          'name': null,
          'waivers': false,
          'waitinglist': false,
          'selfservice': false
        },
        addKiosk: {
          'name': null,
          'waivers': false,
          'waitinglist': false,
          'selfservice': false
        },
        fields: [
          {
            key: 'name',
            label: 'Display Name',
            sortable: true
          },
          {
            key: 'waivers',
            label: 'Waivers',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'waitinglist',
            label: 'Waiting List',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'selfservice',
            label: 'Self-Service',
            formatter: (value) => formats.format_yes_no(value)
          },
          {
            key: 'id',
            label: 'Action'
          }
        ]
      }
    },
    mounted: function () {
      this.getData()
    },
    methods: {
      getData (response) {
        rangepro.get_secure('store/kiosks', this.handleResponse, 'GetStoreKiosks')
      },
      saveKiosk () {
        this.loading = true
        var kiosk = {
          kiosk: this.addKiosk
        }
        rangepro.post_secure('store/kiosks', kiosk, this.handleAddKiosksResponse, 'AddStoreKiosk', 'Successfully Added Kiosk')
      },
      updateKiosk () {
        this.loading = true
        var kiosk = {
          kiosk: this.editKiosk
        }
        rangepro.put_secure('store/kiosk/' + this.edit_id, kiosk, this.handleAddKiosksResponse, 'AddStoreKiosk', 'Successfully Updated Kiosk')
      },
      deleteItem () {
        this.loading = true
        this.$refs.confirmDelete.hide()
        rangepro.delete_secure('store/kiosk', this.delete_id, this.getData, 'GetStoreKiosks', 'Removed Store Kiosk')
      },
      confirmDelete (id) {
        this.$refs.confirmDelete.show()
        this.delete_id = id
      },
      handleAddKiosksResponse (data, success) {
        if (success) {
          this.addKiosk.name = null
          this.addKiosk.waivers = false
          this.addKiosk.waitinglist = false
          this.addKiosk.selfservice = false
          this.editKiosk.name = null
          this.editKiosk.waivers = false
          this.editKiosk.waitinglist = false
          this.editKiosk.selfservice = false
          this.edit_id = null
          this.$refs.kioskAdd.hide()
          this.$refs.kioskEdit.hide()
          this.getData()
        }
        this.loading = false
      },
      handleResponse (data, success) {
        if (success) {
          this.item_list = data.kiosks
          this.loading = false
        } else {
          this.loading = false
        }
      },
      selectKiosk (name, waivers, waitinglist, selfservice, id) {
        this.edit_id = id
        this.editKiosk.name = name
        this.editKiosk.waivers = waivers
        this.editKiosk.waitinglist = waitinglist
        this.editKiosk.selfservice = selfservice
        this.$refs.kioskEdit.show()
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      }
    },
    validations: {
      addKiosk: {
        name: {
          required
        }
      },
      editKiosk: {
        name: {
          required
        }
      }
    }
  }
</script>

<style scoped>

</style>



