<template>
  <div class="col-12">
    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>RangePro</strong> Account
      </div>
      <b-form-group
        description="Enter your first name."
        label="First Name"
        label-for="firstName"
        :label-cols="3"
        :horizontal="true">
        <b-form-input id="firstName" type="text" placeholder="Enter your first name" v-model="first"></b-form-input>
        <span class="form-group__message text-danger" v-if="!$v.first.required">First Name is Required</span>
      </b-form-group>
      <b-form-group
        description="Enter your last name."
        label="Last Name"
        label-for="lastName"
        :label-cols="3"
        :horizontal="true">
        <b-form-input id="lastName" type="text" placeholder="Enter your last name" v-model="last"></b-form-input>
        <span class="form-group__message text-danger" v-if="!$v.last.required">Last Name is Required</span>
      </b-form-group>
      <b-form-group
        description="Enter your email address."
        label="Email Address"
        label-for="emailAddress"
        :label-cols="3"
        :horizontal="true">
        <b-form-input id="emailAddress" type="text" placeholder="Enter your email address" v-model="emailAddress" :disabled="true"></b-form-input>
        <span class="form-group__message text-danger" v-if="!$v.emailAddress.required">Email is Required</span><span class="form-group__message text-danger" v-if="!$v.emailAddress.email">Invalid Email Address</span>
      </b-form-group>

      <div slot="footer">
        <b-button type="submit" size="sm" variant="primary" @click="updateProfile(first, last, emailAddress)" :disabled="$v.$invalid"><i class="fa fa-dot-circle-o"></i> Submit</b-button>
      </div>
    </b-card>
  </div>
</template>

<script>
  import auth from '../services/auth'
  import { mapGetters } from 'vuex'
  import { required, email } from 'vuelidate/lib/validators'

  export default {
    name: 'Account',
    data () {
      return {
        first: '',
        last: '',
        emailAddress: ''
      }
    },
    mounted: function () {
      this.first = this.getProfileData['first']
      this.last = this.getProfileData['last']
      this.emailAddress = this.email
    },
    methods: {
      updateProfile (first, last, email) {
        return auth.updateProfile(first, last, email)
      }
    },
    computed: {
      ...mapGetters([
        'getProfileData',
        'email'
      ])
    },
    validations: {
      emailAddress: {
        required,
        email
      },
      first: {
        required
      },
      last: {
        required
      }
    }
  }
</script>

<style scoped>

</style>



